<template>
  <div class="home">
    <Layout>
      <div class="card">
        <div class="card-body">
          <h3>Teen Lesson</h3>
          <form class="mt-5" @submit.prevent="onSubmit()">
            <div class="form-group">
              <div class="row">
                <div class="col-md-3">
                  <p>แสดงวิดีโอ Live Streaming</p>
                </div>
                <div class="col-md-9">
                  <input
                    type="checkbox"
                    v-model="form.admin_als_enable_link"
                    :checked="form.admin_als_enable_link"
                  />
                </div>
              </div>
            </div>

            <div class="form-group">
              <div class="row">
                <div class="col-md-3">
                  <p>เปิดให้มีการบันทึกกิจกรรม</p>
                </div>
                <div class="col-md-9">
                  <input
                    type="checkbox"
                    v-model.trim="form.admin_als_enable_done"
                    :checked="form.admin_als_enable_done"
                  />
                </div>
              </div>
            </div>
            <div class="form-group">
              <div class="row">
                <div class="col-md-3">
                  <label>Preview Live Streaming Youtube URL</label>
                </div>
                <div class="col-md-6">
                  <input
                    type="text"
                    name="admin_als_pyt_id"
                    v-validate="{
                    required: true
                  }"
                    :class="{ 'is_invalid': errors.has('admin_als_pyt_id') }"
                    v-model.trim="form.admin_als_pyt_id"
                    v-on:change="ytUrlChange"
                    class="form-control"
                    placeholder="URL..."
                  />
                  <p class="invalid_feedback">{{ errors.first('admin_als_pyt_id') }}</p>
                </div>
                <div class="col-md-3"></div>
              </div>

              <div class="row">
                <div class="col-md-3"></div>
                <div class="col-md-5">
                  <img
                    v-if="form.admin_als_pyt_id"
                    v-bind:src="`https://img.youtube.com/vi/${form.admin_als_pyt_id}/default.jpg`"
                    @click="displayYoutube(form.admin_als_pyt_id)"
                  />
                  <img
                    v-else
                    class="img-fluid"
                    style="width:auto; height:150%"
                    :src="'/img/no-image.jpg'"
                    alt="no image"
                  />
                </div>
                <div class="col-md-4"></div>
              </div>
            </div>
            <br />
            <br />
            <br />
            <div class="form-group">
              <div class="row">
                <div class="col-md-3">
                  <label>Live Streaming Youtube URL</label>
                </div>
                <div class="col-md-6">
                  <input
                    type="text"
                    name="admin_als_yt_link"
                    v-validate="{
                    required: true
                  }"
                    :class="{ 'is_invalid': errors.has('admin_als_yt_link') }"
                    v-model.trim="form.admin_als_yt_link"
                    class="form-control"
                    placeholder="URL..."
                  />
                  <p class="invalid_feedback">{{ errors.first('admin_als_yt_link') }}</p>
                </div>
                <div class="col-md-3"></div>
              </div>
            </div>
            <div class="row">
              <div class="col-md-3"></div>
              <div class="col-md-5">
                <button type="submit" class="btn btn-primary btn-md mt-2">บันทึกข้อมูล</button>
              </div>
              <div class="col-md-4"></div>
            </div>
          </form>
        </div>
      </div>
    </Layout>
    <YoutubeModal ref="ytModal" />
  </div>
</template>
<script>
import Axios from "axios";
import Layout from "@/components/admin/Layout";
import getVideoId from "get-video-id";
import YoutubeModal from "@/components/YoutubeModal";
export default {
  components: {
    Layout,
    YoutubeModal
  },
  data() {
    return {
      form: {
        admin_als_pyt_id: "",
        admin_als_yt_link: "",
        admin_als_enable_done: "",
        admin_als_enable_link: ""
      }
    };
  },
  mounted() {
    this.initialValue();
  },
  methods: {
    initialValue() {
      Axios.get(`/api/controllerLiveStreaming`)
        .then(res => {
          const form = res.data;
          this.form.admin_als_pyt_id = form.admin_als_pyt_id;
          this.form.admin_als_yt_link = form.admin_als_yt_link;
          this.form.admin_als_enable_done = form.admin_als_enable_done;
          this.form.admin_als_enable_link = form.admin_als_enable_link;
        })
        .catch(err => {
          //this.alertify.error(err);
        });
    },
    displayYoutube(youtubeID) {
      this.$refs.ytModal.showModal(youtubeID);
    },
    ytUrlChange() {
      this.form.admin_als_pyt_id = getVideoId(this.form.admin_als_pyt_id).id;
    },
    onSubmit() {
      this.$validator.validateAll().then(valid => {
        Axios.post("/api/controllerLiveStreaming/", this.form)
          .then(res => {
            this.alertify.success("บันทึกข้อมูลเรียบร้อย");
          })
          .catch(err => {
            this.alertify.error("ไม่สามารถบันทึกข้อมูลได้");
          });
      });
    }
  }
};
</script>
<style scoped>
label {
  color: black;
}
</style>