<script>
import axios from "axios";
export default {
  component:{
  },
  name:'Login',
  data() {
    return {
      form: {
        u_username: "",
        u_password: ""
      },
      errorMessage: ""
    };
  },
  computed:{},
  watch:{},
  mounted(){

  },
  created(){

  },
  beforeDestroy(){

  },
  methods: {
    onSubmit() {

      this.$validator.validateAll().then(async valid => {
        if (!valid) return;
        axios
          .post("api/admin/login", this.form)
          .then(response => {
            //console.log(response.data)

            if (response.data.message == "Authen fail") {
              this.errorMessage = "Please check youy username and password";
            } else {
              this.$router.push({ name: "adminHome" });
            }
          })
          .catch(err => {
            this.errorMessage = err.response.data.message;
          });
      });
    },
    onReset() {
      this.errorMessage = null;
      this.$validator.reset();
      this.form = {
        u_username: "",
        u_password: ""
      };
    }
  }
};
</script>
