// Check if user is not authen then redirect to login form
router.beforeEach((to, from, next) => {
  if (!to.meta.auth && !to.meta.adminAuth) {
    return next()
  } else if (to.meta.auth) {
    /*
    if (to.meta.authDone != undefined) {
      if (router.app.$store.getters.userProgress.activity[to.meta.authDone].done == 1) {
        router.app.$store.dispatch('get_user_login').then(() => next()).catch(() => next({ name: 'login' }))
      }else{
        router.app.$store.dispatch('get_user_login').then(() => next({ name: 'home' })).catch(() => next({ name: 'login' }))
      }
    }
    else {
      router.app.$store.dispatch('get_user_login').then(() => next()).catch(() => next({ name: 'login' }))
    }
    */

    router.app.$store.dispatch('get_user_login').then(() => {
      if(router.app.$store.getters.user != undefined && router.app.$store.getters.user != ""){
        next()
      }else{
        next({ name: 'login' })
      }
    }).catch(() => next({ name: 'login' }))

  } else if (to.meta.adminAuth) {
    router.app.$store.dispatch('get_admin_login').then(() => next()).catch(() => next({ name: 'adminLogin' }))
    /*
    router.app.$store.dispatch('get_admin_login').then(() => {
      console.log(router.app.$store.getters.userAdmin)
      if(router.app.$store.getters.userAdmin != undefined && router.app.$store.getters.userAdmin != ""){
        next()
      }else{
        next({ name: 'adminLogin' })
      }
    }).catch(() => next({ name: 'adminLogin' }))
    */
  }

})
export default router