<template>
  <div class="home">
    <Layout>
      <div class="card">
        <div class="card-body">
          <h3>User</h3>
          <form class="mt-5 form-inline" @submit.prevent="onSearch()">
            <div class="form-group">
              <input
                type="search"
                class="form-control"
                name="search_text"
                v-validate="{
                    required: true
                  }"
                :class="{ 'is_invalid': errors.has('search_text') }"
                v-model.trim="search.search_text"
                placeholder="รหัสนักศึกษา"
              />
            </div>
            <button type="submit" class="btn btn-primary ml-3">ค้นหา</button>
          </form>
          <div class="table-responsive mt-3">
            <table class="table">
              <thead class="thead-dark">
                <tr>
                  <th>#</th>
                  <th>ชื่อ</th>
                  <th>นามสกุล</th>
                  <th>รหัสนักศึกษา</th>
                  <th>รหัสผ่าน</th>
                  <th>แก้ไข</th>
                </tr>
              </thead>
              <tbody class="text-muted">
                 
                <tr v-for="(item, index) in report.result" :key="index">
                  <td>{{ (index + 1)+((page-1)*report.limit) }}</td>
                  <!-- ชื่อ -->
                  <td>{{ item.u_username }}</td>
                  <!-- สกุล -->
                  <td>{{ item.u_lastname }}</td>
                  <!-- รหัสนักศึกษา -->
                  <td>{{ item.u_id }}</td>
                  <!-- รหัสผ่าน -->
                  <td class="text-center">{{ item.u_password }}</td>
                  <!-- แก้ไข -->
                  <td>
                    <button
                      type="button"
                      class="btn btn-info btn-sm"
                      @click.prevent="onPopup(item)"
                    >
                      <i class="fa fa-edit"></i>
                    </button>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
        <div class="p-3" v-if="report.rows > report.limit">
          <Pagination :data="report" :page="page" @onPage="onPage($event)" />
        </div>
      </div>
      <form class="mt-5 form-inline" @submit.prevent="onChange()">
        <!-- Modal -->
        <div
          class="modal fade modal-md"
          id="detailModal"
          tabindex="-1"
          role="dialog"
          aria-labelledby="detailModalLabel"
          aria-hidden="true"
        >
          <div class="modal-dialog" role="document">
            <div class="modal-content">
              <div class="modal-header">
                <h5 class="modal-title" id="detailModalLabel">แก้ไขข้อมูล User</h5>
                <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                  <span aria-hidden="true">&times;</span>
                </button>
              </div>
              <div class="modal-body">
                <div class="row text-center mb-3">
                  <div class="col-md-6">ชื่อจริง</div>
                  <div class="col-md-6">
                    <input type="text" id="fieldFirstname" />
                  </div>
                </div>
                <div class="row text-center mb-3">
                  <div class="col-md-6">นามสกุล</div>
                  <div class="col-md-6">
                    <input type="text" id="fieldLastname" />
                  </div>
                </div>
                <div class="row text-center">
                  <div class="col-md-6">รหัสผ่าน</div>
                  <div class="col-md-6">
                    <input type="text" id="fieldPassword" />
                  </div>
                </div>
              </div>
              <div class="modal-footer">
                <div class="row">
                  <div class="col-md-3"></div>
                  <div class="col-md-6">
                    <button type="submit" class="btn btn-primary w-100">บันทึก</button>
                  </div>
                  <div class="col-md-3"></div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </form>
    </Layout>
  </div>
</template>

<script>
import Axios from "axios";
import Layout from "@/components/admin/Layout";
import Pagination from "@/components/admin/Pagination";
import { mapState } from "vuex";
export default {
  components: {
    Layout,
    Pagination,
  },
  name: "getUser",
  data() {
    return {
      tbody: {
        u_id: [],
      },
      modalData: {
        u_username: "",
        u_lastname: "",
        u_password: "",
        u_id: "",
      },
      search: {
        search_key: "u_id",
        search_text: "",
      },
      page: 1,
      report: [],
    };
  },
  computed: {},
  watch: {},
  created() {
    this.getData({
      params: {
        page: this.page,
      },
    });
  },
  beforeDestroy() {},
  methods: {
    getData(param) {
      Axios.get("/api/getuser/all", param)
        .then((res) => {
          this.report = res.data;
          console.log("=====");
          console.log(res);
        })
        .catch((err) => {});
    },
    onPopup(item) {
      this.jquery("#fieldFirstname").val(item.u_username);
      this.jquery("#fieldLastname").val(item.u_lastname);
      this.jquery("#fieldPassword").val(item.u_password);
      this.modalData.u_id = item.u_id;
      this.jquery("#detailModal").modal("show");
    },
    onChange() {
      // alert(this.jquery("#fieldFirstname").val())
      this.modalData.u_username = this.jquery("#fieldFirstname").val();
      this.modalData.u_lastname = this.jquery("#fieldLastname").val();
      this.modalData.u_password = this.jquery("#fieldPassword").val();
      Axios.post("/api/account/onUpdateData", this.modalData)
        .then((res) => {
          this.jquery("#detailModal").modal("toggle");
          this.getData();
        })
        .catch((err) => {
          console.log(err);
        });
    },
    onSearch() {
      this.page = 1;
      this.getData({
        params: {
          page: this.page,
          search_key: this.search.search_key,
          search_text: this.search.search_text,
        },
      });
    },
    onPage(page) {
      this.page = page;
      this.getData({
        params: {
          page: this.page,
        },
      });
    },
  },
};
</script>
<style scoped>
thead th,
tbody td {
  text-align: center;
}
.card-body {
  padding-left: 25px;
  padding-right: 25px;
}
</style>
