<template>
  <div class="activity h100">
    <Layout>
      <div class="container">
        <div id="main" class="row position-relative h-100">
          <div
            id="video-container"
            class="col-md-7 col-lg-8 justify-content-center position-relative"
          >
            <div v-if="setting.admin_av_hide_controller == 0" class="hideController"></div>
            <div id="ytContainer" class="ytContainer">
              <youtube
                v-if="loaded"
                class="w-100 h-100"
                ref="youtube"
                @ready="ready"
                @playing="playing"
                @paused="paused"
                :video-id="setting.admin_av_yt_id"
                :player-vars="{ autoplay: 0, controls: setting.admin_av_hide_controller, rel: 0 }"
              ></youtube>
            </div>
            <div
              v-for="(item, index) in questions"
              :id="item.id"
              :key="index"
              class="question-container"
            >
              <div class="position-relative">
                <small class="position-absolute" style="right:15px;top:15px;">
                  <a @click.prevent="toggleLang('th')">TH</a> |
                  <a @click.prevent="toggleLang('en')">EN</a>
                </small>

                <div class="p-5">
                  <h4 v-if="lang=='en'" class="text-center">{{ item.question.en.title }}</h4>
                  <h4 v-if="lang=='th'" class="text-center">{{ item.question.th.title }}</h4>
                  <p v-if="lang=='en'">{{ item.question.en.content }}</p>
                  <p v-if="lang=='th'">{{ item.question.th.content }}</p>
                  <div v-if="lang=='en'" class="row mt-5">
                    <div
                      v-for="(answer, indexAns) in item.question.en.answer"
                      :key="indexAns"
                      class="col-12 text-center answer"
                    >
                      <div :class="`d-block ` + indexAns" @click.prevent="onAnswer()">
                        <div class="row">
                          <div class="col-2 caption p-2">{{ indexAns }}</div>
                          <div class="col-10 text-left w100 p-2">{{ answer }}</div>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div v-if="lang=='th'" class="row mt-5">
                    <div
                      v-for="(answer, indexAns) in item.question.th.answer"
                      :key="indexAns"
                      class="col-md-4 text-center answer"
                    >
                      <div :class="`d-block ` + indexAns" @click.prevent="onAnswer()">
                        <div class="row">
                          <div class="col-2 caption p-2">{{ indexAns }}</div>
                          <div class="col-10 text-left w100 p-2">{{ answer }}</div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="col-md-5 col-lg-4">
            <div id="chat" class="card">
              <div class="card-body text-center" style="min-height: 500px;">
                <div id="chat-container">
                  <div class="h-100 position-relative">
                    <div
                      id="message-container"
                      class="position-absolute p-2"
                      ref="messageBoxContainer"
                    >
                      <div ref="messageBox">
                        <div
                          class="chat-conversation-message"
                          v-for="message in messages"
                          :message="message"
                          v-bind:key="message.id"
                        >
                          <strong>{{ message.user.name }}:</strong>
                          <div
                            v-if="message.message.match(/\/\*(stck-[0-1]?[0-9])\*\//)"
                            class="ml-2 d-inline"
                          >
                            <img
                              style="width:50px;"
                              :src="'/stickers/' + message.message.match(/\/\*(.*)\*\//)[1] + '.png'"
                            />
                          </div>
                          <span class="text-muted wrap-auto ml-2" v-else>{{ message.message }}</span>
                        </div>
                      </div>
                    </div>
                    <div class="input-group position-absolute" style="bottom: 5px">
                      <input
                        id="inputChat"
                        type="text"
                        class="form-control"
                        placeholder="Add comment"
                        @keyup="onKeyUp"
                        v-model.trim="message"
                      />
                      <div class="input-group-prepend position-relative">
                        <div id="stck-icon" class="input-group-text dropup">
                          <a data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                            <i class="fa fa-smile-o" aria-hidden="true"></i>
                          </a>
                          <div class="dropdown-menu">
                            <ul class="stck-container">
                              <li v-for="index in 18" :key="index">
                                <a @click.prevent="setSticker(`stck-${ index }`)">
                                  <img :src="`/stickers/stck-${ index }.png`" />
                                </a>
                              </li>
                            </ul>
                          </div>
                        </div>
                        <button
                          id="btnSendMsg"
                          type="button"
                          @click="sendMessage"
                          class="input-group-text btn text-primary"
                        >
                          <i class="fa fa-paper-plane" aria-hidden="true"></i>
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="text-center m-3">
              <a @click.prevent="onBack()" class="btn btn-success mr-4 w100">Back</a>

              <Timer
                v-if="!finished"
                :triggerEvent="this.triggerEvent"
                @onTriggerEvent="onTriggerEvent()"
                @onFinish="onFinish($event)"
                ref="timer"
              />
              <button
                v-else
                class="btn btn-primary w100"
                type="button"
                @click.prevent="onEndActivity()"
              >NEXT</button>
            </div>
          </div>
        </div>
      </div>
    </Layout>
    <ModalSurway :activity="activityName" @onComplete="onBack()" ref="modalSurway" />
  </div>
</template>
<script>
import Axios from "axios";
import ModalSurway from "@/components/ModalSurway";
import Complete from "@/components/Complete";
import Timer from "@/components/Timer";
import Layout from "@/components/Layout";
import io from "socket.io-client";
export default {
  components: {
    Axios,
    Timer,
    ModalSurway,
    Complete,
    Layout
  },
  name: "",
  data() {
    return {
      form: {
        u_id: null,
        u_username: null,
        av_name: null
      },
      setting: {
        admin_av_autoplay: null,
        admin_av_hide_controller: null,
        admin_av_disable_msg: null,
        admin_av_enable: null,
        admin_av_yt_id: null
      },
      player: null,
      lang: "en",
      duration: 0,
      display: "video",
      title: "",
      subtitle: "",
      descEN: "",
      descTH: "",
      activityName: "",
      finished: false,
      loaded: false,
      ytHeight: 0,
      //chat variable
      user: {
        id: null,
        name: null
      },
      messageActivity: "messageDinner",
      message: "",
      messages: [],
      socket: null,
      questionPointer: -1,
      questions: [
        {
          id: "question1",
          displayed: false,
          displayAfter: "798", // sec
          question: {
            th: {
              title: "คำถามที่ 1",
              content:
                "แล้วน้องๆละคะ เคยทานอาหารเหนือกันรึเปล่า",
              answer: {
                A: "เคย",
                B: "ไม่เคย"
              }
            },
            en: {
              title: "Question 1",
              content:
                "What about you? Have you ever tried Northern Thai dishes?",
              answer: {
                A: "Yes",
                B: "No"
              }
            }
          }
        },
        {
          id: "question2",
          displayed: false,
          displayAfter: "970", // sec
          question: {
            th: {
              title: "คำถามที่ 2",
              content:
                "ระดับภาษาเหนือของน้องๆ อยู่เลเวลไหนกัน",
              answer: {
                A: "ระดับเทพ",
                B: "ก็พอได้",
                C: "ต่ำเตี้ย"
              }
            },
            en: {
              title: "Question 2",
              content:
                "Can you rate your Northern Thai dialect?",
              answer: {
                A: "High",
                B: "Intermediate",
                C: "Low"
              }
            }
          }
        },
        {
          id: "question3",
          displayed: false,
          displayAfter: "1066", // sec
          question: {
            th: {
              title: "คำถามที่ 3",
              content:
                "ถ้าให้เลือกฝึกภาษาเหนือ น้องๆจะเลือกฝึกด้วยวิธีใด",
              answer: {
                A: "หาแฟนเป็นคนเหนือ",
                B: "เดินกาดคุยกับแม่ค้า",
                C: "ลงเรียนวิชาเสรีภาษาล้านนา"
              }
            },
            en: {
              title: "Question 3",
              content:
                "If you would like to learn Northern Thai dialect, how would you learn it?",
              answer: {
                A: "Find a lover who is Northern",
                B: "Visit a market and talk to local vendors",
                C: "Attend a Lan Na language class, a free elective course"
              }
            }
          }
        }
      ],
      triggerEvent: 0
    };
  },
  computed: {},
  watch: {},
  mounted() {
    if (this.socket) {
      window.addEventListener("resize", this.resizeYT);
      this.socket.on(this.messageActivity, data => {
        this.messages.push(data);
      });
    }
  },
  created() {
    this.socket = io.connect(process.env.VUE_APP_API);
    this.user.id = this.$store.state.user.u_id;
    this.user.name = this.$store.state.user.u_username;
    this.initialValue();
    this.getLog();
  },
  beforeDestroy() {
    window.addEventListener("resize", this.resizeYT);
    if (this.socket) {
      this.socket.emit("disconnected", {
        user: this.user
      });
    }
  },
  updated() {
    this.$refs.messageBoxContainer.scrollTop = this.$refs.messageBox.scrollHeight;
  },
  methods: {
    resizeYT() {
      this.ytHeight = (this.jquery("#ytContainer").width() * 9) / 16;
      this.jquery("#ytContainer").height(this.ytHeight);
    },
    initialValue() {
      //console.log(this.activity)
      const activity = this.activity[3];
      this.title = activity.title;
      this.subtitle = activity.subtitle;
      this.descEN = activity.description.en;
      this.descTH = activity.description.th;
      this.activityName = activity.name;
      this.$store.dispatch("get_activity", { activity: this.activity[3] });
      this.duration = 0;

      Axios.get(`/api/controllerVideo/${activity.name}`)
        .then(res => {
          this.setting.admin_av_autoplay = res.data.admin_av_autoplay ? 1 : 0;
          this.setting.admin_av_hide_controller = res.data
            .admin_av_hide_controller
            ? 0
            : 1;
          this.setting.admin_av_enable = res.data.admin_av_enable;
          this.setting.admin_av_disable_msg = res.data.admin_av_disable_msg;
          this.setting.admin_av_yt_id = res.data.admin_av_yt_id;

          this.loaded = true;
        })
        .catch(err => {
          console.log(err);
        });
    },
    ready() {
      this.player = this.$refs.youtube.player;
      if (this.setting.admin_av_autoplay == 1) {
        this.player.seekTo(0);
        this.player.playVideo();
      }
      this.resizeYT();
    },
    onAnswer() {
      this.jquery("#" + this.questions[this.questionPointer].id).fadeOut();
      this.questions[this.questionPointer].displayed = true;
      this.$refs.youtube.player.playVideo();
    },
    async playing() {
      for (var i = 0; i < this.questions.length; i++) {
        if (!this.questions[i].displayed) {
          this.questionPointer = i;
          this.triggerEvent = this.questions[i].displayAfter;
          break;
        }
      }
      /*
      if(!this.questions[0].displayed){

      }else if(){

      }*/
      //console.log(this.questions[1].displayAfter);
      this.duration = await this.player.getDuration();
      this.videoTime = await this.player.getCurrentTime();
      if (!this.finished) {
        this.$refs.timer.setDuration(this.duration - this.videoTime);
        this.$refs.timer.onResume();
      }
    },
    paused() {
      if (!this.finished) {
        this.$refs.timer.onPause();
      }
    },
    onFinish() {
      const id = this.$store.getters.user.u_id;
      Axios.get(`/api/activityVideo/${id}?av_name=${this.activityName}`).then(
        res => {
          if (!res.data) {
            this.form.u_id = id;
            this.form.u_username = this.$store.getters.user.u_username;
            this.form.av_name = this.activityName;
            Axios.post("/api/activityVideo", this.form).then(res => {
              this.$store.dispatch("get_user_progress", {
                id: this.$store.getters.user.u_id
              });
            });
          }
        }
      );
      this.finished = true;
    },
    onEndActivity() {
      const id = this.$store.getters.user.u_id;
      Axios.get(`/api/surway/${id}?activity=${this.activityName}`)
        .then(res => {
          if (res.data.length == 0) {
            this.$refs.modalSurway.showModal();
          } else {
            this.onBack();
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    onBack() {
      this.$router.push({
        path: `/ActivityPreview/${this.activityName}`
      });
    },
    toggleLang(lang) {
      this.lang = lang;
    },
    ///// chat methods
    sendMessage() {
      if (this.message.trim() != "") {
        this.socket.emit(this.messageActivity, {
          id: this.id,
          user: this.user,
          message: this.message
        });
        this.message = "";
      }
    },
    onKeyUp(event) {
      if (event.keyCode === 13) {
        this.sendMessage();
      }
    },
    setSticker(stName) {
      this.socket.emit(this.messageActivity, {
        id: this.id,
        user: this.user,
        message: `/*${stName}*/`
      });
      this.jquery("#stickerModal").modal("hide");
    },
    getLog() {
      this.socket.emit("returnLog");
      this.socket.on("returnLogResponse", message => {
        var logTmp = [];
        for (var i = 0; i < message.length; i++) {
          logTmp.push(message[i]);
        }
        this.messages = logTmp.slice().reverse();
      });
    },
    onTriggerEvent() {
      this.jquery("#" + this.questions[this.questionPointer].id).fadeIn();
      this.triggerEvent = 0;
      this.$refs.youtube.player.pauseVideo();
      this.paused();
    }
  }
};
</script>
