<template>
  <div class="activity h100">
    <Layout>
      <div class="flex-container">
        <div
          class="box box-body position-relative align-items-start"
          style="flex-direction: initial;"
        >
          <div class="row w-100">
            <div
              class="col-lg-3 col-md-4"
              v-for="(item, index) in videos.result"
              :key="index"
              id="videoURL"
            >
              <VoteVideo
                :item="item"
                :disableVote="disableVote"
                :selectedItem="selectedItem"
                :savedVoted="savedVoted"
                @onDisplay="displayClip(item.ac_url)"
                @selectedVoice="selectVote(item)"
              />
            </div>
          </div>
        </div>
        <div class="text-center d-flex mt-5">
          <Pagination
            id="paging-container"
            :data="videos"
            :page="page"
            class="m-auto"
            @onPage="onPage($event)"
          />
        </div>
        <div class="d-none d-md-block d-lg-block">
          <div class="row mb-4 d-flex">
            <div class="col-md-4 d-flex text-center align-items-center">
              <button
                type="button"
                @click.prevent="onBack()"
                class="btn btn-primary w100 align-middle m-auto"
              >BACK</button>
            </div>
            <div class="col-md-4 text-center">
              <!--
              Excepteur sint occaecat cupidatat non proident,
              <br />sunt in culpa qui officia deserunt-->
            </div>
            <div class="col-md-4 d-flex text-center align-items-center">
              <button
                v-if="!disableVote"
                type="button"
                class="btn btn-primary w100 align-middle m-auto"
                :disabled="!voted"
                @click.prevent="onSubmit()"
              >SUBMIT</button>
              <button
                v-else
                type="button"
                class="btn voted w100 align-middle m-auto text-white text-nowrap"
              >
                <i class="fa fa-heart"></i>&nbsp;VOTED
              </button>
            </div>
          </div>
        </div>
        <div class="d-md-none d-lg-none d-sm-block d-xs-block">
          <div class="row mb-4 d-flex">
            <div class="col-12 text-center justify-content-center align-self-center">
              <!--
            Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt
              -->
            </div>
            <div class="col-12 mt-2 align-self-center text-center">
              <button @click="onBack()" type="button" class="btn btn-primary w100 m-3">BACK</button>
              <button
                v-if="!disableVote"
                type="button"
                class="btn btn-primary w100 align-middle m-auto"
                :disabled="!voted"
                @click.prevent="onSubmit()"
              >SUBMIT</button>
              <button
                v-else
                type="button"
                class="btn voted w100 align-middle m-auto text-white text-nowrap"
              >
                <i class="fa fa-heart"></i>&nbsp;VOTED
              </button>
            </div>
          </div>
        </div>
      </div>
    </Layout>

    <YoutubeModal ref="ytModal" />
    <div
      class="modal fade"
      id="deniedModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-dialog-centered modal-sm">
        <div class="modal-content">
          <div class="modal-body p-3 text-center">
            <h4 class="text-muted">VOTING</h4>
            <h1 class="text-denied font-weight-bolder">DENIED</h1>

            <img src="../assets/img/lock.png" class="w-50 img-fluid mt-3 d-block m-auto" />
            <p
              class="mt-3"
            >Students will be able to vote for their favourite clip when the system shows more than 80% of their progress bar</p>
            <button
              type="button"
              class="btn btn-primary mt-3 w100 d-block m-auto"
              data-dismiss="modal"
              aria-label="Close"
            >BACK</button>
          </div>
        </div>
      </div>
    </div>
    <div
      class="modal fade"
      id="completeModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
      data-backdrop="static"
    >
      <div class="modal-dialog modal-dialog-centered modal-sm">
        <div class="modal-content">
          <div class="modal-body p-3 text-center">
            <h4 class="text-muted">VOTED</h4>
            <h1 class="text-primary font-weight-bolder">COMPLETE</h1>

            <img src="../assets/img/wl.png" class="w-50 img-fluid mt-3 d-block m-auto" />
            <div class="mt-3">
              <button
                type="button"
                class="btn btn-primary mt-3 w100 d-block m-auto"
                data-dismiss="modal"
                aria-label="Close"
                @click.prevent="onConfirm()"
              >CONFIRM</button>
            </div>
          </div>
        </div>
      </div>
    </div>
    <ModalSurway :activity="activityName" @onComplete="onVoted()" ref="modalSurway" />
  </div>
</template>
<script>
import Axios from "axios";
import Layout from "@/components/Layout";
import Pagination from "@/components/Pagination";
import YoutubeModal from "@/components/YoutubeModal";
import ModalSurway from "@/components/ModalSurway";
import VoteVideo from "@/components/VoteVideo";
export default {
  components: {
    Layout,
    VoteVideo,
    YoutubeModal,
    Pagination,
    ModalSurway
  },
  name: "voteVideo",
  data() {
    return {
      videos: [],
      page: 1,
      Vote: [],
      activityName: this.activity[11].name,
      voted: false,
      selectedItem: [],
      displaySurway: false,
      disableVote: false,
      savedVoted: false
    };
  },
  computed: {},
  watch: {},
  mounted() {
    const flex = this.jquery(".flex-container");
    this.jquery("#deniedModal, #completeModal")
      .on("show.bs.modal", function(e) {
        flex.addClass("blur");
      })
      .on("hidden.bs.modal", function(e) {
        flex.removeClass("blur");
      });
  },
  created() {
    const id = this.$store.getters.user.u_id;
    Axios.get(`/api/surway/${id}?activity=${this.activityName}`)
      .then(res => {
        if (res.data.length == 0) {
          this.displaySurway = true;
        } else {
          this.displaySurway = false;
        }
      })
      .catch(err => {
        console.log(err);
      });
    this.getData({
      params: {
        page: 1
      }
    });
    this.getVoted();
  },
  beforeDestroy() {},
  methods: {
    getVoted() {
      Axios.get(
        `/api/activityContest/voter/${this.$store.getters.user.u_id}`
      ).then(res => {
        if (res.data.voteNum > 0) {
          this.savedVoted = true;
          this.disableVote = true;
          Axios.get(`/api/controllerContest/${res.data.result[0].ac_id}`).then(
            res => {
              this.selectedItem = res.data.message;
            }
          );
        }
      });
    },
    onPage(page) {
      this.page = page;
      this.getData({
        params: {
          page: this.page
        }
      });
    },
    onBack() {
      this.$router.push({
        path: `/ActivityPreview/${this.activityName}`
      });
    },
    onVoted() {
      this.disableVote = true;
      this.onBack();
    },
    displayClip(ytID) {
      this.$refs.ytModal.showModal(ytID);
    },
    selectVote(item) {
      if (this.$store.getters.userProgress.progress >= 80) {
        if (this.selectedItem == item) {
          this.selectedItem = [];
          this.voted = false;
        } else {
          this.selectedItem = item;
          this.voted = true;
        }
      } else {
        this.jquery("#deniedModal").modal("show");
      }
    },
    getData() {
      Axios.get("/api/controllerContest/order", {
        params: {
          page: this.page
        }
      }).then(res => {
        this.videos = res.data;
      });
    },
    onConfirm() {
      if (this.displaySurway) {
        this.$refs.modalSurway.showModal();
      } else {
        this.onBack();
      }
    },
    onSubmit() {
      if (this.selectedItem) {
        const form = {
          ac_url: this.selectedItem.ac_url,
          ac_id: this.selectedItem.u_id,
          acv_u_id: this.$store.getters.user.u_id,
          u_name: this.$store.getters.user.u_username,
          u_school: this.$store.getters.user.u_school
        };
        Axios.post("/api/activityContest", form)
          .then(res => {
            this.jquery("#completeModal").modal("show");

            this.$store.dispatch("get_user_progress", {
              id: this.$store.getters.user.u_id
            });
          })
          .catch(err => {
            console.log(err);
          });
      }
    }
  }
};
</script>
<style scoped>
.center {
  margin: auto;
  width: 105%;
  padding: 10px;
}
.voted {
  background-color: #ff8888 !important;
}
.voted i {
  color: #ff4b4b;
}
</style>
