<template>
  <div class="activity h100">
    <Layout>
      <div class="text-center d-flex position-relative">
        <div
          id="loading-container"
          v-if="loading"
          class="position-absolute d-flex align-items-center align-middle"
        >
          <div class="m-auto text-center">
            <img src="../assets/img/loader.gif" class="w100 rounded-circle">
            <p
              class="mt-5 d-sm-block d-xs-block d-md-nond d-lg-none"
            >We recommend you to do this activity on a tablet or PC.</p>
          </div>
        </div>
        <iframe
          id="game"
          ref="game"
          name="gameIframe"
          class="d-inline-block align-self-center text-center"
          :src="`/painting/index.html?${shortName}`"
          v-if="canAddListener"
        ></iframe>
      </div>

      <div v-if="!loading" id="comment-container">
        <textarea
          id="comment-text"
          name="acp_moto"
          v-validate="{ required: true }"
          v-model="form.acp_moto"
          class="form-control"
          :class="{ 'is-invalid': errors.has('acp_moto') }"
          rows="7"
          placeholder="Add comment"
        ></textarea>
        <div class="text-center mt-3">
          <button type="button" @click.prevent="onBack()" class="btn btn-success w100 m-2">Back</button>
          <button type="button" class="btn btn-success w100 m-2" @click.prevent="sendValue()">Submit</button>
        </div>
      </div>
    </Layout>
    <ModalSurway :activity="activityName" @onComplete="onBack()" ref="modalSurway" />
  </div>
</template>
<script>
import Axios from "axios";
import Timer from "@/components/Timer";
import ModalSurway from "@/components/ModalSurway";
import Layout from "@/components/Layout";
export default {
  components: {
    Layout,
    Axios,
    Timer,
    ModalSurway
  },
  name: "",
  data() {
    return {
      param: "",
      form: {
        u_id: null,
        u_username: null,
        acp_moto: null,
        acp_image: null
      },
      shortName: null,
      activityName: this.activity[9].name,
      loading: true,
      displaySurway: false,
      canAddListener: true
    };
  },
  computed: {},
  watch: {},
  mounted() {},
  created() {
    switch (this.$store.getters.user.u_school) {
      case "Science":
        this.shortName = "sci";
        break;
      case "Information Technology":
        this.shortName = "it";
        break;
      case "Liberal Arts":
        this.shortName = "la";
        break;
      case "Law":
        this.shortName = "law";
        break;
      case "Cosmetic Science":
        this.shortName = "sc";
        break;
      case "Dentistry":
        this.shortName = "dents";
        break;
      case "Agro-industry":
        this.shortName = "ai";
        break;
      case "Nursing":
        this.shortName = "nursing";
        break;
      case "Social Innovation":
        this.shortName = "si";
        break;
      case "Management":
        this.shortName = "mn";
        break;
      case "Medicine":
        this.shortName = "med";
        break;
      case "Sinology":
        this.shortName = "sin";
        break;
      case "Health Science":
        this.shortName = "hs";
        break;
      case "Integrative Medicine":
        this.shortName = "im";
        break;
    }
    this.$store.dispatch("get_activity", { activity: this.activity[9] });
    const id = this.$store.getters.user.u_id;

    Axios.get(`/api/surway/${id}?activity=${this.activityName}`)
      .then(res => {
        //console.log(res);
        //console.log("---------");
        if (res.data.length == 0) {
          this.displaySurway = true;
        } else {
          this.displaySurway = false;
        }
      })
      .catch(err => {
        console.log(err);
      });
  },
  beforeDestroy() {},
  methods: {
    sendValue() {
      var iframe = document.getElementById("game");
      iframe.contentWindow.SendValue();
      iframe.focus();
    },
    changeSchool(param) {
      this.param = param;
      this.displayIframe = true;
    },
    onBack() {
      this.$router.push({
        path: `/ActivityPreview/${this.activityName}`
      });
    },
    onSubmit() {
      this.$validator.validateAll().then(async valid => {
        if (!valid) return;
        const id = this.$store.getters.user.u_id;
        Axios.get(`/api/activityPainting/${id}`).then(res => {
          this.form.u_id = this.$store.getters.user.u_id;
          this.form.u_username = this.$store.getters.user.u_username;
          const request = res.data
            ? Axios.put(`/api/activityPainting/${this.form.u_id}`, this.form) // update old
            : Axios.post("/api/activityPainting", this.form); // create new
          request
            .then(res => {
              this.$store.dispatch("get_user_progress", { id: id });
              if (this.displaySurway) {
                this.$refs.modalSurway.showModal();
              } else {
                this.$router.push({
                  path: `/ActivityPaintingGallery`
                });
              }
            })
            .catch(ex => {
              console.log(ex);
              //this.alertify.error(ex);
            });
        });
      });
    }
  },
  mounted() {
    /*
    this.SendValue();
    */
    if (this.canAddListener) {
      window.addEventListener("message", e => {
        if (String(e.data).match(/(^painting\-)/)) {
          //this.canAddListener = false;
          //const str = String(e.data).split("||");
          //this.form.acp_image = e.data.replace(/(^painting\-)/, "");
          this.form.acp_image = String(e.data).replace(/(^painting\-)/, "");
          //this.form.acp_moto = str[1];
          this.onSubmit();
        } else if (String(e.data).match(/application-loaded/)) {
          this.loading = false;
        } else if (String(e.data).match(/application-back/)) {
          this.onBack();
        }
      });
    }
  }
};
</script>
