  #comment-container {
    width: 90%;
    margin: 0 auto;
  }
}
@media screen and (min-width: 768px) {
  #comment-container {
    position: absolute;
    width: 260px;
    height: auto;
    right: 50px;
    top: 0;
    background: #fff;
    z-index: 99;
  }
  #game {
    border: none;
    height: 100vh;
    width: 100vw;
  }
}
