<template>
  <div class="home">
    <Layout>
      <div class="card">
        <div class="card-body">
          <h3>Admin Home</h3>
          <div class="mt-4" v-if="loadedGraph">
            <h4 class="text-muted text-center">Number of students engaged in each activities</h4>
            <div class="mt-4">
              <bar-chart
                :chartData="chartdata"
                :options="{responsive: true, maintainAspectRatio: false}"
              />
            </div>
          </div>
          <div>
            <div class="mt-4 text-right form-inline">
              <select class="form-control m-1" v-model="queryData" @change="getData">
                <option value="1000">1-1000</option>
                <option value="2000">1001-2000</option>
                <option value="3000">2001-3000</option>
                <option value="4000">3001-4000</option>
                <option value="5000">4001-5000</option>
              </select>
              <button
                type="button"
                class="btn btn-primary"
                @click="downloadFile()"
                v-if="loaded == false"
                disabled
              >
                <i class="fa fa-spinner fa-spin fa-1x fa-fw mt-0"></i>
                <span class="sr-only">Loading...</span> Get Student Data
              </button>
              <button
                type="button"
                class="btn btn-primary"
                @click="downloadFile()"
                v-if="loaded == true"
              >
                <i class="fa fa-download" aria-hidden="true"></i>&nbsp;Download As CSV
              </button>
            </div>
            
          </div>
        </div>
      </div>
    </Layout>
  </div>
</template>
<script>
import Axios from "axios";
import Layout from "@/components/admin/Layout";
import BarChart from "@/components/admin/LineChart.js";

export default {
  components: {
    Layout,
    BarChart
  },
  name:'Home',
  data() {
    return {
      headers: {
        u_id: "Student ID".replace(/,/g, ""), // remove commas to avoid errors
        activity1: "Follow The Path",
        activity2: "Lamduan Flower",
        activity3: "Walk Rally",
        activity4: "Khantok",
        activity5: "Set Your Goal",
        activity6: "Smart Learner",
        activity7: "Teen Lesson",
        //activity8: "School",
        activity9: "Inspiration",
        activity10: "Painting",
        activity11: "Alumni",
        activity12: "Content Contest",
        progress: "progress(%)"
      },
      queryData: 1000,
      fileTitle: "StudentActivity",
      itemsFile: [],
      sumActivity: [],
      loaded: true,
      loadedGraph: false,
      chartdata: {
        labels: [],
        datasets: [
          {
            backgroundColor: "#f87979"
          }
        ]
      },
      options: {
        responsive: true,
        maintainAspectRatio: false
      }
    };
  },
  computed:{

  },
  watch:{

  },
  mounted(){

  },
  created() {
    this.getData();
    //console.log("disable get data")
    
  },
  beforeDestroy(){

  },
  methods: {
    getRandomInt() {
      return Math.floor(Math.random() * (50 - 5 + 1)) + 5;
    },
    getData() {
      var activityData = [];
      var activityName = [];
      Axios.get("/api/account/activity", {
        params: {
          dataLimit: this.queryData
        }
      }).then(res => {
        for (let i in res.data.message) {
          activityName.push(res.data.message[i].name);
          activityData.push(res.data.message[i].sum);
        }
        this.chartdata = {
          labels: activityName,
          datasets: [
            {
              label: "Number of Student",
              backgroundColor: "#00cc66",
              data: activityData
            }
          ]
        };
        this.loadedGraph = true;
      });
    },
    convertToCSV(objArray) {
      var array = typeof objArray != "object" ? JSON.parse(objArray) : objArray;
      var str = "";
      for (var i = 0; i < array.length; i++) {
        var line = "";
        for (var index in array[i]) {
          if (line != "") line += ",";
          line += array[i][index];
        }
        str += line + "\r\n";
      }
      return str;
    },
    exportCSVFile(headers, items, fileTitle) {
      if (headers) {
        items.unshift(headers);
      }

      // Convert Object to JSON
      var jsonObject = JSON.stringify(items);
      var csv = this.convertToCSV(jsonObject);
      var exportedFilenmae = fileTitle + this.queryData + ".csv" || "export.csv";
      var blob = new Blob([csv], { type: "text/csv;charset=utf-8;" });
      if (navigator.msSaveBlob) {
        // IE 10+
        navigator.msSaveBlob(blob, exportedFilenmae);
      } else {
        var link = document.createElement("a");
        if (link.download !== undefined) {
          // feature detection
          // Browsers that support HTML5 download attribute
          var url = URL.createObjectURL(blob);
          link.setAttribute("href", url);
          link.setAttribute("download", exportedFilenmae);
          link.style.visibility = "hidden";
          document.body.appendChild(link);
          link.click();
          document.body.removeChild(link);

        }
      }
    },
    downloadFile() {
      
      this.loaded = false;
      var countAct = 0;
      var progress = 0;
      Axios.get("/api/account/summary", {
        params: {
          dataLimit: this.queryData
        }
      }).then(res => {
        console.log(res)
        this.itemsFile = []
        res.data.message.forEach(item => {
          countAct = 0;
          progress = 0;
          if (item.activity) {
            item.activity.forEach((activity, index) => {
              countAct += activity.done;
            });
          }
         
          progress = (countAct / item.activity.length) * 100;
          this.itemsFile.push({
            u_id: item.u_id,
            activity1: item.activity[0].done,
            activity2: item.activity[1].done,
            activity3: item.activity[2].done,
            activity4: item.activity[3].done,
            activity5: item.activity[4].done,
            activity6: item.activity[5].done,
            activity7: item.activity[6].done,
            activity8: item.activity[7].done,
            activity9: item.activity[8].done,
            activity10: item.activity[9].done,
            activity11: item.activity[10].done,
            progress: progress
          });
        });
        this.exportCSVFile(this.headers, this.itemsFile, this.fileTitle);
        this.loaded = true;
        
      }).catch(err=>{
        console.log(err)
      });
      
      console.log(this.queryData)
    }
  }
};
</script>
