                        </div>
                        <div class="col-md-9">
                          <div class="btn-group">
                            <button
                              type="button"
                              class="btn btn-secondary dropdown-toggle p-2 mt-2 mb-4 pl-4 pr-4"
                              data-toggle="dropdown"
                              aria-haspopup="true"
                              aria-expanded="false"
                            >{{this.form.aln_school}}</button>
                            <div class="dropdown-menu">
                              <a
                                class="dropdown-item"
                                @click="ChangeSelectSchool('เลือกสำนักวิชา....')"
                              >เลือกสำนักวิชา....</a>
                              <div class="dropdown-divider"></div>
                              <div v-for="(item, index) in School" :key="index">
                                <a
                                  class="dropdown-item"
                                  @click="ChangeSelectSchool(item)"
                                >{{item.name}}</a>
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                      <!-- //สาขาวิชา -->
                      <div class="row" v-if="this.form.aln_major != null ">
                        <div class="col-md-3">
                          <label class="mt-3">โปรดระบุสาขาวิชา</label>
                        </div>
                        <div class="col-md-9">
                          <div class="btn-group">
                            <button
                              type="button"
                              class="btn btn-secondary dropdown-toggle p-2 mt-2 mb-4 pl-4 pr-4"
                              data-toggle="dropdown"
                              aria-haspopup="true"
                              aria-expanded="false"
                            >{{this.form.aln_major}}</button>
                            <div class="dropdown-menu">
                              <a
                                class="dropdown-item"
                                @click="ChangeSelectMajor('เลือกสาขาวิชา....')"
                              >เลือกสาขาวิชา....</a>
                              <div class="dropdown-divider"></div>
                              <div v-for="(item, index) in major" :key="index">
                                <a
                                  class="dropdown-item"
                                  @click="ChangeSelectMajor(item.major_name)"
                                >{{item.major_name}}</a>
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                      <!-- //---------DropDown -->
                      <div class="form-group">
                        <div class="row">
                          <div class="col-md-3">
                            <label>ชื่อคลิปวิดีโอ</label>
                          </div>
                          <div class="col-md-9">
                            <input
                              type="text"
                              name="aln_name"
                              v-validate="{ required: true }"
                              :class="{ 'is_invalid': errors.has('aln_name') }"
                              v-model.trim="form.aln_name"
                              class="form-control"
                              :placeholder="this.form.aln_name"
                            />
                            <p class="invalid_feedback">{{ errors.first('aln_name') }}</p>
                          </div>
                        </div>
                      </div>
                      <div class="form-group">
                        <div class="row">
                          <div class="col-md-3">
                            <label>คำอธิบายคลิปวิดีโอ</label>
                          </div>
                          <div class="col-md-9">
                            <textarea
                              class="form-control"
                              :placeholder="this.form.aln_description"
                              rows="10"
                              v-model.trim="form.aln_description"
                            ></textarea>
                            <p class="invalid_feedback">{{ errors.first('aln_description') }}</p>
                          </div>
                        </div>
                      </div>
                      <div class="form-group">
                        <div class="row">
                          <div class="col-md-3">
                            <label class="pb-2">Youtube URL</label>
                          </div>
                          <div class="col-md-9">
                            <input
                              type="text"
                              name="aln_url"
                              v-validate="{
                    required: true
                  }"
                              :class="{ 'is_invalid': errors.has('aln_url') }"
                              v-model.trim="form.aln_url"
                              v-on:change="ytUrlChange"
                              class="form-control"
                              :placeholder="this.form.aln_url"
                            />
                            <p class="invalid_feedback">{{ errors.first('ac_url') }}</p>
                          </div>
                        </div>
                        <div class="row">
                          <div class="col-md-9 offset-md-3">
                            <img
                              v-if="form.aln_url"
                              width="200"
                              class="w-50 mt-2 justify-content-md-center"
                              v-bind:src="`https://img.youtube.com/vi/${form.aln_url_id}/default.jpg`"
                              @click="displayYoutube(form.aln_url_id)"
                            />
                          </div>
                        </div>
                        <!-- <img v-else class="img-fluid mt-2" :src="'/img/no-image.jpg'" alt="no image" /> -->
                      </div>
                      <div class="form-group">
                        <div class="row">
                          <div class="col-md-3">
