                        </div>
                      </div>
                    </form>
                  </div>
                </div>
              </div>
            </div>
            <!-- /////modal -->
          </div>
          <div class="table-responsive mt-3">
            <table id="mainTB" class="table text-center">
              <thead class="thead-dark">
                <tr>
                  <th>#</th>
                  <th>สำนักวิชา</th>
                  <th>สาขาวิชา</th>
                  <th>รูป</th>
                  <th>คลิป</th>
                  <th>ชื่อคลิป</th>
                  <th>คำอธิบาย</th>
                  <th>&nbsp;</th>
                  <!--<th>วันที่แก้ไขล่าสุด</th>-->
                </tr>
              </thead>
              <tbody>
                <tr v-for="(item, index) in activityAlumni.result" :key="index">
                  <td>{{ (index + 1)+((page-1)*activityAlumni.limit) }}</td>
                  <td>{{ item.aln_school }}</td>
                  <td>{{ item.aln_major }}</td>
                  <td>
                    <img v-if="item.aln_img != ''" :src="item.aln_img" class="w100" />
                    <span v-else class="text-center">-</span>
                  </td>
                  <td class="text-center">
                    <img
                      v-if="item.aln_url != ''"
                      v-bind:src="`https://img.youtube.com/vi/${item.aln_url}/default.jpg`"
                      @click="displayYoutube(item.aln_url)"
                    />
                    <span v-else>-</span>
                  </td>
                  <td>{{ item.aln_name }}</td>
                  <td>{{ item.aln_description }}</td>
                  <td>
                    <button
                      type="button"
                      class="btn btn-danger text-white btn-sm"
                      @click.prevent="onDelete(item._id)"
                    >
                      <i class="fa fa-trash"></i>
                    </button>
                  </td>
                  <!--<td>{{ new Date(item.aln_updated).toISOString().replace(/T/, ' ').replace(/\..+/, '') }}</td>-->
                </tr>
              </tbody>
            </table>
          </div>
        </div>
        <div class="p-3" v-if="activityAlumni.rows > activityAlumni.limit">
          <Pagination :data="activityAlumni" :page="page" @onPage="onPage($event)" />
        </div>
      </div>
    </Layout>
    <YoutubeModal ref="ytModal" />
  </div>
</template>
<script>
import Axios from "axios";
import school from "@/school";
import Layout from "@/components/admin/Layout";
import YoutubeModal from "@/components/admin/YoutubeModal.vue";
import Pagination from "@/components/admin/Pagination";
import getVideoId from "get-video-id";
import { mapState } from "vuex";
export default {
  components: {
    Layout,
    Pagination,
    YoutubeModal
  },
  data() {
    return {
      form: {
        aln_major: null,
        aln_school: "เลือกสำนักวิชา....",
        aln_url: "URL...",
        aln_name: "Video Name....",
        aln_description: "Descriptions....",
        aln_url_id: "",
        aln_img: ""
      },
      activityAlumni: {
        aln_school: "",
        aln_url: "",
        aln_name: "",
        aln_img: "",
        aln_description: ""
      },
      page: 1,
      Vote: {
        _id: "",
        total: 0
      },
      UploadVideo: "",
      School: school,
      major: []
    };
  },
  computed: {},
  watch: {
    "$route.params.type": function(type) {
      this.page = 1;
      this.getContent();
    }
  },
  mounted() {},
  created() {
    this.getContent();
  },
  beforeDestroy() {},
  methods: {
    onDelete(_id) {
      this.alertify.confirm("Confirm Deleting", () => {
        Axios.delete(`/api/controllerAlumni/${_id}`).then(() => {
          this.getContent();
        });
      });
    },
    onChangeFile(input) {
      this.form.aln_img = "";
      //console.log(input.files)
      if (input.files && input.files.length > 0) {
        const file = input.files[0];
        if (file.type.indexOf("image/") >= 0) {
          const reader = new FileReader();

          reader.readAsDataURL(file);
          reader.addEventListener("load", () => {
            //console.log(reader.result)
            this.form.aln_img = reader.result;
          });
          //console.log(file);
          return;
        }
      } else {
        this.alertify.warning("กรุณาเลือกภาพที่จะอัพโหลด");
      }
    },
    getContent() {
      let ranking = [];
      Axios.get("/api/controllerAlumni/", {
        params: {
          page: this.page
        }
      })
        .then(res => {
          this.activityAlumni = res.data;
        })
        .catch(err => {
          console.log(err);
        });
    },
    displayYoutube(youtubeID) {
      this.$refs.ytModal.showModal(youtubeID);
    },
    onPage(page) {
      this.page = page;
      this.getContent();
    },
    onSubmit() {
      if (
        this.form.aln_school == "เลือกสำนักวิชา...." ||
        this.form.aln_name == "Video Name...." ||
        this.form.aln_description == "Descriptions...." ||
        this.form.aln_url == "URL..."
      ) {
        this.alertify.error("กรุณาตรวจสอบข้อมูล!!!");
      } else {
        this.$validator.validateAll().then(valid => {
          const ytUrl = this.form.aln_url;
          var ytID = "";
          if (this.form.aln_url != "") {
            ytID = getVideoId(this.form.aln_url).id;
          }
          /*
          if (ytID === undefined) {
            this.alertify.error("กรุณาตรวจสอบ youtube URL!");
            return;
          }
          */
          this.form.aln_url = ytID;
          Axios.post("/api/controllerAlumni/", this.form)
            .then(res => {
              this.getContent();
              this.alertify.success("เพิ่มข้อมูลเรียบร้อย");
              this.form.aln_url = "";
              this.form.aln_school = "เลือกสำนักวิชา....";
              (this.form.aln_major = null), this.$validator.reset();
            })
            .catch(error => {
              this.alertify.error(error.response.data.message);
            });
        });
      }
    },
    onUpdate() {
      this.$validator.validateAll().then(valid => {
        const ytUrl = this.form.aln_url;
        const ytID = getVideoId(this.form.aln_url).id;
        if (ytID === undefined) {
          this.alertify.error("กรุณาตรวจสอบ youtube URL!");
          return;
        }
        this.form.aln_url = ytID;

        this.displayYoutube(this.form.aln_url);

        // Axios.post("/api/activityAlumni/update/", this.form)
        //   .then(res => {
        //     this.alertify.success("อัพเดทข้อมูลเรียบร้อย");
        //     this.getContent();
        //     this.form.aln_url = "";
        //     this.form.aln_school = "";
        //     this.$validator.reset();
        //   })
        //   .catch(error => {
        //     this.alertify.error(error.response.data.message);
        //     this.form.aln_url = ytUrl;
        //   });
      });
    },
    ChangeSelectSchool(value) {
      this.form.aln_school = value.name;
      if (value == "เลือกสำนักวิชา....") {
        this.form.aln_school = value;
        this.form.aln_major = null;
      } else {
        this.form.aln_major = "เลือกสาขาวิชา....";
      }
      this.major = value.major;
    },
    ChangeSelectMajor(value) {
      this.form.aln_major = value;
    },
    ytUrlChange() {
      this.form.aln_url_id = getVideoId(this.form.aln_url).id;
    },
    showEditModal(item) {
      if (item != undefined) {
        const url = "https://youtu.be/" + item.ins_url;
        this.form.aln_img = item.aln_img;
        this.form.aln_school = item.aln_school;
        this.form.aln_major = item.aln_major;
        this.form.aln_name = item.aln_name;
        this.form.aln_description = item.aln_description;
        this.form.aln_url = url;
        this.ytUrlChange();
      } else {
        (this.form.aln_major = null),
          (this.form.aln_school = "เลือกสำนักวิชา...."),
          (this.form.aln_url = "URL..."),
          (this.form.aln_name = "Video Name...."),
          (this.form.aln_description = "Descriptions...."),
          (this.form.aln_url_id = ""),
          (this.form.aln_img = "");
      }
      this.modalData = item;
      this.jquery("#EditModal").modal("show");
    }
  }
};
</script>
<style scoped>
label {
  color: black;
}
#mainTB tbody {
  font-size: 12px;
}
#mainTB thead th {
  white-space: nowrap;
