<template>
  <div class="activity h-100">
    <Layout>
      <div class="flex-container position-relative">
        <div
          id="loading-container"
          v-if="loading"
          class="position-absolute d-flex align-items-center align-middle"
        >
          <div class="m-auto text-center">
            <img src="../assets/img/loader.gif" class="w100 rounded-circle">
            <p
              class="mt-5 d-sm-block d-xs-block d-md-nond d-lg-none"
            >We recommend you to do this activity on a tablet or PC.
            </p>
          </div>
        </div>
        <div id="iframeContainer" class="box box-body">
          <iframe
            id="game"
            :src="'/walkRally/index.html'"
            class="d-inline-block align-self-center text-center"
          ></iframe>
        </div>
        <div class="d-none d-md-block d-lg-block">
          <div class="row d-flex p-4 btnContainer">
            <div class="col-md-4 align-self-center text-center">
              <button @click="onBack()" type="button" class="btn btn-primary w100">BACK</button>
            </div>
            <div class="col-md-4 text-center text-white justify-content-center align-self-center">
              <h3>{{ time }}</h3>
            </div>
            <div class="col-md-4 align-self-center text-center">
              <button
                type="button"
                class="btn btn-primary w100"
                @click.prevent="onFinish()"
                :disabled="!finish"
              >SUBMIT</button>
            </div>
          </div>
        </div>
        <div class="d-md-none d-lg-none d-sm-block d-xs-block">
          <div class="row d-flex p-4 btnContainer">
            <div class="col-12 text-center text-white justify-content-center align-self-center">
              <h3>{{ time }}</h3>
            </div>
            <div class="col-12 mt-2 text-center">
              <button @click="onBack()" type="button" class="btn btn-primary w100 m-3">BACK</button>
              <button
                type="button"
                class="btn btn-primary w100 m-3"
                @click.prevent="onFinish()"
                :disabled="!finish"
              >SUBMIT</button>
            </div>
          </div>
        </div>
      </div>
    </Layout>
    <ModalSurway :activity="activityName" @onComplete="onBack()" ref="modalSurway" />
    <ModalComplete @onComplete="onComplete()" :text="time" ref="modalComplete" />
  </div>
</template>
<script>
import Axios from "axios";
import Layout from "@/components/Layout";
import Complete from "@/components/Complete";
import ModalSurway from "@/components/ModalSurway";
import ModalComplete from "@/components/ModalComplete";
export default {
  components: {
    Layout,
    Complete,
    Axios,
    ModalSurway,
    ModalComplete
  },
  name: "activityWalkRally",
  data() {
    return {
      form: {
        u_username: "",
        u_id: ""
      },
      displaySurway: false,
      activityName: this.activity[2].name,
      finish: false,
      loading: true,
      time: "00:00:00",
      duration: 0
    };
  },
  computed: {},
  watch: {},
  mounted() {
    this.form.u_id = this.$store.getters.user.u_id;
    this.form.u_username = this.$store.getters.user.u_username;
    window.addEventListener("message", e => {
      if (String(e.data).match(/walkRally/)) {
        this.finish = true;
      } else if (String(e.data).match(/application-loaded/)) {
        this.loading = false;
        this.duration = 0;
        this.startTimer();
      }
    });
  },
  created() {
    const id = this.$store.getters.user.u_id;
    Axios.get(`/api/surway/${id}?activity=${this.activityName}`)
      .then(res => {
        if (res.data.length == 0) {
          this.displaySurway = true;
        } else {
          this.displaySurway = false;
        }
      })
      .catch(err => {
        //console.log(err);
      });
  },
  beforeDestroy() {},
  methods: {
    onBack() {
      this.$router.push({
        path: `/ActivityPreview/${this.activityName}`
      });
    },
    startTimer() {
      setTimeout(() => {
        this.duration += 1;

        if (!this.finish) {
          this.startTimer();
        }
      }, 1000);
      var date = new Date(0);
      date.setSeconds(this.duration); // specify value for SECONDS here
      this.time = date.toISOString().substr(11, 8);
    },
    onFinish() {
      this.$refs.modalComplete.showModal();
    },
    onComplete() {
      this.$refs.modalComplete.hideModal();
      this.form.u_id = this.$store.getters.user.u_id;
      this.form.u_username = this.$store.getters.user.u_username;
      Axios.get(`/api/activityWalkRally/${this.form.u_id}`).then(res => {
        if (!res.data) {
          Axios.post("/api/activityWalkRally/", this.form).then(res => {
            this.$store.dispatch("get_user_progress", {
              id: this.$store.getters.user.u_id
            });
            if (this.displaySurway) {
              this.$refs.modalSurway.showModal();
            } else {
              this.onBack();
            }
          });
        } else {
          this.onBack();
        }
      });
    }
  }
};
</script>
