<template>
  <div id="surwayModal" class="modal" tabindex="-1" role="dialog">
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <form @submit.prevent="onSubmit()">
          <div class="modal-header">
            <h5 class="modal-title">แบบประเมินความพึงพอใจ</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <p>กิจกรรมของคุณจะได้รับการบันทึกเมื่อคุณทำแบบสอบถามเสร็จเรียบร้อย</p>
            <div class="mt-4 text-center">
              <star-rating
                inline
                @rating-selected="setRating"
                text-class="d-none"
                :rating="this.form.s_rating"
              ></star-rating>
            </div>
            <div class="mt-4 form-group">
              <textarea
                class="form-control"
                v-model="form.s_comment"
                rows="4"
                placeholder="ความเห็นและข้อเสนอแนะ"
              ></textarea>
            </div>
          </div>
          <div class="modal-footer">
            <button type="submit" class="btn btn-primary">บันทึกข้อมูล</button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>
<script>
import Axios from "axios";
import StarRating from "vue-star-rating";
export default {
  components: {
    StarRating
  },
  props: {
    activity: {
      type: String,
      required: true
    },
    returnName: {
      type: String
    }
  },
  data() {
    return {
      form: {
        u_id: "",
        u_username: "",
        s_activity: "",
        s_rating: 3,
        s_comment: ""
      }
    };
  },

  methods: {
    showModal() {
      this.jquery("#surwayModal").modal("show");
    },
    setRating(rating) {
      this.form.s_rating = rating;
    },
    onSubmit() {
      this.form.u_id = this.$store.getters.user.u_id;
      this.form.u_username = this.$store.getters.user.u_username;
      this.form.s_activity = this.activity;
      Axios.post("/api/surway", this.form)
        .then(res => {
          if (this.returnName) this.$router.push({ name: this.returnName });
          this.alertify.success("บันทึกแบบสำรวจกิจกรรมเรียบร้อย");
          this.jquery("#surwayModal").modal("hide");
        })
        .catch(error => {
          //this.alertify.error(error.response.data.message);
          this.jquery("#surwayModal").modal("hide");
        });
    }
  }
};
</script>
