              <div class="modal-body">
                <form class="form-row" @submit.prevent="onSubmit()">
                  <div class="form-group col-md-12">
                    <input
                      type="text"
                      name="ac_name"
                      v-validate="{
                    required: true
                  }"
                      :class="{ 'is_invalid': errors.has('ac_name') }"
                      v-model.trim="form.ac_name"
                      class="form-control"
                      placeholder="ชื่อคลิปวิดีโอ"
                    />
                    <p class="invalid_feedback">{{ errors.first('ac_name') }}</p>
                  </div>
                  <div class="form-group col-md-12">
                    <input
                      type="text"
                      name="ac_description"
                      v-validate="{
                    required: true
                  }"
                      :class="{ 'is_invalid': errors.has('ac_description') }"
                      v-model.trim="form.ac_description"
                      class="form-control"
                      placeholder="คำอธิบายคลิปวิดีโอ"
                    />
                    <p class="invalid_feedback">{{ errors.first('ac_description') }}</p>
                  </div>
                  <div class="form-group col-md-12">
                    <input
                      type="text"
