</template>
<script>
import Axios from "axios";
import Layout from "@/components/admin/Layout";
import YoutubeModal from "@/components/admin/YoutubeModal.vue";
import Pagination from "@/components/admin/Pagination";
import getVideoId from "get-video-id";
import { mapState } from "vuex";
export default {
  components: {
    Layout,
    Pagination,
    YoutubeModal
  },
  name:'ContentContest',
  data() {
    return {
      ShowContent:{
        _id:"",
        ac_contentcontest:""
      },
      form: {
        u_id: "",
        ac_url: "",
        ac_name: "",
        ac_description: "",
        ac_vote: false
      },

      activityContest: {
        u_id: "",
        ac_url: "",
        ac_name: "",
        ac_description: "",
        ac_vote: false,

        ac_order: ""
      },
      modalData: {
        u_id: "",
        ac_url: "",
        ac_name: "",
        ac_description: "",
        ac_vote: false,
        avc: 0,
        ac_order: "",
        ac_updated: ""
      },
      page: 1,

      Vote: {
        _id: "",
        total: 0
      },
      activityContestRanking: [],
      sort: "desc"
    };
  },
  computed:{

  },
  watch: {
    "$route.params.type": function(type) {
      this.page = 1;
      this.ShowContent._id = type;
      this.$validator.reset();
      this.getContent();
    }
  },
  mounted(){
    this.getContent();
  },
  created(){
    this.ShowContent._id = this.$route.params.type;
  },
  beforeDestory(){

  },
  methods: {
    showDetailModal(item) {
      this.modalData = item;
      this.jquery("#detailModal").modal("show");
    },
    getContent() {
      let ranking = [];
      Axios.get("/api/controllerContest/vote", {
        params: {
          page: this.page,
          sort: this.sort
        }
      })
        .then(res => {
          this.activityContest = res.data;
        })
        .catch(err => {
          console.log(err);
        });
      
        Axios.get("/api/setting/").then(res => {
          // console.log(res.data.ac_contentcontest)
          this.ShowContent._id = res.data._id
          this.ShowContent.ac_contentcontest = res.data.ac_contentcontest
        })
    },
    displayYoutube(youtubeID) {
      this.$refs.ytModal.showModal(youtubeID);
    },
    onDelete(item) {
      this.alertify.confirm("Confirm Deleting?", () => {
        Axios.delete(`/api/controllerContest/${item.u_id}`)
          .then(() => {
            this.getContent();
          })
          .catch(error => this.alertify.error(error));
      });
    },
    toggleSort() {
      if (this.sort == "asc") {
        this.sort = "desc";
      } else if (this.sort == "desc") {
        this.sort = "asc";
      }
      this.getContent();
    },
    onPage(page) {
      this.page = page;
      this.getContent();
    },
    onSubmit() {
      this.$validator.validateAll().then(valid => {
        const ytUrl = this.form.ac_url;
        const ytID = getVideoId(this.form.ac_url).id;
        if (ytID === undefined) {
          this.alertify.error("กรุณาตรวจสอบ youtube URL!");
          return;
        }
        this.form.ac_url = ytID;

        Axios.post("/api/controllerContest", this.form)
          .then(res => {
            this.alertify.success("เพิ่มข้อมูลเรียบร้อย");
            this.getContent();
            this.form.ac_url = "";
            this.form.u_id = "";
            this.form.ac_vote = false;
            this.$validator.reset();
          })
          .catch(error => {
            this.alertify.error(error.response.data.message);
            this.form.ac_url = ytUrl;
          });
      });
    },

    onShuffle() {
      Axios.post("/api/controllerContest/shuffle")
        .then(res => {
          this.alertify.success("Shuffle complete");
        })
        .catch(error => {
          this.alertify.error(error);
        });
    },
    onOpenContent(){
      this.ShowContent.ac_contentcontest = !this.ShowContent.ac_contentcontest
      Axios.post("/api/setting/",this.ShowContent)
        .then(res => {
          console.log(res.data)
          if(this.ShowContent.ac_contentcontest == true){
          this.alertify.success("Open Content");
          }else{
          this.alertify.error("Close Content");
          }
        })
        .catch(error => {
          this.alertify.error("error");
        });
    }
  }
};
</script>
