<script>
import Layout from "@/components/Layout";
import ModalActivity from "@/components/ModalActivity";
import ModalTutorial from "@/components/ModalTutorial";
//import ModalNotification from "@/components/ModalNotification";
import Timer from "@/components/Timer";
import Axios from "axios";

export default {
  components: {
    Layout,
    ModalActivity,
    ModalTutorial,
    Timer,
    //ModalNotification,
  },
  name: "home",
  data() {
    return {
      preview: { name: "", description: "", image: "", type: "" },
      activityItem: [],
      activityIndex: -1,
      tmpProgress: [],
      loaded: false,
      view: 0,
      setting: {
        admin_av_yt_id: "NzQ5SsQPBMM",
        admin_av_autoplay: 1,
      },
      player: null,
      finished: false,
      duration: 0,
      displayVote: true,
    };
  },
  computed: {},

  watch: {},
  mounted() {
    this.getContent();
    window.addEventListener("resize", this.resizeYT);
    //console.log(this.jquery("#colosseum").length)
    //this.jquery("[data-toggle='popover']").popover();
  },
  created() {
    //console.log(this.$store.getters.userProgress.activity)
    //console.log("----")
    this.tmpProgress = this.$store.getters.userProgress.activity;
    this.view = this.$store.getters.user.u_view;
    Axios.get(`/api/account/${this.$store.getters.user.u_id}`).then((res) => {
      this.view = res.data.u_view;
    });
  },
  beforeDestroy() {
    window.removeEventListener("resize", this.resizeYT);
  },
  methods: {
    displayNotice() {
      this.$root.$emit("component1");
      //this.$refs.noticeModal.showModal();
    },
    resizeYT() {
      this.ytHeight = (this.jquery("#ytContainer").width() * 9) / 16;
      this.jquery("#ytContainer").height(this.ytHeight);
      this.jquery("#ytContainer").css("margin-top", -(this.ytHeight / 2));
    },
    getContent() {
      this.jquery("#sci").popover();
      this.jquery("#statue").popover();
      this.jquery("#vill").popover();
      //console.log(this.setting);
      Axios.get("/api/setting/").then((res) => {
        this.displayVote = res.data.ac_contentcontest;
        this.jquery("#colosseum").popover();
        //if (res.data.ac_contentcontest == false) {
        //this.jquery("#colosseum").hide();
        //}
      });
    },
    onPreview(index, top, left) {
      this.jquery("#colosseum").popover("hide");
      this.jquery("#sci").popover("hide");
      this.jquery("#statue").popover("hide");
      this.jquery("#vill").popover("hide");
      const act = this.activity[index];
      this.activityIndex = index;
      this.activityItem = act;
      this.$store.dispatch("get_activity", { activity: act });
      this.$refs.modalActivity.showModal(top, left);
    },
    displayTutorial() {
      this.$refs.modalTutorial.showModal();
    },
    PopUp() {
      document.getElementById("ytContainer").style.display = "none";
    },

    onSubmit() {
      const UserId = this.$store.getters.user.u_id;
      Axios.post("/api/account/view", {
        Id: UserId,
      })
        .then((res) => {
          /*
          this.$store.dispatch("get_user_progress", {
            id: UserId
          });
          */
          this.view = 1;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    onFinish() {
      this.$refs.youtube.player.seekTo(0);
      this.$refs.youtube.player.pauseVideo();
      this.finished = true;
    },
    ready() {
      this.player = this.$refs.youtube.player;
      if (this.setting.admin_av_autoplay == 1) {
        this.player.seekTo(0);
        this.player.playVideo();
      }
      this.resizeYT();
    },
    async playing() {
      this.duration = await this.player.getDuration();
      this.videoTime = await this.player.getCurrentTime();
      if (!this.finished) {
        this.$refs.timer.setDuration(this.duration - this.videoTime);
        this.$refs.timer.onResume();
      }
    },
    paused() {
      if (!this.finished) {
        this.$refs.timer.onPause();
      }
    },
  },
};
</script>
