            v-bind:style="{ top:randomPosition[index].top+'%',left: randomPosition[index].left+'%', backgroundImage: 'url(' +item.af_image + ')' }"
            :key="index"
            @click.prevent="showImgThumb(item)"
          ></div>
          <!--<img src="../assets/img/LamduanBG.png" class="w-75 mt-3 m-auto align-self-center" />-->
        </div>

