</template>
<script>
import Random from "@/random";
import Axios from "axios";
import Layout from "@/components/Layout";
import Pagination from "@/components/Pagination";
export default {
  components: {
    Pagination,
    Random,
    Layout
  },
  name: "lamduanGallery",
  data() {
    return {
      picture: {
        img: "",
        name: "",
        desc: ""
      },
      searchId: "",
      searchFound: false,
      page: 1,
      activityFlowerImg: [],
      activityName: "",
      randomPosition: Random
    };
  },
  computed: {},
  watch: {},
  mounted() {},
  created() {
    this.getData({
      params: {
        page: 1
      }
    });
    const activity = this.activity[1]
    this.activityName = activity.name;
    this.$store.dispatch("get_activity", { activity: activity });
   
  },
  beforeDestroy() {},
  methods: {
    onPage(page) {
      this.page = page;
      this.getData({
        params: {
          page: this.page
        }
      });
    },
    onHome() {
      this.$router.push({
        name: `home`
      });
    },
    onBack() {
      this.$router.push({
        path: `/ActivityPreview/${this.activityName}`
      });
    },
    getData(parem) {
      Axios.get("/api/activityFlower", parem)
        .then(res => {
          console.log(res.data);
          this.activityFlowerImg = res.data;
        })
        .catch(err => {
          //this.alertify.error(err);
        });
    },
    onSearch() {
      this.page = 1;
      Axios.get(`/api/activityFlower/${this.searchId}`).then(res => {
        //console.log(res)
        this.showImgThumb(res.data);
      });
    },
    showImgThumb(item) {
      if (item != "") {
        this.picture.img = item.af_image;
        this.picture.name = item.u_username;
        this.picture.desc = item.af_description;
        this.searchFound = true;
      } else {
        this.searchFound = false;
      }
      this.jquery("#img-thumb").modal("show");
    }
  }
};
</script>
