<template>
  <div class="activity h100">
    <Layout>
      <div class="container">
        <div id="main" class="row position-relative h-100">
          <div class="col-md-7 position-relative">
            <div v-if="setting.admin_av_hide_controller == 0" class="hideController"></div>
            <div id="ytContainer" class="ytContainer">
              <youtube
                v-if="loaded"
                class="w-100 h-100"
                ref="youtube"
                @ready="ready"
                @playing="playing"
                @paused="paused"
               
                :video-id="setting.admin_av_yt_id"
                :player-vars="{ autoplay: 0, controls: setting.admin_av_hide_controller,modestbranding:1, rel: 0, showinfo: 0 }"
              ></youtube>
            </div>
          </div>
          <div class="col-md-5">
            <div class="text-right mt-3">
              <small>
                <a @click.prevent="toggleLang('th')">TH</a> |
                <a @click.prevent="toggleLang('en')">EN</a>
              </small>
            </div>
            <h3 class="mt-4 text-center">{{ title }}</h3>
            <h2
              v-if="lang=='th'"
              class="text-primary font-weight-bold text-center"
            >{{ subtitle.th }}</h2>
            <h2
              v-if="lang=='en'"
              class="text-primary font-weight-bold text-center"
            >{{ subtitle.en }}</h2>

            <div v-if="lang=='en'" class="text-center mt-3">{{ descEN }}</div>
            <div v-else-if="lang=='th'" class="text-center mt-2">{{ descTH }}</div>
            <div class="text-center m-3">
              <a @click.prevent="onBack()" class="btn btn-success mr-4 w100">Back</a>

              <Timer v-if="!finished" @onFinish="onFinish($event)" ref="timer" />
              <!--<button
                v-else
                class="btn btn-primary w100"
                type="button"
                @click.prevent="onEndActivity()"
              >NEXT</button>-->
              <button
                v-else
                class="btn btn-primary w100"
                type="button"
                @click.prevent="saveActivity()"
              >NEXT</button>
              
            </div>
          </div>
        </div>
      </div>
    </Layout>
    <ModalSurway :activity="activityName" @onComplete="onBack()" ref="modalSurway" />
  </div>
</template>
<script>
import Axios from "axios";
import ModalSurway from "@/components/ModalSurway";
import Complete from "@/components/Complete";
import Timer from "@/components/Timer";
import Layout from "@/components/Layout";
export default {
  components: {
    Axios,
    Timer,
    ModalSurway,
    Complete,
    Layout
  },
  name: "",
  data() {
    return {
      form: {
        u_id: null,
        u_username: null,
        av_name: null
      },
      setting: {
        admin_av_autoplay: null,
        admin_av_hide_controller: null,
        admin_av_disable_msg: null,
        admin_av_enable: null,
        admin_av_yt_id: null
      },
      loaded: false,
      player: null,
      lang: "en",
      duration: 0,
      display: "video",
      title: "",
      subtitle: "",
      descEN: "",
      descTH: "",
      activityName: "",
      finished: false,
      ytHeight: 0
    };
  },
  computed: {},
  watch: {},
  mounted() {
    window.addEventListener("resize", this.resizeYT);
  },
  beforeCreate() {},
  created() {
    this.initialValue();
  },
  beforeDestroy() {
    window.removeEventListener("resize", this.resizeYT);
  },
  methods: {
    resizeYT() {
      this.ytHeight = (this.jquery("#ytContainer").width() * 9) / 16;
      this.jquery("#ytContainer").height(this.ytHeight);
    },
    initialValue() {
      this.title = this.$store.getters.activity.title;
      this.subtitle = this.$store.getters.activity.subtitle;
      this.descEN = this.$store.getters.activity.description.en;
      this.descTH = this.$store.getters.activity.description.th;
      this.activityName = this.$store.getters.activity.name;
      this.duration = 0;

      Axios.get(`/api/controllerVideo/${this.$store.getters.activity.name}`)
        .then(res => {
          this.setting.admin_av_autoplay = res.data.admin_av_autoplay ? 1 : 0;
          this.setting.admin_av_hide_controller = res.data
            .admin_av_hide_controller
            ? 0
            : 1;
          this.setting.admin_av_enable = res.data.admin_av_enable;
          this.setting.admin_av_disable_msg = res.data.admin_av_disable_msg;
          this.setting.admin_av_yt_id = res.data.admin_av_yt_id;
          this.loaded = true;
        })
        .catch(err => {
          console.log(err);
        });
    },
    ready() {
      this.player = this.$refs.youtube.player;
      if (this.setting.admin_av_autoplay == 1) {
        this.player.seekTo(0);
        this.player.playVideo();
      }
      this.resizeYT();
    },
    async playing() {
      this.duration = await this.player.getDuration();
      this.videoTime = await this.player.getCurrentTime();
      if (!this.finished) {
        this.$refs.timer.setDuration(this.duration - this.videoTime);
        this.$refs.timer.onResume();
      }
    },
    paused() {
      if (!this.finished) {
        this.$refs.timer.onPause();
      }
    },
    saveActivity() {
      //console.log("saveActivity")
      const id = this.$store.getters.user.u_id;
      Axios.get(`/api/activityVideo/${id}?av_name=${this.activityName}`).then(
        res => {
          //console.log(res)
          if (!res.data) {
             //console.log("add data")
            this.form.u_id = id;
            this.form.u_username = this.$store.getters.user.u_username;
            this.form.av_name = this.activityName;
            Axios.post("/api/activityVideo", this.form).then(res => {
              this.$store.dispatch("get_user_progress", {
                id: this.$store.getters.user.u_id
              });
              this.onEndActivity()
              //this.onBack();
            });
          } else {
            //console.log("back")
            this.onBack();
          }
        }
      );
    },
    onFinish() {
      this.$refs.youtube.player.seekTo(0);
      this.$refs.youtube.player.pauseVideo();
      /*
      const id = this.$store.getters.user.u_id;
      Axios.get(`/api/activityVideo/${id}?av_name=${this.activityName}`).then(
        res => {
          if (!res.data) {
            this.form.u_id = id;
            this.form.u_username = this.$store.getters.user.u_username;
            this.form.av_name = this.activityName;
            Axios.post("/api/activityVideo", this.form).then(res => {
              this.$store.dispatch("get_user_progress", {
                id: this.$store.getters.user.u_id
              });
            });
          }
        }
      );
      */
      this.finished = true;
    },
    onEndActivity() {
      //console.log(this.$store.getters.user)
      //console.log(this.$store.getters.activity)
      const id = this.$store.getters.user.u_id;
      Axios.get(`/api/surway/${id}?activity=${this.activityName}`)
        .then(res => {
          if (res.data.length == 0) {
            this.$refs.modalSurway.showModal();
          } else {
            this.onBack();
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    onBack() {
      this.$router.push({
        path: `/ActivityPreview/${this.activityName}`
      });
    },
    toggleLang(lang) {
      this.lang = lang;
    }
  }
};
</script>
<style scoped>
.ytContainer {
  margin-top: 50px;
}
@media screen and (max-width: 768px) {
  .ytContainer {
    margin-top: 0;
  }
}
.hideController {
  position: absolute;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0;
  z-index: 9999;
  background: #000;
  opacity: 0;
}
</style>