<template>
  <div
    id="surwayModal"
    class="modal fade bd-example-modal-lg"
    tabindex="-1"
    role="dialog"
    aria-labelledby="myLargeModalLabel"
    aria-hidden="true"
    data-backdrop="static"
    data-keyboard="false"
  >
    <div class="modal-dialog modal-dialog-centered modal-sm">
      <div class="modal-content">
        <div v-if="!completed" class="modal-body p-3 text-center">
          <form @submit.prevent="onSubmit()">
            <h3 class="text-muted mt-3">SURVEY</h3>
            <h1
              class="text-primary text-center font-weight-bolder"
            >{{ this.$store.getters.activity.subtitle.en }}</h1>
            <div class="text-center mt-5">
              <star-rating
                inline
                @rating-selected="setRating"
                text-class="d-none"
                :rating="this.form.s_rating"
              ></star-rating>
            </div>

            <div class="mt-5 form-group">
              <textarea
                class="form-control"
                v-model="form.s_comment"
                rows="4"
                placeholder="Add comment"
              ></textarea>
            </div>

            <button v-if="!submited" type="submit" class="btn btn-primary w100 mt-3 mb-3">SUBMIT</button>
            <div class="text-center" v-else>Please Wait</div>
          </form>
        </div>
        <div v-else class="modal-body p-3 text-center">
          <h3 class="text-muted mt-3">SURVEY</h3>
          <h1 class="text-primary text-center font-weight-bolder">COMPLETE</h1>
          <img src="../assets/img/telegram.png" class="w-75 img-fluid mt-3 d-block m-auto" />
          <div class="mt-3 mb-3">
            <button
              type="button"
              class="btn btn-primary w100 d-block m-auto"
              data-dismiss="modal"
              aria-label="Close"
              @click.prevent="onComplete()"
            >COMPLETE</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Axios from "axios";
import StarRating from "vue-star-rating";
export default {
  components: {
    StarRating
  },
  props: {
    activity: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      form: {
        u_id: "",
        u_username: "",
        s_activity: "",
        s_rating: 0,
        s_comment: ""
      },
      completed: false,
      submited: false
    };
  },
  methods: {
    setRating(rating) {
      this.form.s_rating = rating;
    },
    showModal() {
      this.jquery("#surwayModal").modal("show");
      this.jquery(".flex-container").addClass("blur");
    },
    hideModal() {
      this.jquery(".flex-container").removeClass("blur");
    },
    onSubmit() {
      this.submited = true
      this.form.u_id = this.$store.getters.user.u_id;
      this.form.u_username = this.$store.getters.user.u_username;
      this.form.s_activity = this.activity;
      if (this.form.s_rating == 0) {
        this.alertify.error("กรุณาเลือก rating อย่างน้อย 1");
      } else {
        Axios.post("/api/surway", this.form)
          .then(res => {
            this.completed = true;
            //this.onSurwayFinished();
          })
          .catch(err => {
            console.log(err);
          });
      }
    },
    onComplete() {
      this.$emit("onComplete");
    }
  }
};
</script>
<style scoped>
h3 {
  font-size: 1rem;
}
h1 {
  font-size: 1.5rem;
}
</style>