<template>
  <div class="activity h100">
    <Layout>
      <div class="container">
        <div id="main" class="row position-relative h-100">
          <div class="col-md-7">
            <div class="position-relative w-100 ytContainer">
              <div v-if="setting.admin_av_hide_controller == 0" class="hideController"></div>
              <div id="ytContainer" class="ytContainer">
                <youtube
                  ref="youtube"
                  class="w-100 h-100"
                  v-if="loaded"
                  @ready="ready"
                  @playing="playing"
                  @paused="paused"
                  :video-id="setting.admin_als_pyt_id"
                  :player-vars="{ autoplay: 0, controls: setting.admin_av_hide_controller, rel: 0 }"
                ></youtube>
              </div>
            </div>
          </div>
          <div class="col-md-5">
            <div class="text-right">
              <small>
                <a @click.prevent="toggleLang('th')">TH</a> |
                <a @click.prevent="toggleLang('en')">EN</a>
              </small>
            </div>
            <h3 class="mt-4 text-center">{{ title }}</h3>

            <h2
              v-if="lang=='th'"
              class="text-primary font-weight-bold text-center"
            >{{ subtitle.th }}</h2>
            <h2
              v-if="lang=='en'"
              class="text-primary font-weight-bold text-center"
            >{{ subtitle.en }}</h2>

            <div v-if="lang=='en'" class="text-justify">{{ descEN }}</div>
            <div v-else-if="lang=='th'" class="text-justify">{{ descTH }}</div>
            <!--
            <div
              v-if="setting.admin_als_enable_link"
              @click.prevent="play()"
              class="row justify-content-center mt-1"
            >
              <div class="col-8 text-center">
                <div class="d-table m-auto">
                  <div class="d-table-row">
                    <div class="d-table-cell align-middle text-center p-1">
                      <div class="squre-icon">
                        <i class="fa fa-globe"></i>
                      </div>
                    </div>
                    <div class="d-table-cell text-left p-1">
                      <small class="text-primary font-weight-bold">TEEN LESSON (LIVE STEAM)</small>
                      <br />
                      <small class="text-muted smallest">CLICK. GO TO LINK</small>
                    </div>
                  </div>
                </div>
              </div>
            </div>-->
            <div class="text-center m-3">
              <button type="button" @click.prevent="onBack()" class="btn btn-success mr-4 w100">Back</button>

              <button
                v-if="!setting.admin_als_enable_done"
                disabled
                class="btn btn-secondary w100 disabled"
                type="button"
              >NEXT</button>
              <!--<button
                v-else-if="setting.admin_als_enable_done"
                class="btn btn-primary w100"
                type="button"
                @click.prevent="onEndActivity()"
              >NEXT</button>-->
              <div v-if="setting.admin_als_enable_done" class="d-inline-block">
                <Timer v-if="!finished" @onFinish="onFinish($event)" ref="timer" />
                <button
                  v-else-if="setting.admin_als_enable_done"
                  class="btn btn-primary w100"
                  type="button"
                  @click.prevent="saveActivity()"
                >NEXT</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Layout>
    <ModalSurway :activity="activityName" @onComplete="onBack()" ref="modalSurway" />
  </div>
</template>
<script>
import Axios from "axios";
import ModalSurway from "@/components/ModalSurway";
import Complete from "@/components/Complete";
import Timer from "@/components/Timer";
import Layout from "@/components/Layout";
export default {
  components: {
    Axios,
    ModalSurway,
    Complete,
    Layout,
    Timer
  },
  data() {
    return {
      form: {
        u_id: null,
        u_username: null,
        av_name: null,
      },
      setting: {
        admin_als_pyt_id: "",
        admin_als_yt_link: "",
        admin_als_enable_done: false,
        admin_als_enable_link: false,
        admin_av_hide_controller: 0,
      },
      player: null,
      lang: "en",
      duration: 0,
      display: "video",
      title: "",
      subtitle: "",
      descEN: "",
      descTH: "",
      activityName: "",
      ytHeight: 0,
      finished: false,
      loaded: false,
    };
  },
  computed: {},
  watch: {},
  mounted() {
    window.addEventListener("resize", this.resizeYT);
  },
  created() {
    this.initialValue();
  },
  beforeDestroy() {
    window.addEventListener("resize", this.resizeYT);
  },
  methods: {
    initialValue() {
      const activity = this.activity[6];
      this.title = activity.title;
      this.subtitle = activity.subtitle;
      this.descEN = activity.description.en;
      this.descTH = activity.description.th;
      this.activityName = activity.name;
      this.$store.dispatch("get_activity", { activity: this.activity[6] });
      this.duration = 0;

      Axios.get(`/api/controllerLiveStreaming`)
        .then((res) => {
          this.setting.admin_als_pyt_id = res.data.admin_als_pyt_id;
          this.setting.admin_als_yt_link = res.data.admin_als_yt_link;
          this.setting.admin_als_enable_done = res.data.admin_als_enable_done;
          this.setting.admin_als_enable_link = res.data.admin_als_enable_link;
          this.loaded = true;
          console.log(this.form);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    resizeYT() {
      this.ytHeight = (this.jquery("#ytContainer").width() * 9) / 16;
      this.jquery("#ytContainer").height(this.ytHeight);
    },
    ready() {
      this.player = this.$refs.youtube.player;
      this.player.seekTo(0);
      this.player.playVideo();
      this.resizeYT();
    },
    async playing() {
      this.duration = await this.player.getDuration();
      this.videoTime = await this.player.getCurrentTime();
      if (!this.finished) {
        this.$refs.timer.setDuration(this.duration - this.videoTime);
        this.$refs.timer.onResume();
      }
    },
    play() {
      /*
      this.jquery("#youtube-foreground")
        .removeClass("d-flex")
        .fadeOut();
      */
      window.open(this.setting.admin_als_yt_link, "_blank");
    },
    paused() {
      if (!this.finished) {
        this.$refs.timer.onPause();
      }
    },
    onFinish() {
      this.$refs.youtube.player.seekTo(0);
      this.$refs.youtube.player.pauseVideo();
      this.finished = true;
    },
    saveActivity() {
      //this.$refs.youtube.player.seekTo(0);
      //this.$refs.youtube.player.pauseVideo();
      const id = this.$store.getters.user.u_id;

      Axios.get(`/api/activityVideo/${id}?av_name=${this.activityName}`).then(
        (res) => {
          if (!res.data) {
            this.form.u_id = id;
            this.form.u_username = this.$store.getters.user.u_username;
            this.form.av_name = this.activityName;
            Axios.post("/api/activityVideo", this.form).then((res) => {
              /*
              this.$store.dispatch("get_user_progress", {
                id: this.$store.getters.user.u_id
              });
              */
              this.onEndActivity();
            });
          } else {
            this.onBack();
          }
        }
      );
    },
    onEndActivity() {
      const id = this.$store.getters.user.u_id;
      Axios.get(`/api/surway/${id}?activity=${this.activityName}`)
        .then((res) => {
          /*
          if(this.setting.admin_als_enable_done){
            this.onFinish();
          }
          */

          if (res.data.length == 0) {
            this.$refs.modalSurway.showModal();
          } else {
            this.onBack();
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    onBack() {
      this.$router.push({
        path: `/ActivityPreview/${this.activityName}`,
      });
    },
    toggleLang(lang) {
      this.lang = lang;
    },
  },
};
</script>
<style scoped>
#youtube-foreground {
  position: absolute;
  left: 0;
  top: 5px;
  right: 0;
  bottom: 5px;
  background-color: rgba(0, 0, 0, 0.5);
}
.squre-icon-yt {
  line-height: 60px;
  width: 60px;
  height: 60px;
  color: #7c8184;
  background-color: #ebebeb;
  font-size: 2rem;
}
.hideController {
  position: absolute;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0;
  z-index: 9999;
  background: #000;
  opacity: 0;
}
.ytContainer {
  margin-top: 50px;
}
@media screen and (max-width: 768px) {
  .ytContainer {
    margin-top: 0;
  }
}
</style>
