<template>
  <div class="home">
    <Layout>
      <div class="card">
        <div class="card-body">
          <h3>Notification</h3>
          <form class="form mt-5" @submit.prevent="onSubmit()">
            <div class="form-group">
              <div class="row">
                <div class="col-md-3">
                  <label>Title</label>
                </div>
                <div class="col-md-9">
                  <input
                    type="text"
                    name="no_title"
                    v-validate="{
                    required: true
                  }"
                    :class="{ 'is_invalid': errors.has('no_title') }"
                    v-model.trim="form.no_title"
                    class="form-control"
                    placeholder="Title name...."
                  />
                  <p class="invalid_feedback">{{ errors.first('no_title') }}</p>
                </div>
              </div>
            </div>
            <div class="form-group">
              <div class="row">
                <div class="col-md-3">
                  <label>Message</label>
                </div>
                <div class="col-md-9">
                  <textarea
                    class="form-control"
                    placeholder="Descriptions...."
                    rows="10"
                    v-model.trim="form.no_message"
                  ></textarea>
                  <p class="invalid_feedback">{{ errors.first('no_message') }}</p>
                </div>
              </div>
            </div>
            <div class="form-group">
                <div class="row">
                  <div class="col-md-3">
                    <label class="pb-2">Youtube URL</label>
                  </div>
                  <div class="col-md-9">
                    <input
                      type="text"
                      name="no_url"
                      v-validate="{
                    required: true
                  }"
                      :class="{ 'is_invalid': errors.has('no_url') }"
                      v-model.trim="form.no_url"
                      v-on:change="ytUrlChange"
                      class="form-control"
                      :placeholder="this.form.no_url"
                    />
                    <p class="invalid_feedback">{{ errors.first('no_url') }}</p>
                  </div>
                </div>
                <div class="row">
                  <div class="col-md-9 offset-md-3">
                    <img
                      v-if="form.no_url"
                      width="200"
                      class="w-50 mt-2 justify-content-md-center"
                      v-bind:src="`https://img.youtube.com/vi/${form.no_url_id}/default.jpg`"
                      @click="displayYoutube(form.no_url_id)"
                    />
                  </div>
                </div>
                <!-- <img v-else class="img-fluid mt-2" :src="'/img/no-image.jpg'" alt="no image" /> -->
              </div>
            <div class="row">
              <div class="col-md-3"></div>
              <div class="col-md-5">
                <button type="submit" class="btn btn-primary btn-md mb-3">Submit</button>
              </div>
              <div class="col-md-4"></div>
            </div>
          </form>
          <div class="table-responsive mt-3">
            <table class="table text-center">
              <thead class="thead-dark">
                <tr>
                  <th>#</th>
                  <th>Title</th>
                  <th>Clip video</th>
                  <th>Message</th>
                  <th>Date</th>
                  <th>&nbsp;</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(item, index) in Notification.result" :key="index">
                  <td>{{ (index + 1)+((page-1)*Notification.limit) }}</td>
                  <td class="text-left">{{ item.no_title }}</td>
                  <td>
                    <img
                      v-bind:src="`https://img.youtube.com/vi/${item.no_url}/default.jpg`"
                      @click="displayYoutube(item.no_url)"
                    />
                  </td>
                  <td class="w-75 text-break text-left">{{ item.no_message }}</td>
                  <td>{{ new Date(item.no_updated).toISOString().replace(/T/, ' ').replace(/\..+/, '') }}</td>
                  <td>
                    <button class="btn btn-danger text-white btn-xs" type="button" @click="Delete(item)">
                      <i class="fa fa-trash"></i>
                    </button>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </Layout>
    <YoutubeModal ref="ytModal" />
  </div>
</template>
<script>
import Axios from "axios";
import school from "@/school";
import Layout from "@/components/admin/Layout";
import getVideoId from "get-video-id";
import YoutubeModal from "@/components/admin/YoutubeModal.vue";
import { mapState } from "vuex";
export default {
  components: {
    Layout,
    YoutubeModal
  },
  name: "Notification",
  data() {
    return {
      form: {
        no_title: "",
        no_message: "",
        no_url: "",
        no_url_id:""
      },
      Notification: {},
      page: 1,
      Vote: {
        _id: "",
        total: 0
      },
      UploadVideo: "",
      School: school,
      major: []
    };
  },
  computed: {},
  watch: {
    "$route.params.type": function(type) {
      this.page = 1;
      this.getContent();
    }
  },
  mounted() {},
  created() {
    this.getContent();
  },
  beforeDestroy() {},
  methods: {
    getContent() {
      Axios.get("/api/Notification/", {
        params: {
          page: this.page
        }
      })
        .then(res => {
          //console.log(res.data);
          this.Notification = res.data;
        })
        .catch(err => {
          console.log(err);
        });
    },
    onPage(page) {
      this.page = page;
      this.getContent();
    },
    onSubmit() {
      const ytUrl = this.form.no_url;
          var ytID = "";
          if (this.form.no_url != "") {
            ytID = getVideoId(this.form.no_url).id;
          }
          this.form.no_url = ytID;
          console.log(this.form);
      Axios.post("/api/notification/", this.form).then(res => {
        this.alertify.success("Inserted");
        this.getContent();
      });
    },
    Delete(item) {
      Axios.delete("/api/notification/", { params: item }).then(finish => {
        this.alertify.error("Delete Complete");
        this.getContent();
      });
    },
     ytUrlChange() {
      this.form.no_url_id = getVideoId(this.form.no_url).id;
    },
    displayYoutube(youtubeID) {
      this.$refs.ytModal.showModal(youtubeID);
    },
  },
};
</script>
<style scoped>
label {
  color: black;
}
</style>
