<template>
  <div class="register h-100">
    <div class="d-flex align-items-center align-middle h-100">
      <div class="row w-100 mt-5 ml-0 mr-0 justify-content-center">
        <div class="col-lg-4 col-md-6 col-sm-8 col-12">
          <div class="container">
            <form class="mt-3" @submit.prevent="onSubmit()">
              <h3 class="my-3 font-weight-bold">
                LOGIN
                <br />
                <span class="text-primary">ONE ACCOUNT.</span>
                <br />
                <span class="text-primary">ALL ACCESS.</span>
              </h3>

              <div class="form-group mt-4">
                <label>
                  Student ID&nbsp;
                  <i class="fa fa-question-circle"  @click="popups()" aria-hidden="true">
                    <span class="popuptext" id="student">Fill your Student ID </span>
                  </i>
                </label>
                <input
                  type="text"
                  autocomplete="off"
                  v-validate="{ required: true }"
                  name="u_id"
                  v-model.trim="form.u_id"
                  placeholder="Student ID"
                  :class="{ 'is-invalid': errors.has('u_id') }"
                  class="form-control"
                />
              </div>
              <div class="con">
                <div class="form-group">
                  <label>
                    Password&nbsp;
                    <i class="fa fa-question-circle"  @click="popupp()" aria-hidden="true">
                      <span class="popuptext" id="pass">Fill your Personal ID</span>
                    </i>
                  </label>
                  <input
                    type="password"
                    autocomplete="off"
                    v-validate="{ required: true }"
                    v-model.trim="form.u_password"
                    name="u_password"
                    :class="{ 'is-invalid': errors.has('u_password') }"
                    class="form-control"
                  />
                </div>

                <div class="mb-4 text-right">
                  <button type="submit" class="btn btn-primary form-control">LOGIN</button>
                </div>
                <div class="form-group mt-3">
                  <div
                    v-if="errorMessage"
                    class="alert alert-warning text-center"
                  >{{ errorMessage }}</div>
                </div>
              </div>
            </form>
            <hr />
            <div class="mb-2 mt-4 text-center">
              <a
                type="click"
                class="text-danger font-weight-lighter"
                @click="displayModalReport()"
              >Login issue?</a>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div id="reportProblemModal" class="modal fade" tabindex="-1" role="dialog">
      <div v-if="this.displayForm" class="modal-dialog modal-dialog-scrollable" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <div class="p-2 nowrap text-right w-100">
              <small>
                <a @click.prevent="toggleLang('TH')">TH</a> |
                <a @click.prevent="toggleLang('EN')">EN</a>
              </small>
            </div>
            <!-- ปุ่มปิดหน้าต่าง -->
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <a class="dot-container">
                <span aria-hidden="true">&times;</span>
              </a>
            </button>
          </div>
          <div class="modal-body">
            <div class="tilte">
              <h3 v-if="lang=='EN'" class="card-body">{{ titleEN }}</h3>
              <h3 v-else-if="lang=='TH'" class="card-body">{{ titleTH }}</h3>
            </div>
            <div v-if="lang=='EN'" class="card-body">{{ descEN }}</div>
            <div v-else-if="lang=='TH'" class="card-body">{{ descTH }}</div>
          </div>
          <div class="modal-footer text-center">
            <button type="button" class="btn btn-danger" @click.prevent="displayReportForm()">REPORT</button>
          </div>
        </div>
      </div>
      <div v-else class="modal-dialog modal-dialog-scrollable">
        <ReportForm @onResponse="onResponseSubmit()" />
      </div>
    </div>
    <ModalResponse ref="responseModal" />
  </div>
</template>
<script>
import Axios from "axios";
import ModalResponse from "@/components/ModalResponse";
import ReportForm from "@/components/ReportForm";
export default {
  components: {
    ReportForm,
    ModalResponse
  },
  name: "",
  data() {
    return {
      api: process.env.VUE_APP_API,
      url: process.env.VUE_APP_URL,
      title: process.env.VUE_APP_TITLE,
      form: {
        u_id: "",
        u_password: ""
      },
      displayForm: false,
      errorMessage: "",
      titleEN: "How to Live and Learn System’s issue report",
      titleTH: "แจ้งปัญหาการใช้งานระบบ",
      descEN:
        "If you find issues with the system, you can report them here by filling in your student ID, contact numbers, e-mail, and the issues you found. The admin team will fix them within 24 hours and respond back to your email as soon as possible. ",
      descTH:
        "แจ้งปัญหาการใช้งานระบบ How to Live and Learn หากน้องๆ พบปัญหาการใช้งาน แจ้งได้ที่นี่ โดยกรอกรหัสนักศึกษา เบอร์โทรศัพท์ติดต่อ อีเมลล์ และปัญหาที่พบ โดยพี่แอดมินจะทำการแก้ไขปัญหาให้ภายใน 24 ชม.  และจะตอบกลับผ่านอีเมลโดยเร็วที่สุด",
      lang: "EN"
    };
  },
  computed: {},
  watch: {},
  mounted() {},
  created() {},
  beforeDestroy() {},
  methods: {
    displayReportForm() {
      this.displayForm = false;
    },
    popups() {
      var popup = document.getElementById("student");
      popup.classList.toggle("show");
    },
    popupp(){
      var popup = document.getElementById("pass");
      popup.classList.toggle("show");
    },
    onSubmit() {
      this.$validator.validateAll().then(async valid => {
        if (!valid) return;
        Axios.post("/api/account/login", this.form)
          .then(response => {
            // console.log(response.data)
            if (response.data.message == "Authen fail") {
              // this.errorMessage = 'กรุณาตรวจสอบรหัสนักศึกษาและรหัสประชาชน'
              this.errorMessage =
                "Please check the your Student ID and Citizen ID.";
            } else {
              this.$store
                .dispatch("get_user_progress", {
                  id: this.form.u_id
                })
                .then(res => {
                  this.$router.push({ name: "home" });
                });
              
            }
          })
          .catch(err => {
            this.errorMessage = err.response.data.message;
          });
      });
    },
    onReset() {
      this.errorMessage = null;
      this.$validator.reset();
      this.form = {
        u_id: "",
        u_password: ""
      };
    },
    displayModalReport() {
      this.displayForm = true;
      this.jquery("#reportProblemModal").modal("show");
    },
    onResponseSubmit() {
      this.jquery("#reportProblemModal").modal("hide");
      this.$refs.responseModal.showModal();
    },
    toggleLang(lang) {
      this.lang = lang;
    }
  }
};
</script>
<style scoped>
