<template>
  <div id="timer-container" class="w100 btn">
    <div id="timer-progress" :style="{ width: percent + '%' }"></div>
    <div class="text-nowrap">{{ time }}</div>
  </div>
</template>
<script>
export default {
  props: {
    triggerEvent: 0
  },
  data() {
    return {
      duration: 0,
      tmpDuration: 0,
      time: "",
      pause: false,
      percent: 0
    };
  },
  methods: {
    setDuration(val) {
      if (this.duration == 0) {
        this.tmpDuration = parseInt(val);
      }
      this.duration = parseInt(val);
      this.startCountdown();
    },
    startCountdown() {
      if (this.duration >= 0) {
        setTimeout(() => {
          this.duration -= 1;
          this.percent =
            ((this.tmpDuration - this.duration) / this.tmpDuration) * 100;
          if (this.triggerEvent > 0) {
            if (this.tmpDuration - this.duration > this.triggerEvent) {
              this.onTriggerEvent();
            }
          }
          if (!this.pause) this.startCountdown();
        }, 1000);

        var date = new Date(0);
        date.setSeconds(this.duration); // specify value for SECONDS here
        this.time = date.toISOString().substr(11, 8);
      } else {
        this.onFinish();
      }
    },
    onTriggerEvent() {
      this.$emit("onTriggerEvent");
    },
    onResume() {
      this.pause = false;
    },
    onPause() {
      this.pause = true;
    },
    onFinish() {
      this.$emit("onFinish");
    }
  }
};
</script>
<style scoped>
#timer-container {
  background-color: #ebebeb;
  min-height: 38px;
  color: #212529;
  position: relative;
}
#timer-progress {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  background-color: rgba(1, 151, 63, 0.8);
  transition: all 0.3s ease;
  z-index: 9;
}
</style>