<template>
  <div class="home">
    <Layout>
      <div class="card">
        <div class="card-body">
          <h3>PROBLEM REPORT</h3>
          <form class="mt-5 form-inline" @submit.prevent="onSearch()">
            <!-- <form class="mt-5" @submit.prevent="onSubmit()"> -->

            <div class="form-group">
              <input
                type="search"
                class="form-control"
                name="search_text"
                v-validate="{
                    required: true
                  }"
                :class="{ 'is_invalid': errors.has('search_text') }"
                v-model.trim="search.search_text"
                placeholder="รหัสนักศึกษา"
              />
            </div>
            <button type="submit" class="btn btn-primary ml-3">ค้นหา</button>
          </form>
          <div class="table-responsive mt-3">
            <table class="table">
              <thead class="thead-dark">
                <tr>
                  <th>#</th>
                  <th>รหัสนักศึกษา</th>
                  <th>เบอร์</th>
                  <th>contact</th>
                  <th>ปัญหา</th>
                  <th>รายละเอียด</th>
                  <th>วันที่</th>
                  <th>อ่าน</th>
                </tr>
              </thead>
              <tbody class="text-muted">
                <tr v-for="(item, index) in report.result" :key="index">
                  <td>{{ (index + 1)+((page-1)*report.limit) }}</td>
                  <!-- รหัสนักศึกษา -->
                  <td>{{ item.u_id }}</td>
                  <!-- เบอร์ -->
                  <td>{{ item.u_phone }}</td>
                  <!-- contact -->
                  <td>{{ item.u_email}}</td>
                  <!-- ปัญหา -->
                  <td class="text-center">{{ item.u_problem }}</td>
                  <!-- รายละเอียด -->
                  <td class="text-center">{{ item.u_comment }}</td>
                  <!-- วัน -->
                  <td>{{ new Date(item.u_updated).toISOString().replace(/T/, ' ').replace(/\..+/, '') }}</td>
                  <!-- อ่าน -->
                  <td>
                    <button type="button" class="btn btn-info btn-sm" @click="onShow(item.u_comment)">
                      <i class="fa fa-search"></i>
                    </button>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
        <div class="p-3" v-if="report.rows > report.limit">
          <Pagination :data="report" :page="page" @onPage="onPage($event)" />
        </div>
      </div>
    </Layout>
  </div>
</template>

<script>
import Axios from "axios";
import Layout from "@/components/admin/Layout";
import Pagination from "@/components/admin/Pagination";
import { mapState } from "vuex";
export default {
  components: {
    Layout,
    Pagination
  },
  name: "report",
  data() {
    return {
      tbody: {
        u_id: [],
        u_phone: [],
        u_problem: [],
        u_comment: []
      },
      search: {
        search_key: "u_id",
        search_text: ""
      },
      page: 1,
      report: []
    };
  },
  computed:{

  },
  watch:{

  },
  created() {
    this.getData({
      params: {
        page: this.page
      }
    });
  },
  beforeDestroy(){

  },
  methods: {
    getData(param) {
      Axios.get("/api/report", param)
        .then(res => {
          this.report = res.data;
        })
        .catch(err => {});
    },
    onShow(item) {
      this.alertify.alert("รายละเอียด", item, () => {});
    },
    onSearch() {
      //console.log(this.search);
      this.page = 1;
      this.getData({
        params: {
          page: this.page,
          search_key: this.search.search_key,
          search_text: this.search.search_text
        }
      });
    },
    onPage(page) {
      this.page = page;
      this.getData({
        params: {
          page: this.page
        }
      });
    }
  }
};
</script>
<style scoped>
thead th,
tbody td {
  text-align: center;
}
.card-body {
  padding-left: 25px;
  padding-right: 25px;
}
</style>
