<template>
  <div class="modal-content">
    <div class="modal-header">
      <div class="p-2">
        <small>
          <a @click.prevent="toggleLang('TH')">TH</a> |
          <a @click.prevent="toggleLang('EN')">EN</a>
        </small>
      </div>
      <button type="button" class="close" data-dismiss="modal" aria-label="Close">
        <a class="dot-container">
          <span aria-hidden="true">&times;</span>
        </a>
      </button>
    </div>

    <div class="modal-body">
      <h3 class="text-center font-weight-bold" v-if="lang=='TH'"> {{headTH}} </h3>
      <h3 class="text-center font-weight-bold" v-if="lang=='EN'"> {{headEN}} </h3>
      <div class="panel-body p-4">
        <div class="form-group">
          <label v-if="lang=='TH'">{{ studentTH }}</label>
          <label v-if="lang=='EN'">{{ studentEN }}</label>
          <input
            type="number"
            v-validate="{ required: true }"
            name="u_id"
            v-model.trim="form.u_id"
            :class="{ 'is-invalid': errors.has('u_id') }"
            class="form-control"
          />
        </div>
        <div class="form-group">
          <label v-if="lang=='TH'">{{ contactTH }}</label>
          <label v-if="lang=='EN'">{{ contactEN }}</label>
          <input
            type="number"
            v-validate="{ required: true }"
            name="u_phone"
            v-model.trim="form.u_phone"
            :class="{ 'is-invalid': errors.has('u_phone') }"
            class="form-control"
          />
        </div>
        <div class="form-group">
          <label>E-mail</label>
          <input
            type="email"
            v-validate="{ required: true }"
            name="u_email"
            v-model.trim="form.u_email"
            :class="{ 'is-invalid': errors.has('u_email') }"
            class="form-control"
          />
        </div>
        <div class="form-group">
          <label v-if="lang=='TH'">{{ reportTH }}</label>
          <label v-if="lang=='EN'">{{ reportEN }}</label>
          <select
            v-validate="{ required: true }"
            class="form-control"
            name="u_problem"
            :class="{ 'is-invalid': errors.has('u_problem') }"
            v-model.trim="form.u_problem"
            placeholder="SELECT PROBLEM TYPE"
          >
            <option value disabled selected v-if="lang=='EN'"> {{selectEN}} </option>
            <option value disabled selected v-if="lang=='TH'"> {{selectTH}} </option>
            
            <option :value="proble1EN" v-if="lang=='EN'" > {{proble1EN}} </option>
            <option :value="proble1EN" v-if="lang=='TH'" > {{proble1TH}} </option>

            <option :value="proble2EN" v-if="lang=='EN'" > {{proble2EN}} </option>
            <option :value="proble2EN" v-if="lang=='TH'" > {{proble2TH}} </option>

            <option :value="proble3EN" v-if="lang=='EN'" > {{proble3EN}} </option>
            <option :value="proble3EN" v-if="lang=='TH'" > {{proble3TH}} </option>

            <option :value="proble4EN" v-if="lang=='EN'" > {{proble4EN}} </option>
            <option :value="proble4EN" v-if="lang=='TH'" > {{proble4TH}} </option>

            <option :value="proble5EN" v-if="lang=='EN'" > {{proble5EN}} </option>
            <option :value="proble5EN" v-if="lang=='TH'" > {{proble5TH}} </option>
          </select>
        </div>
        <div class="form-group">
          <label v-if="lang=='TH'">{{ othersTH }}</label>
          <label v-if="lang=='EN'">{{ othersEN }}</label>
          <textarea v-model.trim="form.u_comment" class="form-control" cols="5"></textarea>
        </div>
      </div>
    </div>
    <div class="modal-footer text-center" v-if="lang=='EN'">
      <button @click.prevent="onSubmit()" type="button" class="btn btn-danger w200">{{btnEN}}</button>
    </div>
    <div class="modal-footer text-center" v-if="lang=='TH'">
      <button @click.prevent="onSubmit()" type="button" class="btn btn-danger w200">{{btnTH}}</button>
    </div>
  </div>
</template>
<script>
import Axios from "axios";
import { mapState } from "vuex";
export default {
  props: {
    u_id: {
      type: String,
      required: false
    }
  },
  data : function() {
    return {
      form: {
        u_id: "",
        u_email: "",
        u_phone: "",
        u_comment: "",
        u_problem: ""
      },
      headEN: "LOGIN ISSUE REPORT",
      headTH: "รายงานปัญหา",

      studentEN: "Student",
      studentTH: "รหัสนักศึกษา",
      contactEN: "Contact Number",
      contactTH: "เบอร์ติดต่อ",
      reportEN: "Report",
      reportTH: "รายงาน",
      othersEN: "Others",
      othersTH: "อื่น ๆ ",

      selectEN: "SELECTING PROBLEM TYPE",
      selectTH: "เลือกประเภทของปัญหา",
      proble1EN: "Forgot password",
      proble1TH: "ลืมรหัสผ่าน",
      proble2EN: "Cannot log in to the system",
      proble2TH: "ไม่สามารถ Login เข้าสู่ระบบได้",
      proble3EN: "The account information is incorrect.",
      proble3TH: "ข้อมูลบัญชีผู้ใช้งานไม่ถูกต้อง",
      proble4EN: "Error occurred while using",
      proble4TH: "เกิด Error หรือข้อผิดปกติ ขณะใช้งาน",
      proble5EN: "Others",
      proble5TH: "อื่น ๆ",

      btnEN: "REPORT",
      btnTH: "รายงาน",
      lang: "EN",
    };
  },
  created() {
    this.form.u_id = this.u_id;
  },
  methods: {
    onSubmit() {
      this.$validator.validateAll().then(async valid => {
        if (!valid) return;
        Axios.post("/api/report", this.form)
          .then(res => {
            this.jquery("#reportProblemModal").modal("hide");
            this.$emit("onResponse");
            this.form.u_id = "";
            this.form.u_email = "";
            this.form.u_phone = "";
            this.form.u_comment = "";
            this.form.u_problem = "";
          })
          .catch(error => {
            //this.alertify.error("Error");
          });
      });
    },
    toggleLang(lang) {
      this.lang = lang;
    },
  }
};
</script>
<style scoped>
.form-control {
  border: none;
}
.p-2{
  margin-left: 75%;
}
</style>
