<template>
  <div  id="messageContainer" class="chat-conversation">
    <div
     
      class="chat-conversation__message"
      v-for="message in messages"
      :message="message"
      v-bind:key="message.id"
    >
      <strong>{{ message.user.name }}:</strong>
      <div v-if="message.message.match(/\/\*(stck-[0-1]?[0-9])\*\//)">
        <img style="width:50px;" :src="'/stickers/' + message.message.match(/\/\*(.*)\*\//)[1] + '.png'">
      </div>
      <span class="text-muted wrap-auto ml-2" v-else>{{ message.message }}</span>
    </div>
  </div>
</template>
<script>
export default {
  name: "ChatConversation",
  props: {
    activityMessage: {
      type: String,
      required: true
    },
    socket: {
      type: Object,
      required: true
    },
    log: null
  },
  data() {
    return {
      messages: []
    };
  },
  created() {},
  methods: {
    getLog() {
      this.socket.emit("returnLog");
      this.socket.on("returnLogResponse", message => {
        //console.log(message);
        var logTmp = [];
        for (var i = 0; i < message.length; i++) {
          logTmp.push(message[i]);

          //console.log(message[i]);
        }

        this.messages = logTmp.slice().reverse();
        //console.log(this.logTmp)
      });
    }
  },
  mounted() {
    this.getLog();
    this.socket.on(this.activityMessage, data => {
      //console.log(data.message.match(/\/\*(.*)\*\//)[1].toString())
      this.messages.push(data);
      var objDiv = document.getElementById("messageContainer");

      //objDiv.scrollTop = objDiv.scrollHeight;
      //this.messages.pop();
    });
    this.socket.on("disconnect", () => {
      this.messages = [];
    });
  }
};
</script>
<style scoped>
.chat-conversation {
  padding: 10px 0;
}
.chat-conversation__message {
  padding: 0 10px;
  text-align: left;
}
</style>