<template>
  <div id="activityModal" class="modal" :style="{top: top, left: left}">
    <div class="modal-dialog modal-sm">
      <div class="modal-content">
        <div class="modal-header text-right position-relative">
          <!-- ปุ่มปิดหน้าต่าง -->
          <small class="position-absolute" style="right:60px;top:23px;">
            <a @click.prevent="toggleLang('th')">TH</a> |
            <a @click.prevent="toggleLang('en')">EN</a>
          </small>
          <button
            type="button"
            class="close"
            data-dismiss="modal"
            aria-label="Close"
            @click.prevent="hideModal()"
          >
            <a class="dot-container">
              <span aria-hidden="true">&times;</span>
            </a>
          </button>
        </div>
        <div class="modal-body text-center">
          <!--<h3 class="text-center font-weight-bold">{{ title }}</h3>-->
          <h3 v-if="lang === 'en'" class="text-center text-primary font-weight-bold">{{ subtitle.en }}</h3>
          <h3 v-if="lang === 'th'" class="text-center text-primary font-weight-bold">{{ subtitle.th }}</h3>
          <p v-if="lang === 'en'" class="mt-2 p-3">{{ descEN }}</p>
          <p v-else-if="lang === 'th'" class="mt-2 p-3">{{ descTH }}</p>
          <button type="button" class="mt-2 mb-4 btn btn-primary w100" @click.passive="onJoin();">
            JOIN&nbsp;
            <i class="fa fa-chevron-right"></i>
            <i class="fa fa-chevron-right"></i>
          </button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    activity: {
      required: true
    },
    index: {
      required: true
    }
  },
  watch: {
    activity: function(newVal, oldVal) {
      this.title = this.activity.title;
      this.subtitle = this.activity.subtitle;
      this.descEN = this.activity.shortDesc.en;
      this.descTH = this.activity.shortDesc.th;
      this.activityName = this.activity.name;
    },
    index: function(newVal, oldVal) {
      this.activityIndex = this.index;
    }
  },
  data() {
    return {
      lang: "en",
      descEN: "",
      descTH: "",
      title: "",
      subtitle: "",
      activityName: "",
      activityIndex: -1,
      top: 0,
      left: 0
    };
  },
  created() {},
  methods: {
    showModal(top, left) {
      //alert(top+" == "+left)
      this.top = top;
      this.left = left;
      this.jquery("#activityModal").fadeIn();
    },
    hideModal() {
      this.jquery("#activityModal").fadeOut();
    },
    toggleLang(lang) {
      this.lang = lang;
    },
    onJoin() {
      this.$router.push({
        path: `activityPreview/${this.activityName}`
      });
    }
  }
};
</script>
<style scoped>
.modal {
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 1050;
  display: none;
  width: auto;
  height: auto;
  overflow: hidden;
  outline: 0;
  margin-left: -150px;
  margin-top: -150px;
}
.modal-content {
  background-color: rgba(255, 255, 255, 0.95);
  border: none;
}
