<template>
  <div class="activity h100">
    <Layout>
      <div class="text-center d-flex position-relative w-100 h-100">
        <div
          id="loading-container"
          v-if="loading"
          class="position-absolute d-flex align-items-center align-middle"
        >
          <div class="m-auto">
            <i class="fa fa-spinner fa-spin"></i>
          </div>
        </div>
        <iframe
          id="game"
          ref="game"
          name="gameIframe"
          class="d-inline-block align-self-center text-center"
          :src="`/MFUModel/index.html`"
        ></iframe>
      </div>
      <div class="d-none d-md-block d-lg-block">
        <div class="row mb-4 d-flex">
          <div class="col-md-4 align-self-center text-center">
            <button @click="onBack()" type="button" class="btn btn-primary w100">BACK</button>
          </div>
          <div
            class="col-md-4 text-center justify-content-center align-self-center"
          >&nbsp;</div>
          <div class="col-md-4 align-self-center text-center">
            <button @click="onHome()" type="button" class="btn btn-primary w100">HOME</button>
          </div>
        </div>
      </div>
      <div class="d-md-none d-lg-none d-sm-block d-xs-block">
        <div
          class="col-12 text-center justify-content-center align-self-center"
        >&nbsp;</div>
        <div class="mt-3 mb-4 text-center">
          <button @click="onBack()" type="button" class="btn btn-primary w100 m-3">BACK</button>
          <button @click="onHome()" type="button" class="btn btn-primary w100 m-3">HOME</button>
        </div>
      </div>
    </Layout>
  </div>
</template>
<script>
import Layout from "@/components/Layout";
export default {
  components: {
    Layout
  },
  name: "",
  data() {
    return {
      loading: true,
      activityName: this.activity[2].name
    };
  },
  computed: {},
  watch: {},
  mounted() {
    window.addEventListener("message", e => {
      if (String(e.data).match(/application-loaded/)) {
        this.loading = false;
      }
    });
  },
  created() {},
  beforeDestroy() {},
  methods: {
    onHome() {
      this.$router.push({
        name: `home`
      });
    },
    onBack() {
      this.$router.push({
        path: `/ActivityPreview/${this.activityName}`
      });
    }
  }
};
</script>
<style scoped>
#game {
  border: none;
  height: 70vh;
  width: 100vw;
}
#gameContainer canvas {
  width: 100% !important;
}
#loading-container {
  background: #fff;
  z-index: 999;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
#loading-container i {
  font-size: 3rem;
}
</style>