<template>
  <div class="home">
    <Layout>
      <div class="card">
        <div class="card-body">
          <h3>Inspiration</h3>
          <div class="mt-5">
            <div class="col-2" style="margin-left: 85%;">
              <button
                type="button"
                class="btn btn-primary nowrap w100 text-white"
                data-toggle="modal"
                data-target="#EditModal"
                @click.prevent="showEditModal()"
              >
                <i class="fa fa-plus" aria-hidden="true"></i>&nbsp;เพิ่มคลิป
              </button>
            </div>
          </div>
          <div class="table-responsive mt-3">
            <table class="table text-center">
              <thead class="thead-dark">
                <tr>
                  <th>#</th>
                  <th>คลิป</th>
                  <th>ชื่อคลิป</th>
                  <th>คำอธิบาย</th>
                  <th>สำนักวิชา</th>
                  <th>สาขาวิชา</th>
                  <th>&nbsp;</th>
                  <th>&nbsp;</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(item, index) in activityInspiration.result" :key="index">
                  <td>{{ (index + 1)+((page-1)*activityInspiration.limit) }}</td>
                  <td>
                    <img
                      v-bind:src="`https://img.youtube.com/vi/${item.ins_url}/default.jpg`"
                      @click="displayYoutube(item.ins_url)"
                    />
                  </td>
                  <td>{{ item.ins_name }}</td>
                  <td>{{ item.ins_description }}</td>
                  <td>{{ item.ins_school }}</td>
                  <td>{{ item.ins_major }}</td>
                  <td>
                    <button
                      type="button"
                      class="btn btn-success text-white btn-sm"
                      @click.prevent="showEditModal(item)"
                    >
                      <i class="fa fa-edit"></i>
                    </button>
                  </td>
                  <td>
                    <button
                      type="button"
                      class="btn btn-danger text-white btn-sm"
                      @click.prevent="onDelete(item._id)"
                    >
                      <i class="fa fa-trash"></i>
                    </button>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
        <div class="p-3" v-if="activityInspiration.rows > activityInspiration.limit">
          <Pagination :data="activityInspiration" :page="page" @onPage="onPage($event)" />
        </div>
        <!-- Modal -->
        <div
          class="modal fade"
          id="EditModal"
          tabindex="-1"
          role="dialog"
          aria-labelledby="EditModalLabel"
          aria-hidden="true"
        >
          <div class="modal-dialog" role="document">
            <div class="modal-content">
              <div class="modal-header">
                <h5 class="modal-title w-100" id="exampleModalLabel">
                  เพิ่มคลิปวิดีโอใหม่
                  <button
                    type="button"
                    class="close"
                    data-dismiss="modal"
                    aria-label="Close"
                  >
                    <span aria-hidden="true">&times;</span>
                  </button>
                </h5>
              </div>
              <div class="modal-body">
                <form class="form-row" @submit.prevent="onSubmit()">
                  <!-- //---------DropDown -->
                  <!-- //สำนักวิชา -->
                  <div class="row w-100">
                    <div class="col-3">
                      <label>School</label>
                    </div>
                    <div class="col-9">
                      <div class="btn-group">
                        <button
                          type="button"
                          class="btn btn-secondary dropdown-toggle p-2 mt-2 mb-4 pl-4 pr-4"
                          data-toggle="dropdown"
                          aria-haspopup="true"
                          aria-expanded="false"
                        >{{this.form.ins_school}}</button>
                        <div class="dropdown-menu">
                          <a
                            class="dropdown-item"
                            @click="ChangeSelectSchool('เลือกสำนักวิชา....')"
                          >เลือกสำนักวิชา....</a>
                          <div class="dropdown-divider"></div>
                          <div v-for="(item, index) in School" :key="index">
                            <a class="dropdown-item" @click="ChangeSelectSchool(item)">{{item.name}}</a>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                  <!-- //สาขาวิชา -->
                  <div class="row w-100" v-if="this.form.ins_major != null ">
                    <div class="col-3">
                      <label>Major</label>
                    </div>
                    <div class="col-9">
                      <div class="btn-group">
                        <button
                          type="button"
                          class="btn btn-secondary dropdown-toggle p-2 mt-2 mb-4 pl-4 pr-4"
                          data-toggle="dropdown"
                          aria-haspopup="true"
                          aria-expanded="false"
                        >{{this.form.ins_major}}</button>
                        <div class="dropdown-menu">
                          <a
                            class="dropdown-item"
                            @click="ChangeSelectMajor('เลือกสาขาวิชา....')"
                          >เลือกสาขาวิชา....</a>
                          <div class="dropdown-divider"></div>
                          <div v-for="(item, index) in major" :key="index">
                            <a
                              class="dropdown-item"
                              @click="ChangeSelectMajor(item.major_name)"
                            >{{item.major_name}}</a>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                  <!-- //---------DropDown -->
                  <div class="form-group col-md-12">
                    <label>ชื่อคลิปวิดีโอ</label>
                    <input
                      type="text"
                      name="ins_name"
                      v-validate="{
                    required: true
                  }"
                      :class="{ 'is_invalid': errors.has('ins_name') }"
                      v-model.trim="form.ins_name"
                      class="form-control"
                      :placeholder="this.form.ins_name"
                    />
                    <p class="invalid_feedback">{{ errors.first('ins_name') }}</p>
                  </div>
                  <div class="form-group col-md-12">
                    <label>คำอธิบายคลิปวิดีโอ</label>
                    <textarea
                      type="text"
                      name="ins_description"
                      v-model.trim="form.ins_description"
                      class="form-control"
                      :placeholder="this.form.ins_description"
                    ></textarea>
                  </div>
                  <div class="form-group col-md-12">
                    <label class="pb-2">Youtube URL</label>
                    <input
                      type="text"
                      name="ins_url"
                      v-validate="{
                    required: true
                  }"
                      :class="{ 'is_invalid': errors.has('ins_url') }"
                      v-model.trim="form.ins_url"
                      v-on:change="ytUrlChange"
                      class="form-control"
                      placeholder="URL..."
                    />
                    <p class="invalid_feedback">{{ errors.first('ins_url') }}</p>
                  </div>
                  <div class="col-md-9 offset-md-3 mb-2">
                    <img
                      v-if="form.ins_url"
                      width="200"
                      class="w-50 mt-2 justify-content-md-center"
                      v-bind:src="`https://img.youtube.com/vi/${form.ins_url_id}/default.jpg`"
                      @click="displayYoutube(form.ins_url_id)"
                    />
                  </div>
                  <div class="form-group col-md-12">
                    <button type="submit" class="btn btn-primary" style="width:98%">บันทึกข้อมูล</button>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Layout>
    <YoutubeModal ref="ytModal" />
  </div>
</template>
<script>
import Axios from "axios";
import school from "@/school";
import Layout from "@/components/admin/Layout";
import YoutubeModal from "@/components/admin/YoutubeModal.vue";
import Pagination from "@/components/admin/Pagination";
import getVideoId from "get-video-id";
import { mapState } from "vuex";
export default {
  components: {
    Layout,
    Pagination,
    YoutubeModal
  },
  data() {
    return {
      form: {
        ins_major: null,
        ins_school: "เลือกสำนักวิชา....",
        ins_url: "",
        ins_name: "",
        ins_description: "",
        ins_url_id: ""
      },
      activityInspiration: {
        ins_school: "",
        ins_url: "",
        ins_name: "",
        ins_description: ""
      },
      page: 1,
      Vote: {
        _id: "",
        total: 0
      },
      UploadVideo: "",
      School: school,
      major: []
    };
  },
  computed: {},
  watch: {
    "$route.params.type": function(type) {
      this.page = 1;
      this.getContent();
    }
  },
  mounted() {},
  created() {
    this.getContent();
  },
  beforeDestroy() {},
  methods: {
    onDelete(_id) {
      this.alertify.confirm("Confirm Deleteing", () => {
        Axios.delete(`/api/controllerInspiration/${_id}`).then(() => {
          this.getContent();
        });
      });
    },
    getContent() {
      let ranking = [];
      Axios.get("/api/controllerInspiration/", {
        params: {
          page: this.page
        }
      })
        .then(res => {
          this.activityInspiration = res.data;
        })
        .catch(err => {
          console.log(err);
        });
    },
    displayYoutube(youtubeID) {
      this.$refs.ytModal.showModal(youtubeID);
    },
    onPage(page) {
      this.page = page;
      this.getContent();
    },
    onSubmit() {
      if (
        this.form.ins_school == "เลือกสำนักวิชา...." ||
        this.form.ins_name == "" ||
        this.form.ins_url == ""
      ) {
        this.alertify.error("กรุณาครวจสอบข้อมูล");
      } else {
        this.$validator.validateAll().then(valid => {
          const ytUrl = this.form.ins_url;
          const ytID = getVideoId(this.form.ins_url).id;
          if (ytID === undefined) {
            this.alertify.error("กรุณาตรวจสอบ youtube URL!");
            return;
          }
          this.form.ins_url = ytID;
          Axios.post("/api/controllerInspiration/", this.form)
            .then(res => {
              this.getContent();
              this.alertify.success("เพิ่มข้อมูลเรียบร้อย");
              (this.form.ins_major = null),
                (this.form.ins_school = "เลือกสำนักวิชา...."),
                (this.form.ins_url = ""),
                (this.form.ins_name = ""),
                (this.form.ins_description = ""),
                (this.form.ins_url_id = ""),
                this.$validator.reset();
            })
            .catch(error => {
              this.alertify.error(error.response.data.message);
              this.form.ins_url = ytUrl;
            });
        });
      }
    },
    onUpdate() {
      this.$validator.validateAll().then(valid => {
        const ytUrl = this.form.ins_url;
        const ytID = getVideoId(this.form.ins_url).id;
        if (ytID === undefined) {
          this.alertify.error("กรุณาตรวจสอบ youtube URL!");
          return;
        }
        this.form.ins_url = ytID;

        this.displayYoutube(this.form.ins_url);

        // Axios.post("/api/activityAlumni/update/", this.form)
        //   .then(res => {
        //     this.alertify.success("อัพเดทข้อมูลเรียบร้อย");
        //     this.getContent();
        //     this.form.ins_url = "";
        //     this.form.ins_school = "";
        //     this.$validator.reset();
        //   })
        //   .catch(error => {
        //     this.alertify.error(error.response.data.message);
        //     this.form.ins_url = ytUrl;
        //   });
      });
    },
    ChangeSelectSchool(value) {
      this.form.ins_school = value.name;
      if (value == "เลือกสำนักวิชา....") {
        this.form.ins_school = value;
        this.form.ins_major = null;
      } else {
        this.form.ins_major = "เลือกสาขาวิชา....";
      }
      this.major = value.major;
    },
    ChangeSelectMajor(value) {
      this.form.ins_major = value;
    },
    ytUrlChange() {
      this.form.ins_url_id = getVideoId(this.form.ins_url).id;
      console.log(this.form.ins_url_id);
    },
    showEditModal(item) {
      console.log(item);
      if (item != undefined) {
        const url = "https://youtu.be/" + item.ins_url;
        this.form.ins_school = item.ins_school;
        this.form.ins_major = item.ins_major;
        this.form.ins_name = item.ins_name;
        this.form.ins_description = item.ins_description;
        this.form.ins_url = url;
        this.ytUrlChange();
      } else {
        (this.form.ins_major = null),
          (this.form.ins_school = "เลือกสำนักวิชา...."),
          (this.form.ins_url = ""),
          (this.form.ins_name = ""),
          (this.form.ins_description = ""),
          (this.form.ins_url_id = "");
      }
      this.modalData = item;
      this.jquery("#EditModal").modal("show");
    }
  }
};
</script>
<style scoped>
label {
  color: black;
}
</style>
