<template>
  <div class="activity h100">
    <Layout>
      <div class="container mt-5">
        <h1>LiveSteam</h1>
        <small
          style="font-weight: 300;font-size: 16px;"
        >ท่านจะผ่านกิจกรรมนี้เมื่อรับชม กิจกรรม Live Steam อย่างน้อย 10 นาที</small>
        <div class="row mt-5 mb-5">
          <div class="col-md-8">
            <div class="video-container" style="width: 100%">
              <div v-if="setting.admin_av_hide_controller == 0" class="hideController"></div>
              <youtube
                :video-id="setting.admin_av_yt_id"
                :player-vars="{ autoplay: setting.admin_av_autoplay, controls: 0 }"
                @ended="finished"
                style="width:100%;"
                ref="youtube"
              ></youtube>
            </div>
          </div>
          <div class="col-md-4" style="background: #FFF">
            <h5>Chat board</h5>
            <div class="chat__conversation">
              <chat-conversation :socket="socket" :activityMessage="this.messageActivity"></chat-conversation>
            </div>
          </div>
          <div class="col-md-12 mt-3" style="padding-left:0;padding-right:0;">
            <div class="col-auto">
              <div class="input-group mb-2">
                <input
                  type="text"
                  class="form-control"
                  id="inlineFormInputGroup"
                  placeholder="Comment here..."
                  @keyup="onKeyUp"
                  v-model="message"
                />
                <div class="input-group-prepend">
                  <button class="input-group-text btn btn-primary" @click="sendMessage">Submit</button>
                </div>
                <div>
                  <!-- Button trigger modal -->
                  <button
                    type="button"
                    class="btn btn-primary"
                    data-toggle="modal"
                    data-target="#stickerModal"
                    style="margin-left: 10px;"
                  >
                    <span>
                      <i class="fa fa-smile-o" style="font-size: 20px;"></i>
                    </span>
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
        <!-- Modal -->
        <div
          class="modal fade"
          id="stickerModal"
          tabindex="-1"
          role="dialog"
          aria-labelledby="exampleModalCenterTitle"
          aria-hidden="true"
        >
          <div class="modal-dialog modal-dialog-centered" role="document">
            <div class="modal-content">
              <div class="modal-header">
                <h5 class="modal-title" id="exampleModalCenterTitle">Stickers</h5>
                <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                  <span aria-hidden="true">&times;</span>
                </button>
              </div>
              <div class="modal-body">
                <!--
                <img
                  src="../assets/stickers/stck-1.png"
                  @click="setSticker('stck_1')"
                  height="80"
                  width="100"
                />
                <img
                  src="../assets/stickers/stck-2.png"
                  @click="setSticker('stck_2')"
                  height="80"
                  width="100"
                />
                <img
                  src="../assets/stickers/stck-3.png"
                  @click="setSticker('stck_3')"
                  height="80"
                  width="100"
                />
                -->
              </div>
            </div>
          </div>
        </div>
      </div>
    </Layout>
    <Modal :activity="this.activity" ref="modalSurway" />
  </div>
</template>
<script>
import Axios from "axios";
import Layout from "@/components/Layout";
import io from "socket.io-client";
import ChatConversation from "@/components/ChatCoversation";
import Modal from "@/components/Modal";
export default {
  components: {
    Layout,
    ChatConversation,
    Modal
  },
  name: "",
  data() {
    return {
      messageActivity: "messageLive",
      message: "",
      socket: null,
      data: null,
      user: {
        id: null,
        name: null
      },
      activity: this.activity[5].name,
      setting: {
        admin_av_autoplay: null,
        admin_av_disable_msg: null,
        admin_av_enable: null,
        admin_av_yt_id: null
      },
      form: {
        u_id: null,
        u_username: null,
        av_name: null
      }
    };
  },
  computed: {},
  watch: {},
  mounted() {
    this.initialValue();
  },
  created() {
    this.user.id = this.$store.state.user.u_id;
    this.user.name = this.$store.state.user.u_username;
    if (this.user.name) {
      this.socket = io.connect(process.env.VUE_APP_API);
      this.socket.on("connect", () => {
        this.connect();
      });
    }
  },
  beforeDestroy() {},
  methods: {
    connect() {
      this.socket.emit("connected", this.user);
    },
    initialValue() {
      const id = this.$store.getters.user.u_id;
      if (id) {
        Axios.get(`/api/surway/${id}?activity=${this.activity}`).then(s_res => {
          if (s_res.data.length == 0) {
            this.hideSurway = false;
            // display surway if user activity is saved but surway is not
            Axios.get(`/api/activityVideo/${id}?av_name=${this.activity}`).then(
              res => {
                if (res.data) {
                  this.$refs.modalSurway.showModal();
                }
              }
            );
          }
        });
      }
      // get setting values
      Axios.get(`/api/controllerVideo/${this.activity}`)
        .then(res => {
          this.setting.admin_av_autoplay = res.data.admin_av_autoplay ? 1 : 0;
          this.setting.admin_av_enable = res.data.admin_av_enable;
          this.setting.admin_av_disable_msg = res.data.admin_av_disable_msg;
          this.setting.admin_av_yt_id = res.data.admin_av_yt_id;
        })
        .catch(err => {
          console.log(err);
        });
    },
    sendMessage() {
      this.socket.emit(this.messageActivity, {
        id: this.id,
        user: this.user,
        message: this.message
      });
      this.message = "";
    },
    onKeyUp(event) {
      if (event.keyCode === 13) {
        this.sendMessage();
      }
    },
    setSticker(stName) {
      this.socket.emit(this.messageActivity, {
        id: this.id,
        user: this.user,
        message: `/*${stName}*/`
      });
      this.jquery("#stickerModal").modal("hide");
    },
    finished() {
      const id = this.$store.getters.user.u_id;
      Axios.get(`/api/activityVideo/${id}?av_name=${this.activity}`).then(
        res => {
          if (!res.data) {
            this.form.u_id = id;
            this.form.u_username = this.$store.getters.user.u_username;
            this.form.av_name = this.activity;
            Axios.post("/api/activityVideo", this.form).then(res => {
              this.alertify.success("บันทึกกิจกรรมเรียบร้อย");
            });
          }
        }
      );
      if (!this.hideSurway) this.$refs.modalSurway.showModal();
      this.hideSurway = true;
    }
  },
  beforeDestroy() {
    if (this.socket) {
      this.socket.emit("disconnected", {
        user: this.user
      });
    }
  }
};
</script>
<style scoped>
.hideController {
  position: absolute;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0;
  z-index: 9999;
  background: #000;
  opacity: 0;
}
</style>