<template>
  <div
    class="modal fade"
    id="lamduanModal"
    tabindex="-1"
    role="dialog"
    aria-labelledby="exampleModalLabel"
    aria-hidden="true"
  >
    <div class="modal-dialog modal-dialog-centered modal-sm">
      <div class="modal-content">
        <div class="modal-header">
          <div class="p-2">
            <small>
              <a @click.prevent="toggleLang('TH')">TH</a> |
              <a @click.prevent="toggleLang('EN')">EN</a>
            </small>
          </div>

          <button
            type="button"
            class="close"
            data-dismiss="modal"
            aria-label="Close"
            @click.prevent="hideModal()"
          >
            <a class="dot-container">
              <span aria-hidden="true">&times;</span>
            </a>
          </button>
        </div>
        <form action @submit.prevent="onSubmit()">
          <div class="modal-body text-center">
            <h4 class="text-muted">UPLOAD PHOTO</h4>
            <small class="text-danger smallest">(Max file size: 2MB:PNG or JPEG)</small>
            <div id="upload-container" class="mt-2 d-flex">
              <img
                v-if="form.af_image != ''"
                class="img-fluid m-auto justify-content-center align-self-center"
                :src="form.af_image"
                alt="no image"
              />
              <label id="upload-container-desc" class="d-table pointer-event">
                <input type="file" class="d-none" @change="onChangeFile($event.target)" />
                <div class="d-table-row">
                  <div class="d-table-cell text-right p-1">
                    <small class="text-primary font-weight-bold">UPLOAD PHOTO</small>
                    <br />
                    <small class="text-muted smallest">SELECT FROM YOUR COMPUTER</small>
                  </div>
                  <div class="d-table-cell align-middle text-center p-1">
                    <div class="squre-icon">
                      <i class="fa fa-plus" aria-hidden="true"></i>
                    </div>
                  </div>
                </div>
              </label>
            </div>
            <br />
            <!--<small class="text-danger">(Max file size: 5MB)</small>-->
            <div class="form-group mt-2">
              <textarea
                class="form-control mt-2 upload-text"
                rows="3"
                placeholder="Add comment (limit 50 characters)..."
                name="af_description"
                v-validate="{
                required: true,
                max: 50
              }"
                :class="{ 'is_invalid': errors.has('af_description') }"
                v-model.trim="form.af_description"
              ></textarea>
              <p
                class="invalid_feedback"
                :class="{'d-none': !validDesc}"
              >Please fill your photo description</p>
            </div>
          </div>
          <div class="note">
            
            <label v-if="lang=='TH'" class="text-danger"><i class="fa fa-exclamation"></i>&nbsp;{{ noteTH }}</label>
            <label v-if="lang=='EN'" class="text-danger"><i class="fa fa-exclamation"></i>&nbsp;{{ noteEN }}</label>
          </div>

          <div class="text-center mt-1 mb-4">
            <button
              v-if="!submited"
              id="btn-submit-upload"
              type="submit"
              class="btn btn-primary w100"
            >SUBMIT</button>
            <p v-else>Please Wait</p>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>
<script>
import Axios from "axios";
export default {
  name: "lamduanUpload",
  data() {
    return {
      form: {
        af_description: "",
        af_image: "",
        u_username: "",
        u_id: "",
      },
      validDesc: false,
      submited: false,
      activity: "Lamduan Flower",
      noteTH:
        "หมายเหตุ: หากน้องๆ อัพรูปภาพอื่นๆที่ไม่ใช่ดอกลำดวน หรือมีการอัพรูปซ้ำกับของเพื่อน พี่ๆแอดมินจะทำการลบภาพ และกิจกรรมจะไม่สามารถนับคะแนน progress bar ให้ได้ รบกวนน้องๆ ทำกิจกรรมตามเงื่อนไขของระบบด้วยนะคะ",
      noteEN:
        "Note: If you upload picture that is not the Lamduan flower or upload the same picture with your friend. Our admins will delete the picture and your points will not be shown on a progress bar. Please do the activities according to the system conditions.",
      lang: "EN",
    };
  },
  methods: {
    showModal() {
      this.jquery("#lamduanModal").modal("show");
      this.jquery(".flex-container").addClass("blur");
      this.getData();
    },
    hideModal() {
      this.jquery(".flex-container").removeClass("blur");
    },
    getData() {
      const id = this.$store.getters.user.u_id;
      Axios.get(`/api/activityFlower/${id}`)
        .then((res) => {
          if (res.data !== "") {
            const form = res.data;
            this.form.af_description = form.af_description;
            this.form.af_image = form.af_image;
          }
        })
        .catch((ex) => {
          console.log(ex);
        });
    },
    downloadFlower() {
      Axios({
        url: "/download/flowerCrafting.pdf",
        method: "GET",
        responseType: "blob",
      }).then((response) => {
        var fileURL = window.URL.createObjectURL(new Blob([response.data]));
        var fileLink = document.createElement("a");

        fileLink.href = fileURL;
        fileLink.setAttribute("download", "flowerCrafting.pdf");
        document.body.appendChild(fileLink);

        fileLink.click();
      });
    },
    onChangeFile(input) {
      this.form.af_image = "";
      //console.log(input.files)
      if (input.files && input.files.length > 0) {
        const file = input.files[0];
        if (file.type.indexOf("image/") >= 0) {
          //console.log(file.type)
          const reader = new FileReader();
         
          if (file.size > 2000000) {
            this.alertify.error("ขนาดของภาพมากกว่า 2MB");
            return;
          }
          if(file.type != 'image/jpeg' && file.type != 'image/png'){
            this.alertify.error("Please upload jpeg or png file");
            return;
          }
          reader.readAsDataURL(file);
          reader.addEventListener("load", () => {
           
            this.form.af_image = reader.result;
          });
          //console.log(file);
          return;
        }
      } else {
        this.alertify.warning("กรุณาเลือกภาพที่จะอัพโหลด");
      }
    },
    onSubmit() {
      
      this.$validator.validateAll().then((valid) => {
        if (this.form.af_description.trim() == "") {
          this.validDesc = true;
          return;
        }
        if (this.jquery.trim(this.form.af_image) == "") {
          return this.alertify.warning("Attach your Lamduan image");
        }
        this.form.u_username = this.$store.getters.user.u_username;
        this.form.u_id = this.$store.getters.user.u_id;
        this.submited = true;
        Axios.get(`/api/activityFlower/${this.form.u_id}`).then((res) => {
          const request =
            res.data == ""
              ? Axios.post("/api/activityFlower", this.form) // create new
              : Axios.put(`/api/activityFlower/${this.form.u_id}`, this.form); // update old
          request.then((res) => {
            this.$store.dispatch("get_user_progress", {
              id: this.$store.getters.user.u_id,
            });
            this.jquery("#lamduanModal").modal("hide");
            this.$emit("onFinish");
          });
        });
      });
    },
    toggleLang(lang) {
      this.lang = lang;
    }

  },
};
</script>
