<template>
  <div class="activity h100">
    <Layout ref="mainLayout">
      <div class="text-right p-4">
        <a @click.prevent="changeLang('th')" class="pointer-event">TH</a> |
        <a @click.prevent="changeLang('en')" class="pointer-event">EN</a>
      </div>
      <div v-if="img != ''" class="mt-2">
        <img
          id="info-th"
          class="w-100 info"
          style="display: none;"
          v-bind:src="require('@/assets/Info-th/' + img + '.png')"
        />
        <img
          id="info-en"
          class="w-100 info"
          style="display: none;"
          v-bind:src="require('@/assets/Info-en/' + img + '.png')"
        />
        <div class="text-center mt-1">
          <button type="button" class="btn btn-primary" @click="onBack()">BACK</button>
        </div>
      </div>
    </Layout>
  </div>
</template>
<script>
import Axios from "axios";
import Layout from "@/components/Layout";
export default {
  components: {
    Layout
  },
  name: "schoolIntroductionInfo",
  data() {
    return {
      img: "",
      form: {
        u_id: ""
      },
      lang: "th"
    };
  },
  mounted() {
    this.loadImg();
  },
  created() {
    this.form.u_id = this.$store.getters.user.u_id;
    Axios.get(`/api/activitySchool/${this.form.u_id}`).then(res => {
      if (res.data == "") {
        Axios.post(`/api/activitySchool`, this.form).then(res => {
          //console.log("Inserted");
        });
      }
    });
    console.log(this.$route.query.school);
    this.img = this.$route.query.school;
  },
  beforeDestroy() {},
  methods: {
    loadImg(){
      this.jquery(".info").hide();
      this.jquery(`#info-${this.lang}`).show();
    },
    changeLang(lang) {
      this.lang = lang;
      this.loadImg();
    },
    onBack() {
      this.$router.push({
        path: `/ActivityIntroduction`
      });
    }
  },
  watch: {
    "$route.params.school": function(school) {}
  }
};
</script>
