<script>
export default {
  props: {
    disableVote: {
      required: true,
      type: Boolean
    },
    selectedItem: {
      required: true
    },
    item: {
      required: true,
      define: {
        u_id: 0,
        ac_url: "",
        ac_name: "",
        ac_description: ""
      }
    }
  },
  data() {
    return {
      clipName: "",
      clipDesc: ""
    };
  },
  created() {
    this.SubStr();
  },
  watch: {
    item: function(newVal, oldVal) {
      //console.log(newVal);
      this.SubStr();
    }
  },
  mounted() {
    this.onHover();
  },
  methods: {
    onHover() {
      //alert(1)

      //alert(this.jquery('[data-toggle="popover"]').length);
      this.jquery('[data-toggle="popover"]').popover({
        placement: "top",
        trigger: "hover"
      });
    },
    OnPlay(ytID) {
      this.$emit("onDisplay", ytID);
    },
    ToggleVote: function() {
      //this.isClicked = !this.isClicked;
    },
    SelectVote(ytID) {
      //this.jquery("#btnVote").click(false);
      //this.ToggleVote();
      this.$emit("selectedVoice", ytID);
    },
    DisableButton() {
      //this.jquery("#btnVote").click(false);
    },
    SubStr() {
      if (this.item.ac_name) {
        if (String(this.item.ac_name).length > 20) {
          this.clipName = String(this.item.ac_name).substring(0, 20) + "...";
        } else {
          this.clipName = this.item.ac_name;
        }
      }
      if (this.item.ac_description) {
        if (String(this.item.ac_description).length > 20) {
          this.clipDesc =
            String(this.item.ac_description).substring(0, 20) + "...";
        } else {
          this.clipDesc = this.item.ac_description;
        }
      }
    }
  }
};
