<template>
  <div class="activity h100">
    <Layout ref="mainLayout">
      <div id="paint-container" class="flex-container">
        <div class="box box-body">
          <div class="align-middle align-self-center text-center h-100 w-100">
            <img id="paint-img" :src="form.acp_image" />
            <div id="paint-moto">{{form.acp_moto}}</div>
          </div>
        </div>
        <div class="d-none d-md-block d-lg-block">
          <div class="row mb-4 d-flex">
            <div class="col-md-4 align-self-center text-center">
              <button @click="onBack()" type="button" class="btn btn-primary w100">BACK</button>
            </div>
            <div
              class="col-md-4 text-center justify-content-center align-self-center"
            >&nbsp;</div>
            <div class="col-md-4 align-self-center text-center">
              <button @click="onHome()" type="button" class="btn btn-primary w100">HOME</button>
            </div>
          </div>
        </div>
        <div class="d-md-none d-lg-none d-sm-block d-xs-block">
          <div class="row mb-4 d-flex">
            <div
              class="col-12 text-center justify-content-center align-self-center"
            >&nbsp;</div>
            <div class="col-12 mt-2 align-self-center text-center">
              <button @click="onBack()" type="button" class="btn btn-primary w100 m-3">BACK</button>
              <button @click="onHome()" type="button" class="btn btn-primary w100 m-3">HOME</button>
            </div>
          </div>
        </div>
      </div>
    </Layout>
  </div>
</template>
<script>
import Axios from "axios";
import Layout from "@/components/Layout";
export default {
  components: {
    Layout
  },
  name: "paintingGallery",
  data() {
    return {
      form: {
        acp_image: null,
        acp_moto: ""
      },
      activityName: this.activity[9].name
    };
  },
  computed: {},
  watch: {},
  mounted() {},
  created() {
    this.getData();
  },
  beforeDestroy() {},
  methods: {
    getData() {
      const id = this.$store.getters.user.u_id;
      Axios.get(`/api/activityPainting/${id}`).then(res => {
        console.log(res);
        this.form.acp_image = res.data.acp_image;
        this.form.acp_moto = res.data.acp_moto;
      });
    },
    onHome() {
      this.$router.push({
        name: `home`
      });
    },
    onBack() {
      this.$router.push({
        path: `/ActivityPreview/${this.activityName}`
      });
    }
  }
};
</script>
