import Vue from 'vue'
import Vuex from 'vuex'
import Axios from 'axios'
import createPersistedState from "vuex-persistedstate";
Vue.use(Vuex)

export default new Vuex.Store({
  plugins: [createPersistedState()],
  state: {
    user: [],
    userAdmin: null,
    userProgress: [
      { progress: 0 },
      { activity: [] }

    ],
    activity: [],
    notification: []
    //activityFlowerImg: [],
    //activityContest: [],
    //activityVoteContest: []
  },

  mutations: {
    set_User: (state, user) => { state.user = user },
    set_UserProgress: (state, userProgress) => { state.userProgress = userProgress },
    set_AdminUser: (state, userAdmin) => { state.userAdmin = userAdmin },
    set_Activity: (state, activity) => { state.activity = activity },
    set_Notification: (state, notification) => { state.notification = notification }
    //set_ActivityFlowerImg: (state, activityFlowerImg) => state.activityFlowerImg = activityFlowerImg,
    //set_ActivityContest: (state, activityContest) => state.activityContest = activityContest,
    //set_ActivityVoteContest: (state, activityVoteContest) => state.activityVoteContest = activityVoteContest
  },
  getters: {
    user: state => state.user,
    userAdmin: state => state.userAdmin,
    userProgress: state => state.userProgress,
    activity: state => state.activity,
    notification: state => state.notification
  },
  actions: {
    get_user_login: ({ commit }) => Axios.post('/api/account/getUserLogin').then(res => {
      //console.log(res)
      //console.log("====")
      commit('set_User', res.data)
    }),
    get_user_progress: ({ commit }, params = { id: 0 }) => Axios.get('/api/account/progress', { params }).then(res =>
      commit('set_UserProgress', res.data)
    ),
    get_notification: ({ commit }, params = { id: 0 }) => Axios.get('/api/notification/byID', { params }).then(res =>
      commit('set_Notification', res.data)
    ),
    //get_ActivityFlowerImg: ({ commit }, params = { page: 1 }) => Axios.get(`/api/activityFlower`, { params }).then(res => commit('set_ActivityFlowerImg', res.data)),
    //get_ActivityContest: ({ commit }, params = { page: 1 }) => Axios.get('/api/activityContest', { params }).then(res => commit('set_ActivityContest', res.data)),
    /*get_ActivityVoteContest: ({ commit }, params = { page: 1 }) => Axios.get('/api/activityContest/contest', { params }).then(res => {
      commit('set_ActivityVoteContest', res.data)
    }),*/
    get_admin_login: ({ commit }) => Axios.post('/api/admin/getAdminLogin').then(res => {
      commit('set_AdminUser', res.data)
    }),
    get_activity: ({ commit }, params = { activity: [] }) =>  { 
      //console.log(params)
      commit("set_Activity", params.activity)
     }
      
  }
})


