<template>
  <div class="home">
    <Layout>
      <div class="card">
        <div class="card-body">
          <h3>{{ form.admin_av_name }}</h3>
          <form class="mt-5" @submit.prevent="onSubmit()">
            <!--
            <div class="form-group">
              <div class="row">
                <div class="col-md-3">
                  <p>แสดงวิดีโอ</p>
                </div>
                <div class="col-md-9">
                  <input type="checkbox" v-model="form.admin_av_enable" value="1" />
                </div>
              </div>
              <div class="row">
                <div class="col-md-3">
                  <p>ข้อความที่แสดง ในกรณีที่ปิดการแสดงผล</p>
                </div>
                <div class="col-md-6">
                  <div class="form-group">
                    <textarea
                      class="form-control"
                      cols="5"
                      v-model.trim="form.admin_av_disable_msg"
                    ></textarea>
                  </div>
                </div>
              </div>
            </div
            -->
            <div class="form-group">
              <div class="row">
                <div class="col-md-3">
                  <p>เริ่มการเล่นอัตโนมัติ</p>
                </div>
                <div class="col-md-9">
                  <input
                    type="checkbox"
                    v-model.trim="form.admin_av_autoplay"
                    :checked="form.admin_av_autoplay"
                  />
                </div>
              </div>
            </div>

            <div class="form-group">
              <div class="row">
                <div class="col-md-3">
                  <p>ปิด​ Youtube Controller</p>
                </div>
                <div class="col-md-9">
                  <input
                    type="checkbox"
                    v-model.trim="form.admin_av_hide_controller"
                    :checked="form.admin_av_hide_controller"
                  />
                </div>
              </div>
            </div>
            <div class="form-group">
              <div class="row">
                <div class="col-md-3">
                  <label>Youtube URL</label>
                </div>
                <div class="col-md-6">
                  <input
                    type="text"
                    name="ac_url"
                    v-validate="{
                    required: true
                  }"
                    :class="{ 'is_invalid': errors.has('ac_url') }"
                    v-model.trim="form.admin_av_yt_id"
                    v-on:change="ytUrlChange"
                    class="form-control"
                    placeholder="URL..."
                  />
                  <p class="invalid_feedback">{{ errors.first('ac_url') }}</p>
                </div>
                <div class="col-md-3"></div>
              </div>

              <div class="row">
                <div class="col-md-3"></div>
                <div class="col-md-5">
                  <img
                    v-if="form.admin_av_yt_id"
                    v-bind:src="`https://img.youtube.com/vi/${form.admin_av_yt_id}/default.jpg`"
                    @click="displayYoutube(form.admin_av_yt_id)"
                  />
                  <img
                    v-else
                    class="img-fluid"
                    style="width:auto; height:100%"
                    :src="'/img/no-image.jpg'"
                    alt="no image"
