              <div class="col-md-4"></div>
            </div>
          </form>
        </div>
      </div>
    </Layout>
    <YoutubeModal ref="ytModal" />
  </div>
</template>
<script>
import Axios from "axios";
import Layout from "@/components/admin/Layout";
import YoutubeModal from "@/components/YoutubeModal";
import getVideoId from "get-video-id";
export default {
  components: {
    Layout,
    YoutubeModal
  },
  name:'VideoController',
  data() {
    return {
      form: {
        admin_av_name: "",
        admin_av_enable: "",
        admin_av_disable_msg: "",
        admin_av_autoplay: "",
        admin_av_hide_controller: "",
        admin_av_yt_id: ""
      }
    };
  },
  computed:{

  },
  watch: {
    "$route.params.type": function(type) {
      this.form.admin_av_name = type;
      this.$validator.reset();
      this.initialValue();
    }
  },
  mounted() {
    this.initialValue();
  },
  created() {
    this.form.admin_av_name = this.$route.params.type;
  },
  beforeDestroy(){

  },
  methods: {
    initialValue() {
      Axios.get(`/api/controllerVideo/${this.form.admin_av_name}`)
        .then(res => {
          const form = res.data;
          this.form.admin_av_yt_id = form.admin_av_yt_id;
          this.form.admin_av_enable = form.admin_av_enable;
          this.form.admin_av_disable_msg = form.admin_av_disable_msg;
          this.form.admin_av_autoplay = form.admin_av_autoplay;
          this.form.admin_av_hide_controller = form.admin_av_hide_controller;
        })
        .catch(err => {
          //this.alertify.error(err);
        });
    },
    displayYoutube(youtubeID) {
      this.$refs.ytModal.showModal(youtubeID);
    },
    ytUrlChange() {
      this.form.admin_av_yt_id = getVideoId(this.form.admin_av_yt_id).id;
    },
    onSubmit() {
      this.$validator.validateAll().then(valid => {
        Axios.post("/api/controllerVideo/", this.form)
          .then(res => {
            this.alertify.success("บันทึกข้อมูลเรียบร้อย");
          })
          .catch(err => {
            this.alertify.error("กรุณาตรวจสอบใหม่");
          });
      });
    }
  }
};
</script>
<style scoped>
label {
  color: black;
}
