<template>
  <div class="activity h100">
    <Layout>
      <div class="container">
        <div id="main" class="row position-relative h-100">
          <div class="col-md-7">
            <div id="ytContainer" class="ytContainer">
              <youtube
                class="w-100 h-100"
                ref="youtube"
                @ready="ready"
                video-id="mIhENcGXKMU"
                :player-vars="{ autoplay: 1, controls: 0 }"
              ></youtube>
            </div>
          </div>
          <div class="col-md-5">
            <div class="text-right">
              <small>
                <a @click.prevent="toggleLang('th')">TH</a> |
                <a @click.prevent="toggleLang('en')">EN</a>
              </small>
            </div>
            <h3 class="mt-4 text-center">{{ title }}</h3>
            <!--<h2 class="text-primary font-weight-bold text-center">{{ subtitle }}</h2>-->
            <h2 v-if="lang=='th'" class="text-primary font-weight-bold text-center">{{ subtitle.th }}</h2>
            <h2 v-if="lang=='en'" class="text-primary font-weight-bold text-center">{{ subtitle.en }}</h2>
            <div v-if="lang=='en'" class="text-justify">{{ descEN }}</div>
            <div v-else-if="lang=='th'" class="text-justify">{{ descTH }}</div>
            <div class="row justify-content-center mt-1">
              <div class="col-8 text-center">
                <div class="d-table m-auto">
                  <div class="d-table-row">
                    <div class="d-table-cell align-middle text-center p-1">
                      <div class="squre-icon">
                        <a @click.prevent="downloadFlower()">
                          <i class="fa fa-long-arrow-down" aria-hidden="true"></i>
                        </a>
                      </div>
                    </div>
                    <div class="d-table-cell text-left p-1">
                      <a @click.prevent="downloadFlower()">
                        <small class="text-primary font-weight-bold">DOWNLOAD</small>
                        <br />
                        <small class="text-muted smallest">LAMDUAN ORIGAMI TUTORIAL</small>
                      </a>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="row justify-content-center mt-4">
              <div class="col-10">
                <button
                  class="btn btn-primary pull-left w100"
                  type="button"
                  @click.prevent="onBack()"
                >BACK</button>
                <button
                  class="btn btn-primary pull-right w100"
                  type="button"
                  @click.prevent="onStart()"
                >NEXT</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Layout>
    <ModalLamduanUpload ref="lamduanUpload" @onFinish="onEndActivity()" />
    <ModalSurway :activity="activityName" @onComplete="onBack()" ref="modalSurway" />
  </div>
</template>
<script>
import Layout from "@/components/Layout";
import ModalLamduanUpload from "@/components/ModalLamduanUpload";
import ModalSurway from "@/components/ModalSurway";
import Axios from "axios";
export default {
  name: "lamduanUpload",
  components: {
    Layout,
    Axios,
    ModalLamduanUpload,
    ModalSurway
  },
  data() {
    return {
      form: {
        u_id: null,
        u_username: null,
        av_name: null
      },
      setting: {
        admin_av_autoplay: null,
        admin_av_hide_controller: null,
        admin_av_disable_msg: null,
        admin_av_enable: null,
        admin_av_yt_id: null
      },
      player: null,
      lang: "en",
      duration: 0,
      display: "video",
      title: "",
      subtitle: "",
      descEN: "",
      descTH: "",
      activityName: "",
      finished: false
    };
  },
  computed: {},
  watch: {},
  mounted() {
    window.addEventListener("resize", this.resizeYT);
  },

  created() {
    this.initialValue();
  },
  beforeDestroy() {
    window.addEventListener("resize", this.resizeYT);
  },
  methods: {
    ready() {
      this.resizeYT();
    },
    initialValue() {
      const activity = this.activity[1]
      this.title = activity.title;
      this.subtitle = activity.subtitle;
      this.descEN = activity.description.en;
      this.descTH = activity.description.th;
      this.activityName = activity.name;
      this.$store.dispatch("get_activity", { activity: this.activity[1] });
      this.duration = 0;
    },
    resizeYT() {
      this.ytHeight = (this.jquery("#ytContainer").width() * 9) / 16;
      this.jquery("#ytContainer").height(this.ytHeight);
    },
    toggleLang(lang) {
      this.lang = lang;
    },
    downloadFlower() {
      Axios({
        url: "/download/flowerCrafting.pdf",
        method: "GET",
        responseType: "blob"
      }).then(response => {
        var fileURL = window.URL.createObjectURL(new Blob([response.data]));
        var fileLink = document.createElement("a");
        fileLink.href = fileURL;
        fileLink.setAttribute("download", "flowerCrafting.pdf");
        document.body.appendChild(fileLink);
        fileLink.click();
      });
    },

    onBack() {
      this.$router.push({
        path: `/ActivityPreview/${this.activityName}`
      });
    },
    onEndActivity() {
      const id = this.$store.getters.user.u_id;
      Axios.get(`/api/surway/${id}?activity=${this.activityName}`)
        .then(res => {
          if (res.data.length == 0) {
            this.$refs.modalSurway.showModal();
          } else {
            this.$router.push({
              name: `activityFlowerGallery`
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    onStart() {
      this.$refs.lamduanUpload.showModal();
    }
  }
};
</script>
<style scoped>
.ytContainer {
  margin-top: 50px;
}
.hideController {
  position: absolute;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0;
  z-index: 9999;
  background: #000;
  opacity: 0;
}
@media screen and (max-width: 768px) {
  .ytContainer {
    margin-top: 0;
  }
  .col-md-5,
  .col-md-7 {
    flex: 0 0 95%;
    max-width: 100%;
  }
}
</style>