      playerVars: {}
    };
  },
  methods: {
    resetData() {
      this.ytID = "";
      this.playerVars = {};
      this.$refs.youtube.player.stopVideo();
      //this.$refs.yt.player.stopVideo();
    },
    showModal(youtubeID) {
