      ],
      loaded: false
    };
  },
  mounted() {},
  created() {
    const id = this.$store.getters.user.u_id;
    Axios.get(`/api/activitySchool/${id}`).then(res => {
      //console.log(res)
      if (res.data == "") {
        this.$router.push({
          path: `/ActivityIntroductionInfo`,
          query: { school: this.$store.getters.user.u_school }
        });
      }else{
        this.loaded = true;
      }
    });
    //console.log(this.School)
  },
  beforeDestroy() {},
  methods: {
    style(index) {
      return (
        "border:8px solid " +
        this.School[index].borderColor +
        ";color: " +
        this.School[index].borderColor +
        "; background-color: " +
        this.School[index].bgColor +
        ";"
      );
    },
    onHome() {
      this.$router.push({
        name: `home`
      });
    },
    onBack() {
      this.$router.push({
        path: `/ActivityPreview/${this.activityName}`
      });
    },
    displayInfo(school) {
      ///alert(school)
      
      this.$router.push({
        path: `/ActivityIntroductionInfo`,
        query: { school: school }
      });
      
    }
  },
  computed: {
    SchoolList() {
      return this.School.filter(post => {
        return post.name.toLowerCase().includes(this.search.toLowerCase());
      });
    }
  }
};
</script>
