<template>
  <div class="home">
    <Layout>
      <div class="card">
        <div class="card-body">
          <h3>Lamduan Flower</h3>
          <div class="table-responsive mt-3">
            <table class="table text-center">
              <thead class="thead-dark">
                <tr>
                  <th>#</th>
                  <th>รูปภาพ</th>
                  <th>รหัสนักศึกษา</th>
                  <th>ชื่อนักศึกษา</th>
                  <th>Comment</th>
                  <th>วันที่แก้ไขล่าสุด</th>
                  <th>ลบข้อมูล</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(item, index) in activityFlower.result" :key="index">
                  <td>{{ (index + 1)+((page-1)*activityFlower.limit) }}</td>
                  <td>
                    <img :src="item.af_image" class="w100" />
                  </td>
                  <td>{{ item.u_id }}</td>
                  <td>{{ item.u_username }}</td>
                  <td>{{ item.af_description }}</td>
                  <td>{{ new Date(item.af_updated).toISOString().replace(/T/, ' ').replace(/\..+/, '') }}</td>
                  <td>
                    <button class="btn btn-danger text-white btn-sm" @click="onDelete(item)">
                      <i class="fa fa-trash"></i>
                    </button>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
          <div class="mt-2 overflow-auto w-100" v-if="activityFlower.rows > activityFlower.limit">
            <Pagination :data="activityFlower" :page="page" @onPage="onPage($event)" />
          </div>
        </div>
      </div>
    </Layout>
  </div>
</template>
<script>
import Axios from "axios";
import school from "@/school";
import Layout from "@/components/admin/Layout";
import Pagination from "@/components/admin/Pagination";
import { mapState } from "vuex";
export default {
  components: {
    Layout,
    Pagination,
  },
  name: "Lamduan Flower",
  data() {
    return {
      form: {
        ins_major: null,
        ins_school: "เลือกสำนักวิชา....",
        ins_url: "",
        ins_name: "",
        ins_description: "",
        ins_url_id: "",
      },
      activityFlower: {},
      page: 1,
      Vote: {
        _id: "",
        total: 0,
      },
      UploadVideo: "",
      School: school,
      major: [],
    };
  },
  computed: {},
  watch: {
    "$route.params.type": function (type) {
      this.page = 1;
      this.getContent();
    },
  },
  mounted() {},
  created() {
    this.getContent();
  },
  beforeDestroy() {},
  methods: {
    getContent() {
      Axios.get("/api/activityFlower/", {
        params: {
          page: this.page,
        },
      })
        .then((res) => {
          console.log(res.data);
          this.activityFlower = res.data;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    onPage(page) {
      this.page = page;
      this.getContent();
    },
    onDelete(item) {
      //console.log(item._id);
      let form = { id: item.u_id, s_activity: "Lamduan Flower" };
      this.alertify.confirm("Confirm Deleting?", () => {
        Axios.delete(`api/activityFlower/${item._id}`, { params: form }).then(
          (res) => {
            this.alertify.error("Delete Complete");
            this.getContent();
          }
        );
      });
      /*
      this.alertify.confirm("Confirm Deleting?", () => {
        let form = { id: id, s_activity: "Lamduan Flower" };
        Axios.delete(`api/activityFlower/${id}`).then(res => {
          Axios.delete("api/surway/", { params: form }).then(finish => {
            this.alertify.error("Delete Complete");
            this.getContent();
          });
        });
      });
      */
    },
  },
};
</script>
