<template>
  <div class="activity h100">
    <Layout ref="mainLayout">
      <div
        class="row position-absolute justify-content-center"
        style="top: 0;left: 0;right: 0;bottom: 0;z-index: 9;background: transparent"
      >
        <div class="col-lg-4 col-md-12 col-sm-12 col-12 d-flex">
          <div class="mt-5 align-self-center">
            <div class="card text-container">
              <div class="p-2" :class="`text-right ${ paragraphCss }`">
                <small>
                  <a @click.prevent="toggleLang('TH')">TH</a> |
                  <a @click.prevent="toggleLang('EN')">EN</a>
                </small>
              </div>
              <div class="card-body text-center">
                <h3 :class="`${ titleCss }`">{{ title }}</h3>
                <h2 :class="`font-weight-bold ${ subTitleCss }`" v-if="lang=='EN'">{{ subtitle.en }}</h2>
                <h2 :class="`font-weight-bold ${ subTitleCss }`" v-if="lang=='TH'">{{ subtitle.th }}</h2>
              </div>
              <div :class="`${ paragraphCss }`">
                <div v-if="lang=='EN'" class="card-body text-center">{{ descEN }}</div>
                <div v-else-if="lang=='TH'" class="card-body text-center">{{ descTH }}</div>
              </div>
              <div class="text-center m-3">
                <button @click.prevent="onHome()" class="btn btn-success mr-4 w100">Back</button>
                <button @click.prevent="onJoin()" type="button" class="btn btn-success w100">JOIN</button>
              </div>
            </div>
          </div>
        </div>
        <div class="col-lg-5 offset-lg-1 col-md-12 col-sm-12 col-12">
          <div class="row align-items-center h-100">
            <div class="col-xs-12 col-sm-6 col-lg-6" v-for="(item, index) in options" :key="index">
              <div v-if="!item.lock || done" class="option-container p-4">
                <h3 v-if="lang=='EN'" class="text-primary font-weight-bold">{{ item.title.en }}</h3>
                <h3 v-else-if="lang=='TH'" class="text-primary font-weight-bold">{{ item.title.th }}</h3>
                <h2 v-if="lang=='EN'" class="font-weight-bold">{{ item.subtitle.en }}</h2>
                <h2 v-if="lang=='TH'" class="font-weight-bold">{{ item.subtitle.th }}</h2>
                <p v-if="lang=='EN'" class="text-center">{{ item.desc.en }}</p>
                <p v-if="lang=='TH'" class="text-center">{{ item.desc.th }}</p>
                <div class="text-center mt-3">
                  <button
                    type="button"
                    @click.prevent="onLink(`/${item.path}`)"
                    class="btn btn-primary w100"
                  >JOIN</button>
                </div>
              </div>
              <div v-else class="option-container p-4 d-flex">
                <img
                  src="../assets/img/lock.png"
                  class="w-75 m-auto justify-content-center align-self-center"
                />
              </div>
            </div>
          </div>
        </div>
      </div>
      <!-- <div id="bg-thumb-container">
        <a
          @click.prevent="onChangeBG(item)"
          v-for="(item, index) in this.bgs"
          :key="index"
          class="bg-thumb"
          :style="{ backgroundImage: 'url(' + require('@/assets/img/activity/'+item) + ')' }"
        ></a>
      </div>-->
    </Layout>
  </div>
</template>
<script>
import Layout from "@/components/Layout";
export default {
  components: {
    Layout
  },
  name: "",
  data() {
    return {
      title: "TITLE",
      subtitle: "SUBTITLE",
      descEN: "",
      descTH: "",
      lang: "EN",
      bgs: [],
      options: [],
      titleCss: "",
      subTitleCss: "",
      paragraphCss: "",
      gradient: false,
      name: "",
      done: false
    };
  },
  computed: {},
  watch: {},
  mounted() {
    this.onChangeBG(this.$store.getters.activity.bgs[0]);
  },
  created() {
    //console.log(this.$store.getters.activity);
    if (this.$store.getters.activity) {
      this.title = this.$store.getters.activity.title;
      this.subtitle = this.$store.getters.activity.subtitle;
      this.descEN = this.$store.getters.activity.description.en;
      this.descTH = this.$store.getters.activity.description.th;
      this.bgs = this.$store.getters.activity.bgs;
      this.options = this.$store.getters.activity.options;
      this.titleCss = this.$store.getters.activity.css.title;
      this.subTitleCss = this.$store.getters.activity.css.subtitle;
      this.paragraphCss = this.$store.getters.activity.css.paragraph;
      this.name = this.$store.getters.activity.name;
      this.gradient = this.$store.getters.activity.gradient;
     
    } else {
      this.$router.push({ name: "home" });
    }
    this.checkActDone();
  },
  beforeDestroy() {},
  methods: {
    onHome() {
      this.$router.push({
        name: `home`
      });
    },
    checkActDone() {
      const activityProgress = this.$store.getters.userProgress.activity;
      activityProgress.forEach(item => {
        if (item.name == this.name) {
          if (item.done == 1) {
            this.done = true;
          }
          return;
        }
      });
    },
    toggleLang(lang) {
      this.lang = lang;
    },
    onChangeBG(img) {
      //alert(window.innerWidth);
      var gradient = "";
      if (this.gradient == "black") {
        gradient =
          "linear-gradient(to right, rgba(0, 0, 0, 0.8), rgba(256, 256, 256, 0.1)),";
      } else if (this.gradient == "white") {
        if (window.innerWidth <= 792) {
          gradient =
            "linear-gradient(to right, rgba(256, 256, 256, 0.8), rgba(256, 256, 256, 0.8)),";
        }
      }
      this.$refs.mainLayout.setBG({
        backgroundImage:
          gradient + " url(" + require("@/assets/img/activity/" + img) + ")"
      });
    },
    onLink(path) {
      this.$router.push({
        path: path
      });
    },
    onJoin() {
      this.$router.push({
        path: this.$store.getters.activity.path
      });
    }
  }
};
</script>
