<template>
  <div class="activity h100">
    <Layout>
      <div class="container mt-5">
        <div class="container">
          <div id="main" class="row position-relative h-100 ytContainer">
            <div class="col-md-7 position-relative">
              <div id="ytContainer" class="ytContainer">
                <youtube
                  v-if="loaded"
                  class="w-100 h-100"
                  ref="youtube"
                  @ready="ready"
                  @playing="playing"
                  @paused="paused"
                  :video-id="setting.ytID"
                  :player-vars="{ autoplay: 1, controls: 0, modestbranding:1, rel: 0, showinfo: 0 }"
                ></youtube>
              </div>
            </div>
            <div class="col-md-5">
              <div class="text-right mt-5">
                <small>
                  <a @click.prevent="toggleLang('th')">TH</a> |
                  <a @click.prevent="toggleLang('en')">EN</a>
                </small>
              </div>
              <h3 class="mt-4 text-center">{{ title }}</h3>
              <h2 class="text-primary font-weight-bold text-center">{{ setting.name }}</h2>

              <div
                v-if="lang=='th'"
                class="text-center"
              >แรงบันดาลจากผู้ประสบความสำเร็จสู่น้องๆ ผู้เริ่มต้น รับฟังแนวคิด ปัญหาอุปสรรค และความท้าทายในสายอาชีพ</div>
              <div
                v-if="lang=='en'"
                class="text-center"
              >Sharing inspring stories of successful people about their ideas, struggles, and challenges in their career paths to the newcomers</div>
              <div class="text-center m-3">
                <a @click.prevent="onBack()" class="btn btn-success mr-4 w100">Back</a>

                <Timer v-if="!finished" @onFinish="onFinish($event)" ref="timer" />
                <!--<button
                  v-else
                  class="btn btn-primary w100"
                  type="button"
                  @click.prevent="onEndActivity()"
                >NEXT</button>-->
                <button
                  v-else
                  class="btn btn-primary w100"
                  type="button"
                  @click.prevent="saveActivity()"
                >NEXT</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Layout>
    <ModalSurway :activity="activityName" @onComplete="onBack()" ref="modalSurway" />
  </div>
</template>
<script>
import Axios from "axios";
import ModalSurway from "@/components/ModalSurway";
import Complete from "@/components/Complete";
import Timer from "@/components/Timer";
import Layout from "@/components/Layout";
export default {
  components: {
    Axios,
    Timer,
    ModalSurway,
    Complete,
    Layout
  },
  name: "",
  data() {
    return {
      form: {
        u_id: null,
        u_username: null,
        av_name: null
      },
      setting: {
        name: "",
        description: "",
        school: "",
        major: "",
        ytID: "",
        imgSrc: ""
      },
      loaded: false,
      player: null,
      lang: "en",
      duration: 0,
      display: "video",
      title: "",
      subtitle: "",
      descEN: "",
      descTH: "",
      activityName: "",
      finished: false,
      ytHeight: 0
    };
  },
  computed: {},
  watch: {},
  mounted() {
    this.getContent();
    window.addEventListener("resize", this.resizeYT);
  },
  created() {
    //console.log(this.activity)
    const activity = this.activity[8];
    this.$store.dispatch("get_activity", { activity: this.activity[8] });
    this.title = activity.title;
    this.activityName = activity.name;
  },
  beforeDestroy() {
    window.removeEventListener("resize", this.resizeYT);
  },
  methods: {
    resizeYT() {
      this.ytHeight = (this.jquery("#ytContainer").width() * 9) / 16;
      this.jquery("#ytContainer").height(this.ytHeight);
    },
    toggleLang(lang) {
      this.lang = lang;
    },
    getContent() {
      Axios.get("/api/controllerInspiration/findOneBymajor", {
        params: {
          ins_major: this.$store.getters.user.u_major,
          ins_school: this.$store.getters.user.u_school
        }
      }).then(res => {
        this.setting.name = res.data.ins_name;
        this.setting.description = res.data.ins_description;
        this.setting.school = res.data.ins_school;
        this.setting.ytID = res.data.ins_url;
        this.loaded = true;

        if (this.setting.ytID == "") {
          this.finished = true;
          /*
          this.finishedByOpen = true;
          const id = this.$store.getters.user.u_id;
          Axios.get(
            `/api/activityVideo/${id}?av_name=${this.activityName}`
          ).then(res => {
            if (!res.data) {
              this.form.u_id = id;
              this.form.u_username = this.$store.getters.user.u_username;
              this.form.av_name = this.activityName;
              Axios.post("/api/activityVideo", this.form).then(res => {
                this.$store.dispatch("get_user_progress", {
                  id: this.$store.getters.user.u_id
                });
                setTimeout(() => {
                  this.onSerway();
                }, 5000);
              });
            } else {
              setTimeout(() => {
                this.onSerway();
              }, 5000);
            }
          });
          */
        }
      });
    },
    ready() {
      this.player = this.$refs.youtube.player;
      this.player.seekTo(0);
      this.player.playVideo();
      this.resizeYT();
    },
    async playing() {
      this.duration = await this.player.getDuration();
      this.videoTime = await this.player.getCurrentTime();
      if (!this.finished) {
        this.$refs.timer.setDuration(this.duration - this.videoTime);
        this.$refs.timer.onResume();
      }
    },
    paused() {
      if (!this.finished) {
        this.$refs.timer.onPause();
      }
    },
    onEndActivity() {
      const id = this.$store.getters.user.u_id;
      Axios.get(`/api/surway/${id}?activity=${this.activityName}`)
        .then(res => {
          if (res.data.length == 0) {
            this.$refs.modalSurway.showModal();
          } else {
            this.onBack();
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    onBack() {
      this.$router.push({
        path: `/ActivityPreview/${this.activityName}`
      });
    },
    onSerway() {
      const id = this.$store.getters.user.u_id;
      Axios.get(`/api/surway/${id}?activity=${this.activityName}`)
        .then(res => {
          if (res.data.length == 0) {
            this.$refs.modalSurway.showModal();
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    onFinish() {
      this.$refs.youtube.player.seekTo(0);
      this.$refs.youtube.player.pauseVideo();
      this.finished = true;
    },
    saveActivity() {
      const id = this.$store.getters.user.u_id;
      Axios.get(`/api/activityVideo/${id}?av_name=${this.activityName}`).then(
        res => {
          console.log("=====check activity====");
          if (!res.data) {
            this.form.u_id = id;
            this.form.u_username = this.$store.getters.user.u_username;
            this.form.av_name = this.activityName;
            Axios.post("/api/activityVideo", this.form).then(res => {
              //console.log("=====saved activity====");
              //this.onBack();
              this.onEndActivity()
            });
          } else {
            this.onBack();
          }
        }
      );
    }
  }
};
</script>
<style scoped>
.ytContainer {
  margin-top: 80px;
}
@media screen and (max-width: 768px) {
  .ytContainer {
    margin-top: 0;
  }
}

.hideController {
  position: absolute;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0;
  z-index: 9999;
  background: #000;
  opacity: 0;
}
</style>
