<template>
  <nav aria-label="Page navigation example pagination">
    <ul class="pagination">
      <li class="page-item" :class="{ disabled: page <= 1 }">
        <a class="page-link" @click.prevent="onPage(page - 1)">
          <span aria-hidden="true">&laquo;</span>
        </a>
      </li>

      <li
        class="page-item"
        v-for="item in getPageItem"
        :key="item"
        :class="{ active: page == item }"
      >
        <a @click.prevent="onPage(item)" class="page-link" href="#">{{ item }}</a>
      </li>

      <li class="page-item" :class="{ disabled: page >= getPageItem.length }">
        <a class="page-link" @click.prevent="onPage(page + 1)">
          <span aria-hidden="true">&raquo;</span>
        </a>
      </li>
    </ul>
  </nav>
</template>
<script>
export default {
  props: {
    data: {
      required: true
    },
    page: {
      type: Number,
      required: true,
      default: 1
    }
  },
  computed: {
    getPageItem() {
      const pages = [];
      if (this.data.rows && this.data.result) {
        const rows = this.data.rows;
        const limit = this.data.limit;
        for (var index = 1; index <= Math.ceil(rows / limit); index++) {
          pages.push(index);
        }
      }
      return pages;
    }
  },
  methods: {
    onPage(page) {
      this.$emit("onPage", page);
    }
  }
};
</script>