<template>
  <div class="home">
    <Layout>
      <div class="card">
        <div class="card-body">
          <h3>Survey</h3>
          <div v-if="loaded == true">
            <BarChart v-if="loaded" :chartData="chartdata" />
          </div>
        </div>
        <!-- DropDown -->
        <div class="row justify-content-end">
          <div class="pl-3">
            <label class="mt-3">Download File CSV</label>
          </div>
          <div class="col-md-9">
            <div class="btn-group">
              <button
                type="button"
                class="btn btn-secondary dropdown-toggle p-2 mt-2 mb-4 pl-4 pr-4"
                data-toggle="dropdown"
                aria-haspopup="true"
                aria-expanded="false"
              >{{this.SelectDownload}}</button>
              <div class="dropdown-menu">
                <a class="dropdown-item" @click="ChangeSelectToDownload('All Activity')">All rating</a>
                <div class="dropdown-divider"></div>
                <div v-for="(item, index) in this.DownloadList" :key="index">
                  <a class="dropdown-item" @click="ChangeSelectToDownload(item.name)">{{item.name}}</a>
                </div>
              </div>
              <div class="Download">
                <button
                  type="button"
                  class="btn btn-primary"
                  @click="downloadFile()"
                  v-if="loaded == true"
                >
                  <i class="fa fa-download" aria-hidden="true"></i>&nbsp;Download As CSV
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
      <!--  -->
    </Layout>
  </div>
</template>

<script>
import Axios from "axios";
import Layout from "@/components/admin/Layout";
import BarChart from "@/components/admin/LineChart.js";
import Pagination from "@/components/admin/Pagination";

import { mapState } from "vuex";
export default {
  components: {
    Layout,
    Pagination,
    BarChart,
  },
  name: "surway",
  data() {
    return {
      form: {
        id: [],
        r_id: "",
      },
      tbody: {
        u_id: [],
        u_phone: [],
        u_problem: [],
        u_comment: [],
      },
      search: "",
      page: 1,
      surway: {
        followThePath: [],
        lamduanFlower: [],
        WalkRally: [],
        Khontok: [],
        SetYourGoal: [],
        SmartLearner: [],
        TeenLesson: [],
        School: [],
        Inspiration: [],
        Painting: [],
        Alumni: [],
        ContentContest: [],
      },
      itemsFile: [],
      fileTitle: "",
      headers: {
        u_id: "Student ID".replace(/,/g, ""),
        s_activity: "Activity",
        s_rating: "Rating",
        s_comment: "Comment",
      },
      DownloadList: [],
      SelectDownload: "เลือกกิจกรรม...",
      selected: [],
      rating: {
        activity: "",
        five: 0,
        four: 0,
        three: 0,
        two: 0,
        one: 0,
      },
      chartdata: {
        labels: ["1 stars", "2 stars", "3 stars", "4 stars", "5 stars"],
        datasets: [
          {
            label: "Data One",
            backgroundColor: "#f87979",
            data: [40, 20],
          },
        ],
      },
      options: {
        responsive: true,
        maintainAspectRatio: false,
      },
      loaded: false,
    };
  },
  computed: {},
  watch: {},
  mounted() {},
  created() {
    this.getData();
  },
  beforeDestory() {},
  methods: {
    getData() {
      Axios.get("/api/surway/rating/").then((res) => {
        let array = [];
        let list = [];
        for (let i in res.data) {
          for (let n in res.data[0].rating) {
            array.push(res.data[i].rating[n]);
          }
          array.push({ name: res.data[i].activity });
          list.push(array);
          array = [];
        }
        let add = [{ data: [] }];
        let chart = [];

        for (let h in list) {
          add.data = list[h];
          // console.log(add.data)
          if (add.data[5].name == "School") {
            continue;
          } else {
            this.DownloadList.push({ name: add.data[5].name });
            chart.push({
              label: add.data[5].name,
              data: add.data,
            });
          }
        }
        //สีของ chart
        for (let n in chart) {
          console.log(chart[n]);
          if (this.activity[n].css.Chartcolor == "") {
            continue;
          }
          chart[n].backgroundColor = this.activity[n].css.Chartcolor;
        }
        this.chartdata.datasets = chart;
        // console.log(this.chartdata)
        this.loaded = true;
      });
    },
    onShow(item) {
      this.alertify.alert("รายละเอียด", item, () => {});
    },
    onSearch(search) {
      this.search = search;
      this.page = 1;
      this.getData({
        params: {
          page: this.page,
          ...search,
        },
      });
    },
    onPage(page) {
      this.page = page;
      this.getData({
        params: {
          page: this.page,
        },
      });
    },
    ChangeSelectToDownload(value) {
      this.SelectDownload = value;
      //console.log(this.SelectDownload);
    },
    convertToCSV(objArray) {
      var array = typeof objArray != "object" ? JSON.parse(objArray) : objArray;
      var str = "";
      for (var i = 0; i < array.length; i++) {
        var line = "";
        for (var index in array[i]) {
          if (line != "") line += ",";
          line += array[i][index];
        }
        str += line + "\r\n";
      }
      return str;
    },
    exportCSVFile(headers, items, fileTitle) {
      if (headers) {
        items.unshift(headers);
      }

      // Convert Object to JSON
      var jsonObject = JSON.stringify(items);
      var csv = this.convertToCSV(jsonObject);
      var exportedFilenmae = fileTitle + ".csv" || "export.csv";
      var blob = new Blob(["\ufeff" + csv], {
        type: "text/csv;charset=utf-8;",
      });
      if (navigator.msSaveBlob) {
        // IE 10+
        navigator.msSaveBlob(blob, exportedFilenmae);
      } else {
        var link = document.createElement("a");
        if (link.download !== undefined) {
          // feature detection
          // Browsers that support HTML5 download attribute
          var url = URL.createObjectURL(blob);
          link.setAttribute("href", url);
          link.setAttribute("download", exportedFilenmae);
          link.style.visibility = "hidden";
          document.body.appendChild(link);
          link.click();
          document.body.removeChild(link);
        }
      }
    },
    downloadFile() {
      if (this.SelectDownload == "All Activity") {
        Axios.get("api/surway/all").then((res) => {
          let data = res.data;
          var found;
          this.itemsFile = [];
          for (let i in data.result) {
            if (data.result[i].u_id.trim() != "") {
              found = this.itemsFile.find(
                (o) =>
                  o.u_id === data.result[i].u_id &&
                  o.s_activity === data.result[i].s_activity
              );
              if (found === undefined) {
                this.itemsFile.push({
                  u_id: data.result[i].u_id,
                  s_activity: data.result[i].s_activity,
                  s_rating: data.result[i].s_rating,
                  s_comment: data.result[i].s_comment.replace(/\n$/, ""),
                });
              }
            }
            //console.log(this.itemsFile.indexOf())
          }
          this.fileTitle = "Rating(" + this.SelectDownload + ")";
          this.exportCSVFile(this.headers, this.itemsFile, this.fileTitle);
        });
      } else {
        Axios.get("api/surway/findByActivity/", {
          params: { s_activity: this.SelectDownload },
        }).then((res) => {
          let data = res.data;
          if (data.result[i].u_id.trim() != "" && data.result[i].s_comment.trim() != "") {
            for (let i in data.result) {
              found = this.itemsFile.find(
                (o) =>
                  o.u_id === data.result[i].u_id &&
                  o.s_activity === data.result[i].s_activity
              );
              if (found === undefined) {
                this.itemsFile.push({
                  u_id: data.result[i].u_id,
                  s_activity: data.result[i].s_activity,
                  s_rating: data.result[i].s_rating,
                  s_comment: data.result[i].s_comment.replace(/\n$/, ""),
                });
              }
            }
          }
          this.fileTitle = "Rating(" + this.SelectDownload + ")";
          this.exportCSVFile(this.headers, this.itemsFile, this.fileTitle);
        });
      }
    },
  },
};
</script>
