<template>
  <div class="activity h100">
    <Layout>
      <div class="container mt-5">
        <div class="container">
          <div id="main" class="row position-relative h-100 justify-content-center">
            <div
              v-if="this.setting.imgSrc != '' || this.setting.ytID != ''"
              class="col-md-7 position-relative"
            >
              <div v-if="this.setting.imgSrc != ''" class="text-center imgContainer">
                <img :src="this.setting.imgSrc" class="w-50" />
              </div>
              <div v-else-if="this.setting.ytID != ''">
                <div id="ytContainer" class="ytContainer">
                  <youtube
                    v-if="loaded"
                    class="w-100 h-100"
                    ref="youtube"
                    @ready="ready"
                    @playing="playing"
                    @paused="paused"
                    :video-id="setting.ytID"
                    :player-vars="{ autoplay: 1, controls: 0, modestbranding:1, rel: 0, showinfo: 0 }"
                  ></youtube>
                </div>
              </div>
            </div>
            <div class="col-md-5">
              <h3 class="mt-5 text-center">{{ title }}</h3>
              <h2 class="text-primary font-weight-bold text-center">{{ setting.name }}</h2>

              <div class="mt-3 text-center">{{ setting.description }}</div>
              <div class="text-center mt-3">
                <a @click.prevent="onBack()" class="btn btn-primary mr-4 w100">Back</a>

                <Timer v-if="!finished" @onFinish="onFinish($event)" ref="timer" />
                <!--
                <button
                  v-else
                  class="btn btn-primary w100"
                  type="button"
                  @click.prevent="onEndActivity()"
                >NEXT</button>
                -->
                <button
                  v-else
                  class="btn btn-primary w100"
                  type="button"
                  @click.prevent="saveActivity()"
                >NEXT</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Layout>
    <ModalSurway :activity="activityName" @onComplete="onBack()" ref="modalSurway" />
  </div>
</template>
<script>
import Axios from "axios";
import ModalSurway from "@/components/ModalSurway";
import Complete from "@/components/Complete";
import Timer from "@/components/Timer";
import Layout from "@/components/Layout";
export default {
  components: {
    Axios,
    Timer,
    ModalSurway,
    Complete,
    Layout
  },
  name: "",
  data() {
    return {
      form: {
        u_id: null,
        u_username: null,
        av_name: null
      },
      setting: {
        name: "",
        description: "",
        school: "",
        major: "",
        ytID: "",
        imgSrc: ""
      },
      loaded: false,
      player: null,
      lang: "en",
      duration: 0,
      display: "video",
      title: "",
      subtitle: "",
      descEN: "",
      descTH: "",
      activityName: "",
      finished: false,
      finishedByOpen: false,
      ytHeight: 0
    };
  },
  computed: {},
  watch: {},
  mounted() {
    this.getContent();
    window.addEventListener("resize", this.resizeYT);
  },
  created() {
    const activity = this.activity[10];
    this.title = activity.title;
    this.activityName = activity.name;
    this.$store.dispatch("get_activity", { activity: activity });
  },
  beforeDestroy() {
    window.addEventListener("resize", this.resizeYT);
  },
  methods: {
    resizeYT() {
      this.ytHeight = (this.jquery("#ytContainer").width() * 9) / 16;
      this.jquery("#ytContainer").height(this.ytHeight);
    },
    getContent() {
      Axios.get("/api/controllerAlumni/findOneBymajor", {
        params: {
          aln_major: this.$store.getters.user.u_major,
          aln_school: this.$store.getters.user.u_school
        }
      }).then(res => {
        this.setting.name = res.data.aln_name;
        this.setting.description = res.data.aln_description;
        this.setting.school = res.data.aln_school;
        this.setting.ytID = res.data.aln_url;
        this.setting.imgSrc = res.data.aln_img;
        this.loaded = true;

        if (this.setting.ytID == "") {
          this.finished = true;
        }
      });
    },
    ready() {
      this.player = this.$refs.youtube.player;
      this.player.seekTo(0);
      this.player.playVideo();
      this.resizeYT();
    },
    async playing() {
      this.duration = await this.player.getDuration();
      this.videoTime = await this.player.getCurrentTime();
      if (!this.finished) {
        this.$refs.timer.setDuration(this.duration - this.videoTime);
        this.$refs.timer.onResume();
      }
    },
    paused() {
      if (!this.finished) {
        this.$refs.timer.onPause();
      }
    },
    onEndActivity() {
      const id = this.$store.getters.user.u_id;
      Axios.get(`/api/surway/${id}?activity=${this.activityName}`)
        .then(res => {
          if (res.data.length == 0) {
            this.$refs.modalSurway.showModal();
          } else {
            this.onBack();
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    onBack() {
      this.$router.push({
        path: `/ActivityPreview/${this.activityName}`
      });
    },
    onSerway() {
      const id = this.$store.getters.user.u_id;
      Axios.get(`/api/surway/${id}?activity=${this.activityName}`)
        .then(res => {
          if (res.data.length == 0) {
            this.$refs.modalSurway.showModal();
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    onFinish() {
      this.$refs.youtube.player.seekTo(0);
      this.$refs.youtube.player.pauseVideo();
      this.finished = true;
    },
    saveActivity() {
      const id = this.$store.getters.user.u_id;
      Axios.get(`/api/activityVideo/${id}?av_name=${this.activityName}`).then(
        res => {
          console.log("=====check activity====");
          if (!res.data) {
            this.form.u_id = id;
            this.form.u_username = this.$store.getters.user.u_username;
            this.form.av_name = this.activityName;
            Axios.post("/api/activityVideo", this.form).then(res => {
              console.log("=====saved activity====");
              //this.onBack();
              this.onEndActivity()
            });
          } else {
            this.onBack();
          }
        }
      );
    }
  }
};
</script>
<style scoped>
.imgContainer {
  margin-top: 80px;
}
.ytContainer {
  margin-top: 50px;
}
@media screen and (max-width: 768px) {
  .ytContainer,
  .imgContainer {
    margin-top: 0;
  }
}
.hideController {
  position: absolute;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0;
  z-index: 9999;
  background: #000;
  opacity: 0;
}
</style>