<template>
  <div
    class="modal fade"
    id="completeModal"
    tabindex="-1"
    role="dialog"
    aria-labelledby="exampleModalLabel"
    aria-hidden="true"
    data-backdrop="static"
    data-keyboard="false"
  >
    <div class="modal-dialog modal-dialog-centered modal-sm">
      <div class="modal-content">
        <div class="modal-body p-3 text-center">
          <h5 class="text-muted mt-3">COMPLETE!</h5>
          <h1 class="text-primary text-center font-weight-bolder">{{text}}</h1>
          <img src="../assets/img/trophy.png" class="w-75 img-fluid mt-3 d-block m-auto" />
          <div class="mt-3 mb-3">
            <button
              @click.prevent="onComplete()"
              type="button"
              class="btn btn-primary w100 d-block m-auto"
              data-dismiss="modal"
              aria-label="Close"
            >SUBMIT</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    text: {
      default: "modal Complete"
    }
  },
  data() {
    return {};
  },
  methods: {
    showModal() {
      this.jquery("#completeModal").modal("show");
      this.jquery(".flex-container").addClass("blur");
    },
    hideModal() {
      this.jquery(".flex-container").removeClass("blur");
    },
    onComplete() {
      this.$emit("onComplete");
    }
  }
};
</script>