<script>
import Axios from "axios";
import ReportForm from "@/components/ReportForm";
import ModalResponse from "@/components/ModalResponse";
import ModalNotification from "@/components/ModalNotification";
import { mapState } from "vuex";
import VueCircle from "vue2-circle-progress";
import moment from "moment";
// import TutorialModal from "@/components/TutorialModal";
export default {
  components: {
    ReportForm,
    ModalResponse,
    VueCircle,
    ModalNotification,
  },
  data() {
    return {
      u_id: "",
      displayForm: false,
      isActive: false,
      userThumb: null,
      displayEditProfile: false,
      displayTopic: "main",
      line: ["NOTIFICATION", "PROFILE", "LOGOUT"],
      fill: { color: "#ff8888" },
      progressCircleAnime: { duration: 0, easing: "circleProgressEasing" },
      notice: [],
      countNotice: 0,

      titleEN: "How to Live and Learn System’s issue report",
      titleTH: "แจ้งปัญหาการใช้งานระบบ",
      descEN:
        "If you find issues with the system, you can report them here by filling in your student ID, contact numbers, e-mail, and the issues you found. The admin team will fix them within 24 hours and respond back to your email as soon as possible. ",
      descTH:
        "แจ้งปัญหาการใช้งานระบบ How to Live and Learn หากน้องๆ พบปัญหาการใช้งาน แจ้งได้ที่นี่ โดยกรอกรหัสนักศึกษา เบอร์โทรศัพท์ติดต่อ อีเมลล์ และปัญหาที่พบ โดยพี่แอดมินจะทำการแก้ไขปัญหาให้ภายใน 24 ชม.  และจะตอบกลับผ่านอีเมลโดยเร็วที่สุด",
      lang: "EN",
    };
  },

  created() {
    this.$store.dispatch("get_user_progress", {
      id: this.$store.getters.user.u_id,
    });
    this.$store.dispatch("get_notification", {
      id: this.$store.getters.user.u_id,
    });
    this.u_id = this.$store.getters.user.u_id;
    this.userThumb = this.$store.getters.user.u_thumbnail;
  },
  mounted() {
    this.$root.$on("component1", () => {
      // your code goes here
      this.displayNotice();
    });

    this.fillCirColor();
  },
  computed: {
    ...mapState(["userProgress"]),
    ...mapState(["notification"]),
  },
  watch: {
    userProgress: function (newVal, oldVal) {
      const progress = Math.round(newVal.progress);
      this.$refs.circleProgress.updateProgress(progress);
    },
    fill: function (newVal, oldVal) {
      this.$refs.circleProgress.updateFill(newVal);
    },
    notification: function (newVal, oldVal) {
      this.countNotice = 0;
      this.notice = [];
      newVal.forEach((element) => {
        if (element.view == null) {
          this.countNotice += 1;
        }
        element.no_updated = moment(element.no_updated).format("MMMM Do YYYY");
        //console.log(element.no_updated)
        this.notice.push(element);
      });
    },
  },
  methods: {
    fillCirColor() {
      const progress = Math.round(this.$store.getters.userProgress.progress);
      if (progress <= 25) {
        this.fill = { color: "#ff8888" };
      } else if (progress > 25 && progress <= 50) {
        this.fill = { color: "#fdd273" };
      } else if (progress > 50 && progress <= 75) {
        this.fill = { color: "#90bf3e" };
      } else if (progress > 75 && progress < 99) {
        this.fill = { color: "#00a843" };
      } else if (progress == 100) {
        this.fill = {
          gradient: ["#ff8888", "#fdd273", "#90bf3e", "#00a843", "#c3abfc"],
        };
      }
    },
    displayNotice() {
      this.$refs.noticeModal.showModal();
    },
    displayTutorial() {
      this.displayTopic = "main";
      this.jquery("#tutorialModal").modal("toggle");
    },
    displayReportForm() {
      this.displayForm = false;
    },

    toggleProfileModal(value) {
      this.jquery("#profileModal").modal("show");
      this.displayEditProfile = value;
    },
    setProfilePicture(className) {
      Axios.put("/api/account/updateThumb", {
        id: this.$store.getters.user.u_id,
        thumbnail: className,
      })
        .then((res) => {
          this.$store.dispatch("get_user_login");
          this.userThumb = className;
        })
        .catch((err) => {
          console.log(err);
        });
      //this.imgSrc = className;
      this.toggleProfileModal(false);
    },
    logout() {
      Axios.post("/api/account/logout").then((response) => {
        this.$router.push({ name: "login" });
      });
    },

    toggleMenu: function () {
      this.isActive = !this.isActive;
      this.$emit("onToggle", this.isActive);
      //this.$store.commit("set_MenuToggle", this.isActive);
    },

    onResponseSubmit() {
      this.jquery("#tutorialModal").modal("hide");
      this.$refs.responseModal.showModal();
    },
    progress(event, progress, stepValue) {
      //console.log(stepValue);
    },
    progress_end(event) {
      //console.log("Circle progress end");
    },
    displayModalReport() {
      this.displayForm = true;
      this.jquery("#reportProblemModal").modal("show");
    },
    toggleLang(lang) {
      this.lang = lang;
    },
  },
};
</script>
