<template>
  <nav v-if="allPage > 1" aria-label="pagination">
    <ul class="pagination justify-content-center">
      <li class="page-item" :class="{ disabled: page <= 1 }">
        <a class="page-link" @click.prevent="onPage(page - 1)">
          <span aria-hidden="true">&laquo;</span>
        </a>
      </li>

      <li>{{ page }}:{{ allPage }}</li>

      <li class="page-item" :class="{ disabled: page >= allPage }">
        <a class="page-link" @click.prevent="onPage(page + 1)">
          <span aria-hidden="true">&raquo;</span>
        </a>
      </li>
    </ul>
  </nav>
</template>
<script>
export default {
  props: {
    data: {
      required: true
    },
    page: {
      type: Number,
      required: true,
      default: 1
    }
  },
  data() {
    return {
      allPage: 0
    };
  },
  watch: {
    data: function() {
      this.getPageItem()
    }
  },
  computed: {},
  methods: {
    onPage(page) {
      this.$emit("onPage", page);
    },
    getPageItem() {
      const pages = [];
      if (this.data.rows && this.data.result) {
        const rows = this.data.rows;
        const limit = this.data.limit;
        for (var index = 1; index <= Math.ceil(rows / limit); index++) {
          pages.push(index);
        }
      }
      this.allPage = pages.length;
    }
  }
};
</script>
<style scoped>
.page-link {
  background-color: transparent !important;
}
.page-link,
.page-item {
  display: initial !important;
  border: none !important;
  background-color: transparent !important;
  cursor: pointer;
}
.page-item,
.page-item.page-link {
  border: none;
  background-color: transparent;
}
#paging-container > ul > li {
  color: #01973f !important;
}
.page-item.disabled,
.page-item.disabled .page-link {
  background-color: transparent !important;
  color: #efefef !important;
}
.page-item.disabled,
.page-item.disabled .page-link {
  color: #103b29 !important;
}
</style>